<template>
  <div :class="{ collapsed: layoutCollapsed }" class="bg-white" id="layout">
    <div>
      <sidebar-menu
        :menu="menu"
        :show-one-child="true"
        @toggle-collapse="onToggleCollapse"
        :collapsed="layoutCollapsed"
      >
        <span slot="header">
          <div class="my-5"></div>
        </span>
      </sidebar-menu>
    </div>
    <div class="content">
      <b-navbar class="border" type="light" variant="light">
        <b-navbar-brand class="d-flex align-items-center">
          <img
            src="@/assets/C2H.png"
            style="
              width: 110px;
              height: 50px;
              cursor: pointer;
            "
            @click="noticias"
          />
          <h5 class="mx-2 mb-0">{{ titulo }}</h5>
        </b-navbar-brand>
        <b-navbar-nav class="ml-auto font-weight-bold">
          <b-nav-item>
            {{ usuario.nombre }}
            <!-- <i class="fas fa-bell fa-lg"></i>
            <span class="badge badge-pill badge-danger">0</span> -->
            <Notificaciones class="d-inline" :notificaciones="notifLocal" :notificacionesPendientes="notificacionesPendientes" @actualizarNotificaciones ="actualizarNotificaciones"></Notificaciones>
            </b-nav-item >
          <!-- </b-nav-item> -->
          <button @click="logout" class="btn btn-outline-secondary">
            <div class="row">
              <i class="fa fa-sign-out-alt col-2 mt-1"></i>
              <span class="d-none d-lg-block col-6">Salir </span>
            </div>
          </button>
        </b-navbar-nav>
      </b-navbar>

      <div class="row no-gutters" style="background: white">
        <div class="col-12 px-4">
          <router-view
            class="router-content mt-4"
            @titulo="(t) => (titulo = t)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Notificaciones from "@/components/Notificaciones"
export default {
  props:[
			"notificaciones",
			"notificacionesPendientes"
		],
  components: {
			Notificaciones			
  },
  data() {
    return {
      menu: [],
      layoutCollapsed: false,
      titulo: "",
      notifLocal : null
    };    
  },
  mounted() {
    this.cargarMenu();
    this.obtenerNotificaciones()
    .then(r=>{
      this.notifLocal = r.data
    })
    .catch(e=>console.error(e))
  },
  methods: {
    formatearOpciones(data) {
      let source = [];
      let submenu = [];

      for (let i = 0; i < data.length; i++) {
        let item = data[i];

        let id = item["id"];
        let parentid = item["parentid"];
        let name = item["name"];
        let icon = item["icon"];
        let url = item["url"];

        if (submenu[parentid]) {
          let subitem = {
            id: id,
            parentid: parentid,
            title: name,
            icon: icon,
            href: url,
          };

          if (!submenu[parentid].child) {
            submenu[parentid].child = [];
          }
          submenu[parentid].child[submenu[parentid].child.length] = subitem;
          submenu[id] = subitem;
        } else {
          submenu[id] = {
            id: id,
            parentid: parentid,
            title: name,
            icon: icon,
          };
          source[id] = submenu[id];
        }
      }
      source = source.filter((item) => item.parentid != null);
      return source;
    },
    actualizarNotificaciones(notificaciones){				        
				this.$emit("actualizarNotificaciones",notificaciones);
    },
    cargarMenu() {      
      if (!this.permisos) return;
      this.layoutCollapsed = localStorage.getItem("layoutCollapsed") == "true";
      this.menu = this.permisos;
      this.menu = this.formatearOpciones(this.menu);
    },
    logout() {
      //window.localStorage.removeItem("user")
      //window.localStorage.removeItem("token")
      // localStorage.setItem("RouteTo", to.path)
      window.localStorage.removeItem("tokenWebAdmin");
      this.$router.push({ name: "login" });
    },
    onToggleCollapse(collapsed) {
      this.layoutCollapsed = collapsed;
      //let rootElement = this.$el
      let tabla = document.getElementById("tabla-articulos");
      window.localStorage.setItem("layoutCollapsed", collapsed);
      //let childElement = rootElement.$refs

      //console.log('collapsed', collapsed)

      this.$nextTick(() => {
        //tabla.trigger('reflow')

        this.$floatThead.reflow(tabla);

        this.$floatThead.ini(tabla, {
          position: "absolute",
          scrollContainer: true,
        });
      });
    },
    noticias() {
      this.$router.push({ path: "/noticias" });
    },
  },
};
</script>
<style scoped>
.badge {
  position: relative;
  top: -10px;
  left: -5px;
}

#layout {
  padding-left: 250px;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  height: 100%;
  background-color: #eee;
}

#layout.collapsed {
  padding-left: 50px;
}

.v-sidebar-menu.vsm_expanded {
  width: 250px;
}

.v-sidebar-menu {
  background: rgb(13, 67, 135) !important;
  color: #fff;
}

.v-sidebar-menu a {
  color: #fff;
  text-decoration: none;
}
.difarmer {
  font-style: italic;
  font-size: 25px;
  font-weight: bold;
}
.titulo {
  font-size: 22px;
}
</style>