import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                    E M P L E A D O S
//............................................................
servicio.empleados = () => {
    return base.get(`/empleados/todos`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.obtenerPersonalCargaDiesel = (codPersonal) => {
    codPersonal = codPersonal == null || codPersonal == undefined || codPersonal == 0 ? 0 : codPersonal
    return base.get(`/empleados/personalCargaDiesel/${codPersonal}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.tipos = () => {
    return base.get(`/empleados/tipos`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.tiposUtilizados = () => {
    return base.get(`/empleados/tiposUtilizados`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.guardarEmpleado = (empleado) => {

    return base.post("/empleados/guardar", empleado, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(e => {
            console.log(e);
        })
}

servicio.subtipo = (codTipo) => {
    return base.post("/empleados/SubTipos", { "codTipo": codTipo }, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            console.log("SUBTIPOSSS-> ", r);
            return r.data
        })
        .catch(e => {
            console.log(e);
        })
}

servicio.obtenerEmpleado = (codEmpleado) => {
    // console.log(codEmpleado);
    return base.get(`/Empleados/${codEmpleado}`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.tiposEmpleado = (codTipoEmpleado) => {
    codTipoEmpleado = codTipoEmpleado == null || codTipoEmpleado == undefined ? 0 : codTipoEmpleado
    return base.get(`/empleados/tiposEmpleado/${codTipoEmpleado}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.guardarTipoEmpleado = (tipoEquipo) => {
    return base.post("/empleados/guardar/tipoEmpleado", tipoEquipo, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

export default servicio