import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                       P O R T E R O S
//............................................................
servicio.obtenerEntradasSalidas = (fechaDesde, fechaHasta) => {

    return base.get(`/porteros/obtener-entradas-salidas/${fechaDesde}/${fechaHasta}`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.obtenerSuministros = (fechaDesde, fechaHasta) => {

    return base.get(`/porteros/obtener-suministros/${fechaDesde}/${fechaHasta}`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.guardarEntradasSalidas = (entradaSalida) => {

    return base.post(`/porteros/guardar-entradas-salidas`, entradaSalida, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.guardarSuministros = (suministros) => {

    return base.post(`/porteros/guardar-suministros`, suministros, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}


export default servicio