servicio.obtenerNotificaciones = ()=> {
    return base.get('/notificaciones/ultimasNotificaciones')
    .then(r=>{
        return r.data;
    })
    .catch(e=>{
        console.log(e);
    });    
}

servicio.marcarNotificacionRedirigida = (todasRedirigidas,codigoNotificacion) => {
    return base.post('/notificaciones/redireccionadas',{todasRedirigidas,codigoNotificacion})
    .then(r=>{
            return r.data;
        }).catch(e=>{
            return e ;
    });
}

servicio.marcarNotificacionesVista = () =>{
    return base.post('/notificaciones/marcarVistas')
    .then(r=>{  
        return r.data;
    }).catch(e=>{
        return e;
    })
}

export default servicio;