<template>
<div>
    <div class="container mt-3">
        
        <div class="mt-3">
            <h3><i class="fas fa-gas-pump"></i> Suministros</h3>
        </div>

        <!-- botones -->
        <!-- <b-row>
            <b-col class="col-md-6 col-sm-12 col-12" align-self="end"/>
            <b-col class="col-md-3 col-sm-12 col-12" align-self="end"/>
            <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                <b-button @click="Nuevo" block variant="primary" class="mr-2 mt-2">
                    <i class="fa fa-plus"></i> Nueva
                </b-button>
            </b-col>
        </b-row> -->

        <b-row>
            <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                <label>Desde:</label>
                <b-form-datepicker v-model="desde" class="mb-2" locale="es"></b-form-datepicker>
            </div>
            <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                <label>Hasta:</label>
                <b-form-datepicker v-model="hasta" class="mb-2" locale="es"></b-form-datepicker>
            </div>
        </b-row>

        <b-row>
            <div class="col-sm-12 col-md-3">
                <b-button @click="buscar" block variant="success" class="mr-2 mt-2">
                    <i class="fa fa-search"></i> Buscar
                </b-button>
            </div>
            <b-col class="col-sm-12 col-md-3" align-self="end"/>
            <!-- <b-col class="col-sm-12 col-md-3" align-self="end"/>
            <b-col class="col-sm-12 col-md-3 " align-self="end">
                <b-button @click="Nuevo" block variant="primary" class="mr-2 mt-2">
                    <i class="fa fa-plus"></i> Nueva
                </b-button>
            </b-col> -->
        </b-row>

        <div>
            <table-sortable class="mt-3"
                :items="items"
                :fields="fields"
                :rutaNuevo="rutaNuevo"
                :permisoEscritura="permisoEscritura"
            ></table-sortable>
        </div>
    </div>
</div>
</template>

<script>

import TableSortable from "@/components/TableSortable";
import servicioPorteros from '@/services/servicio-porteros';

export default {
    data(){

        const now = new Date()
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
        var todayDate = today.toISOString().slice(0,10);

        return{
            desde: todayDate,
            hasta: todayDate,
            items: [],
            fields: [
                { key: "equipo", label: "Equipo", sortable: true, sortDirection: "desc", },
                { key: "operador", label: "Operador", sortable: true, sortDirection: "desc", },
                { key: "usuario", label: "Usuario", sortable: true, class: "text-center", },
                { key: "fecha", label: "Fecha", sortable: true, class: "text-center" },
                { key: "action", label: "", class: "text-center" },
            ],
            rutaNuevo: "/portero/suministros/registro/0",
            permisoEscritura: true,
        }
    },
    components: {
        TableSortable
    },
    mounted() {
        // this.verificaPermiso("suministros");
    },
    created() {
    },
    computed: {
    },
    methods: {

        async ObtenerSuministros() {
            this.$loading(true);
            await servicioPorteros
                    .obtenerSuministros(this.desde, this.hasta)
                    .then((r) => {
                        this.items = r.data;
                        this.$loading(false);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
        },

        buscar() {
            this.ObtenerSuministros();
        },

    }
}
</script>

