import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                       U S U A R I O S
//............................................................
servicio.usuarios = () => {
    return base.get(`/usuario/todos`)
        .then(r => {
            console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.tipos = () => {
    return base.get(`/usuario/tipos`)
        .then(r => {
            console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.actualizarEstatus = (idUsuario, activar) => {
    return base.get(`/usuario/actualizar-estatus/${idUsuario}/${activar}`)
        .then(r => {
            console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}
servicio.guardarUsuario = (usuario) => {
    return base.post("/usuario/guardar",usuario,{
        headers:{
            'Content-Type': 'application/json'
        }
    }).then(r=>{
        return r.data
    })
    .catch(err=>{
        console.log(err);
    })
}

export default servicio