<template>
  <div id="app">
    <div class="container my-5">
      <h3><i class="fas fa-hard-hat"></i> Especificaciones de concretos</h3>
      <table-sortable
        :items="items"
        :fields="fields"
        :rutaNuevo="rutaNuevo"
        :permisoEscritura="permisoEscritura"
      ></table-sortable>
    </div>
  </div>
</template>
<script>
import TableSortable from "@/components/TableSortable";
import servicio from "@/services/servicio-materiales";
export default {
  data() {
    return {
      rutaNuevo: "tipos-de-concreto/registro/",
      permisoEscritura: true,
      items: [],
      fields: [
        {
          key: "descripcionCorta",
          label: "Abreviaci√≥n",
          sortable: true,
          sortDirection: "desc",
        },
        {
          key: "descripcion",
          label: "Descripci√≥n",
          sortable: true,
          class: "text-center",
        },
        {
          key: "actions",
          label: "Acciones",
          class: "text-center",
        },
      ],
    };
  },
  /*
        "codigo": 1,
        "descripcionCorta": "100",
        "descripcion": "Concreto con resitencia de 100 kg/cm2",
        "codTipoResistencia": 1,
        "estatus": true,
        "tipoResistencia": "F'C"
    
    */
  components: {
    TableSortable,
  },
  mounted() {
    // if (this.verificaTokenValido()) {
      this.consultarEspecificaciones();
    // }
  },
  methods: {
    consultarEspecificaciones() {
      this.$loading(true);
      servicio
        .especificaciones()
        .then((r) => {
          this.items = r.data;
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
        });
      // this.verificaPermiso();
    },
  },
};
</script>