<template>
  <div id="app">
    <div>
      <!-- <b-button @click="modalShow = !modalShow">Open Modal</b-button> -->

      <b-modal
        v-model="modalShow"
        centered
        size="xl"
        title="Detalle de nota de remisi√≥n"
        :header-bg-variant="headerBgVariant"
        :header-text-variant="headerTextVariant"
      >
        <b-container fluid class="container">
          <div class="row">
            <div class="form-group col-sm-12 col-md-12 col-lg-12 col-xl-2 mb-1">
              <label>Folio</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.folio"
              />
            </div>
            <div class="form-group col-sm-12 col-md-4 col-lg-4 col-xl-2 mb-1">
              <label>Folio Ginco</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.folioGinco"
              />
            </div>
            <div
              class="
                form-group
                col-sm-12 col-md-4 col-lg-4
                offset-xl-5
                col-xl-2
                mb-1
              "
            >
              <label>Hora</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.horaSalida"
              />
            </div>
          </div>

          <div class="row">
            <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-1">
              <label>Cliente</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.cliente"
              />
            </div>
            <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-1">
              <label>Obra</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.obra"
              />
            </div>
          </div>

          <div class="row">
            <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-3 mb-1">
              <label>F√≥rmula</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.formula"
              />
            </div>
            <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-2 mb-1">
              <label>Cantidad</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.cantidad"
              />
            </div>
            <div
              class="
                form-group
                col-sm-12 col-md-4 col-lg-4 col-xl-2
                mb-2
                text-center
                my-1
              "
            >
              <label>ES BOMBEABLE</label>
              <b-form-checkbox
                name="check-button"
                switch
                v-model="data.bombeable"
                size="md"
                :disabled="true"
              >
              </b-form-checkbox>
            </div>

            <div
              class="
                form-group
                col-sm-12 col-md-4 col-lg-4 col-xl-2
                mb-2
                text-center
                my-1
              "
            >
              <label>FIBRA</label>
              <b-form-checkbox
                name="check-button"
                switch
                size="md"
                v-model="data.fibra"
                :disabled="true"
              >
              </b-form-checkbox>
            </div>

            <div
              class="
                form-group
                col-sm-12 col-md-4 col-lg-4 col-xl-2
                mb-2
                text-center
                my-1
              "
            >
              <label>IMPER</label>
              <b-form-checkbox
                name="check-button"
                switch
                size="md"
                :disabled="true"
                v-model="data.imper"
              >
              </b-form-checkbox>
            </div>
          </div>

          <div class="row">
            <div class="form-group col-12 mb-1">
              <label>Producto</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.producto"
              />
            </div>
          </div>
          <div class="row">
            <div class="form-group col-12 mb-1">
              <label>Vendedor</label>
              <input
                type="text"
                class="form-control"
                :disabled="true"
                v-model="data.vendedor"
              />
            </div>
          </div>
          <div class="row my-2">
            <b-col
              class="form-group col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-1"
            >
              <b-row>
                <div class="form-group col-sm-12 col-md-12 col-lg-6 col-xl-6">
                  <b-form-group
                    label-cols-lg="3"
                    id="input-group-4"
                    label="Operador"
                    label-for="cboOperador"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :disabled="true"
                      v-model="data.operadorCR.nombreCompleto"
                    />
                  </b-form-group>
                </div>

                <div class="form-group col-sm-12 col-md-12 col-lg-6 col-xl-6">
                  <b-form-group
                    label-cols-lg="3"
                    id="input-group-4"
                    label="Equipo"
                    label-for="cboOperador"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :disabled="true"
                      v-model="data.operadorCR.equipo"
                    />
                  </b-form-group>
                </div>
              </b-row>
            </b-col>
          </div>
          <div class="row" v-if="data.bombeable">
            <b-col
              class="form-group col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2"
            >
              <b-row>
                <div class="form-group col-sm-12 col-md-12 col-lg-6 col-xl-6">
                  <b-form-group
                    label-cols-lg="3"
                    id="input-group-4"
                    label="Operador"
                    label-for="cboOperador"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :disabled="true"
                      v-model="data.operadorBB.nombreCompleto"
                    />
                  </b-form-group>
                </div>

                <div class="form-group col-sm-12 col-md-12 col-lg-6 col-xl-6">
                  <b-form-group
                    label-cols-lg="3"
                    id="input-group-4"
                    label="Equipo"
                    label-for="cboOperador"
                  >
                    <input
                      type="text"
                      class="form-control"
                      :disabled="true"
                      v-model="data.operadorBB.equipo"
                    />
                  </b-form-group>
                </div>
              </b-row>
            </b-col>
          </div>
        </b-container>
        <template #modal-footer>
          <div class="w-100">
            <b-button
              variant="primary"
              class="float-right"
              @click="modalShow = false"
            >
              Aceptar
            </b-button>
          </div>
        </template>
      </b-modal>
    </div>

    <div class="container mt-3">
      <div class="mt-3">
        <h3><i class="fas fa-clipboard"></i> Pedidos</h3>
      </div>
      <table-sortable
        class="mt-3"
        :items="items"
        :fields="fields"
        :rutaNuevo="rutaNuevo"
        :permisoEscritura="permisoEscritura"
        @show-modal="showModal"
      ></table-sortable>
    </div>
  </div>
</template>

<script>
import TableSortable from "@/components/TableSortable";
import servicio from "@/services/servicio-agentes";

export default {
  data() {
    return {
      data: {
        operadorCR: {
          nombreCompleto: "",
        },
        operadorBB: {
          nombreCompleto: "",
        },
      },
      modalShow: false,
      rutaNuevo: "nota-de-remision/registro/",
      items: [],
      fields: [
        {
          key: "folioGinco",
          label: "Folio Ginco",
          sortable: true,
          class: "text-center",
        },
        {
          key: "cliente",
          label: "Cliente",
          sortable: true,
          class: "text-center",
        },
        { key: "obra", label: "Obra", sortable: true, class: "text-center" },
        {
          key: "estatus",
          label: "Estatus",
          sortable: true,
          class: "text-center",
          formatter: (value) => {
            var valueFormatter = "";
            switch (value) {
              case "S":
                valueFormatter = "Surtiendo";
                break;
              case "C":
                valueFormatter = "Cancelado";
                break;
              case "T":
                valueFormatter = "Terminado";
                break;
            }
            return valueFormatter;
          },
        },
        {
          key: "fechaConFormato",
          label: "Fecha",
          sortable: true,
          class: "text-center",
        },
        { key: "detalleEmit", label: "Ver", class: "text-center" },
        { key: "descargar", label: "Descarga", class: "text-center" },
      ],
      permisoEscritura: false,
      variants: [
        "primary",
        "secondary",
        "success",
        "warning",
        "danger",
        "info",
        "light",
        "dark",
      ],
      headerBgVariant: "dark",
      headerTextVariant: "light",
      bodyBgVariant: "light",
      bodyTextVariant: "dark",
      footerBgVariant: "warning",
      footerTextVariant: "dark",
    };
  },
  components: {
    TableSortable,
  },
  mounted() {
    // if (this.verificaTokenValido()) {
    this.ObtenerNotasRemision();
    // }
  },
  methods: {
    async ObtenerNotasRemision() {
      this.$loading(true);
      let codAgente = window.localStorage.codigo;
      console.log("Codigo agente" + codAgente);
      await servicio
        .obtenerNotasRemisionAgente(codAgente)
        .then((r) => {
          this.items = r.data;
          this.$loading(false);
        })
        .catch((error) => {
          this.$loading(false);
          console.error(error);
        });
    },
    async showModal(data) {
      let r = await servicio.obtenerDetalleNotasRemisionAgente(
        data.item.idNotasRemisionEnc
      );
      this.data = data.item;
      if (r.data.length > 1) {
        this.data.bombeable = true;
        this.data.operadorCR = r.data[0];
        this.data.operadorBB = r.data[1];
      } else {
        this.data.bombeable = false;
        this.data.operadorCR = r.data[0];
      }
      this.modalShow = true;
    },
  },
};
</script>
<style scoped></style>
