<template>
    <div>
        <div :class="{'overlay-collapsed': !mostrar, 'overlay-show': mostrar }" class="overlay">
			<div class="row">
				<div class="col-12">
					<div class="card m-lg-5 m-2 p-3 bg-white">
                        <!-- <div class="row">
                            <div class="col">
                                <div class="form-group mx-md-3">
                                    <div class="form-inline justify-content-end">
                                        <select class="form-control mx-2" v-model="seleccionado" v-on:change="consultarGraficaCedis">
                                            <option v-for="opcion of itemsCedis" :value="opcion.codSucursal" v-bind:key="opcion.codSucursal">{{opcion.descripcion}}</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </div> -->
                        
                        <pm-line-chart :datasetsp="datasets" :descripcion="descripcion"></pm-line-chart>
                        
                        <button class="btn btn-sm btn-info" @click="cerrar">Cerrar</button>
					</div>
				</div>
			</div>
		</div>        
    </div>
</template>

<script>
import PmLineChart from "@/components/LineChart"

export default {
    props: ["datasets", "mostrar", "descripcion", "itemsCedis","cedisMostrarGrafica"],
    data(){
        return{
            seleccionado:this.cedisMostrarGrafica
        }
    },
     components:{
        PmLineChart
    },
    mounted() {
        window.addEventListener('keydown', (e) => {
				//@keyup.enter="seleccionarItem()"
				if (e.key == 'Escape') {
					this.cerrar()
				}
			});
    },
    methods: {
        
        cerrar() {
            this.$emit("cerrar")
        },
        consultarGraficaCedis(){
            this.$emit("consultarGraficaCedis", this.seleccionado)
        }
    },    
}
</script>

<style>
    .overlay-collapsed {
		opacity: 0;
		display: none;
		/*transition: all 50ms ease-out;*/
	}	
	.overlay-show {
		opacity: 1;
		transition: all 100ms ease-out;
		z-index: 10001;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100vw;
		height: 100vh;
		background: rgba(0, 0, 0, 0.8);
		backdrop-filter: blur(2px);
		-webkit-backdrop-filter: blur(2px);
	}
</style>