<script>
import { Line } from "vue-chartjs";

export default {
  props: ["datasetsp", "descripcion"],
  extends: Line,
  mounted() {      
        this.renderChart(
        {
            labels: [
            "Enero",
            "Febrero",
            "Marzo",
            "Abril",
            "Mayo",
            "Junio",
            "Julio",
            "Agosto",
            "Septiembre",
            "Octubre",
            "Noviembre",
            "Diciembre"
            ],
            datasets: [
            {
                label: "",
                data: [],
                backgroundColor: "",
                borderColor: "",
                pointBackgroundColor: "",
            }],
        },        
        {
            responsive: true,
            maintainAspectRatio: false,
            title: {
            display: true,
            text: "My Data"
            }
        }
    );
  },
  watch:{
      datasetsp(newValue){
        //console.log('dasdasda', this.datasets)
        if(newValue){
            this.renderChart(
            {
                labels: [
                "Enero",
                "Febrero",
                "Marzo",
                "Abril",
                "Mayo",
                "Junio",
                "Julio",
                "Agosto",
                "Septiembre",
                "Octubre",
                "Noviembre",
                "Diciembre"
                ],
                datasets:this.datasetsp
            },
            {
                responsive: true,
                maintainAspectRatio: false,
                title: {
                display: true,
                text: this.descripcion
                }
            }
            );
        }
      }
  }
};
</script>