import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                     C L I E N T E S
//............................................................

servicio.clientes = (codCliente) => {
    if (codCliente == null || codCliente == undefined || codCliente == 0) {
        return base.get(`/clientes/todos`)
            .then(r => {
                return r.data;
            })
            .catch(error => {
                return error;
            })
    } else {
        console.log(codCliente);
        return base.get(`/clientes/${codCliente}`)
            .then(r => {
                console.log(r);
                return r.data;
            })
            .catch(error => {
                return error;
            })
    }
}

servicio.clientesCombo = () => {
    return base.get(`/clientes/clientes-combo`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.guardarDireccion = (direccion) => {
    return base.post("/clientes/direcciones/guardar", direccion, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(e => {
            console.log(e);
        })
}

servicio.guardarContacto = (contacto)=>{
    return base.post('/clientes/contactos/guardar', contacto,{
        headers:{
            'Content-Type':'application/json'
        }
    }).then(r=>{
        return r.data
    }).catch(e=>{
        console.log(e);
    })
}

