<template>
  <div class="container py-5">
    <h3><i class="fas"></i> Registro de tipos de empleado</h3>
    <div class="py-5">
      <b-form @submit="onSubmit">
        <b-row>
          <b-col class="col-md-5 col-sm-12 col-12">
            <b-form-group
              id="input-group-2"
              label="Tipo de empleado"
              label-for="txtTipoEmpleado">
              <b-form-input
                id="txtTipoEmpleado"
                v-model="form.tipoEmpleado"
                type="text"
                required
                ref="txtTipoEmpleado"
                placeholder="Sistemas"
                class="text-capitalize"
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col class="col-md-5 col-sm-12 col-12">
            <b-form-group
              id="input-group-4"
              label="Tipo de equipo"
              label-for="cboTipo">
              <b-form-select
                v-model="form.codTipoEquipo"
                :options="tiposEquipo"
                id="cboTipo"
              ></b-form-select>
            </b-form-group>
          </b-col>
          <b-col class="col-md-2 col-sm-12 col-12" v-show="mostrarEstatus">
            <b-form-group label="Estatus:">
              <b-form-checkbox
                id="chckEstatus"
                v-model="form.estatus"
                name="chckEstatus"
                switch
                size="lg">
                {{ form.estatus ? "Activo" : "Inactivo" }}
              </b-form-checkbox>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
          <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
            <b-button type="submit" block variant="success" class="mr-2 mt-2">
              <i class="fa fa-save"></i> Guardar
            </b-button>
          </b-col>
          <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
            <b-button
              @click="cancelar"
              block
              variant="danger"
              class="mr-2 mt-2">
              Cancelar
            </b-button>
          </b-col>
        </b-row>
      </b-form>
    </div>
  </div>
</template>
<script>
import servicio from "@/services/servicio-empleados";
import servicioEquipos from "@/services/servicio-equipos.js";
export default {
  data() {
    return {
      mostrarEstatus: false,
      codTipoEmpleado: 0,
      form: {
        tipoEmpleado: "",
        estatus: true,
        codTipoEquipo: 0,
      },
      tiposEquipo: [{ value: 0, text: "Seleccione el equipo" }],
    };
  },
  mounted() {
    // if (this.verificaTokenValido()) {
      this.consultarTipos();
      this.$refs.txtTipoEmpleado.focus();
      this.codTipoEmpleado = parseInt(this.getUrlParameter("id"));
      if (this.codTipoEmpleado > 0) {
        this.cargarTipoEmpleado();
        this.mostrarEstatus = true;
      }
    // }
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      this.guardarTipoEmpleado();
    },
    cargarTipoEmpleado() {
      this.codTipoEmpleado = parseInt(this.getUrlParameter("id"));
      this.$loading(true);
      if (this.codTipoEmpleado > 0) {
        servicio
          .tiposEmpleado(this.codTipoEmpleado)
          .then((r) => {
            this.form.tipoEmpleado = r.data[0].descripcion;
            this.form.estatus = r.data[0].estatus;
            this.form.codigoTipoEquipo = r.data[0].codTipoEquipo;
          })
          .catch((error) => {
            console.log(error);
          });
      }
      this.$loading(false);
    },
    consultarTipos() {
      servicioEquipos.tipoEquipoCombo().then((r) => {
        console.log(r)
        if (r.value) {
          r.data.forEach((element) => {
            let tipo = { value: element.codigo, text: element.descripcion };
            this.tiposEquipo.push(tipo);
          });
        }
      });
    },
    guardarTipoEmpleado() {
      let tipoEmpleado = {
        codigo: this.codTipoEmpleado,
        descripcion: this.form.tipoEmpleado,
        estatus: this.form.estatus,
        codTipoEquipo: this.form.codTipoEquipo,
      };
      servicio.guardarTipoEmpleado(tipoEmpleado).then((r) => {
        if (r.value) {
          this.$swal.fire({
            text: r.message,
            icon: "success",
          });
           window.history.back();
        } else {
          this.$swal.fire({
            text: r.message,
            icon: "error",
          });
        }
      });
    },
    cancelar() {
      let tipoActividad =
        this.codTipoEmpleado == 0
          ? "¬øDesea cancelar el registro de un nuevo tipo de empleado?"
          : "¬øDesea cancelar la actualizaci√≥n del registro?";
      console.log(tipoActividad);
      this.$swal
        .fire({
          title: tipoActividad,
          text: "",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, volver al cat√°logo",
          cancelButtonText: "Cancelar",
        })
        .then((result) => {
          if (result.value) {
            window.history.back();
          }
        });
    },
  },
};
</script>
<style scoped>
</style>