      <!-- <template v-slot:row-details="row">
        <b-card>
          <ul>
            <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
          </ul>
        </b-card>
      </template> -->
    </b-table>

    <!-- Info modal -->
    <b-modal
      :id="infoModal.id"
      :title="infoModal.title"
      ok-only
      @hide="resetInfoModal"
      :codigo="infoModal.codigo"
      hide-footer
    >
      <tr v-for="(value, key) in infoModal.content" :key="key">
        <th>{{ key }}</th>
        <td>{{ value }}</td>
      </tr>
      <b-button
        v-show="false"
        class="mt-3"
        variant="outline-primary"
        block
        :to="rutaNuevo + '?id=' + idSeleccionado"
        ><i class="fa fa-eye"></i> M√°s informaci√≥n</b-button
      >
      <!-- :to="rutaNuevo + '?id=' + idSeleccionado" -->
    </b-modal>

    <b-pagination
      class="mt-1"
