<script>
import servicioCobranza from "@/services/servicio-cobranza.js";
import servicioDosificador from "@/services/servicio-dosificador.js";
import servicioPedidos from "@/services/servicio-pedidos.js";
import servicioUsuarios from "@/services/servicio-usuarios.js";
import servicioClientes from "@/services/servicio-clientes.js";
import servicioEquipos from "@/services/servicio-equipos.js";

import Swal from "sweetalert2";

export default {
  props: ["items", "fields", "rutaNuevo", "permisoEscritura", "modalEspecial"],
  data() {
    return {
      observacion: "",
      modalShow: false,
      config: {
        toolbar: false,
      },
      mostrarPdf: false,
      pdfBase64: "",
      pdf: "",
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      sortBy: "",
      sortDesc: false,
      bordered: true,
      hover: true,
      striped: true,
      sortDirection: "asc",
      filter: null,
      filterOn: [],
      componentsModal: [],
      infoModal: {
        id: "info-modal",
        title: "",
        content: "",
        codigo: "",
      },
      currentSection: document.location.href.split("/"),
      idSeleccionado: 0,
    };
  },
  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => {
          return { text: f.label, value: f.key };
        });
    },
  },
  mounted() {
    // let id = this.$route.params.id;
    console.log("---->" + this.items.length);
    setTimeout(() => {
      this.totalRows = this.items.length;
    }, 1000);
  },
  methods: {
    redirect() {
      // this.$loading(true);
      setTimeout(() => {
        this.$router.push({ path: `${this.rutaNuevo}0` });
        // this.$loading(false);
      }, 500);
    },
    redirectID(item) {
      this.$loading(true);
      setTimeout(() => {
        // console.log('RUTA NUEVA', this.rutaNuevo);
        // console.log('Item', item);
        // this.$router.push({ path: `${this.rutaNuevo+'?id='+item.codigo}`, params });
        this.$router.push({
          name: this.rutaNuevo,
          params: {
            id: item.codigo,
          },
        });

        this.$loading(false);
      }, 500);
    },
    generarUsuario(item, index, button) {
      this.modal(item, index, button, "generarUsuario");
    },
    verUsuarioCliente(item, index, button) {
      this.modal(item, index, button, "verUsuarioCte");
    },
    infoDocLaboral(item) {
      // this.infoModal.title = `Documentaci√≥n del empleado: \n ${item.nombreCompleto}`
      // this.infoModal.content = 'Documentos registrados: \n Acta de nacimiento '
      // this.$root.$emit('bv::show::modal', this.infoModal.id, button)
      this.$router.push({
        path: `Empleados/Documentacion/${item.codEmpleado}`,
      });
    },
    modalEmit(item, index) {
      let data = {
        item,
        index,
      };
      this.$emit("show-modal", data);
    },
    modal(item, index, button, ultimaParteLink) {
      let content = "";
      this.idSeleccionado = item.codigo;
      //Checo si la ultimaParteLink viene en el metodo
      ultimaParteLink =
        ultimaParteLink == undefined
          ? this.currentSection[this.currentSection.length - 1].toLowerCase()
          : ultimaParteLink;
      switch (ultimaParteLink) {
        case "clientes":
          this.infoModal.title = `Datos del cliente ${item.nombreCompleto}`;
          content += `{ "RFC": "${item.rFC}", "Tel√©fono": "${item.telefono}","Celular": "${item.celular}","Correo": "${item.correo}",`;
          content += `"Registrado": "${item.fechaCorta}","Cr√©dito": "${item.creditoFormat}" }`;
          content = JSON.parse(content);
          this.infoModal.content = content;
          this.$root.$emit("bv::show::modal", this.infoModal.id, button);
          break;
        case "empleados":
          break;
        case "equipos":
          console.log(item);
          this.infoModal.title = `Datos del equipo ${item.identificador}`;
          content += `{ "Marca": "${item.marca}", "Modelo": "${item.modelo}","Nombre": "${item.descripcion}","Numero de serie": "${item.numeroSerie}", "Numero de serie de motor " : "${item.numeroSerieMotor}",`;
          content += `"tipoEquipo": "${item.tipoEquipo}" }`;
          content = JSON.parse(content);
          this.infoModal.content = content;
          this.$root.$emit("bv::show::modal", this.infoModal.id, button);
          break;
        case "marca-equipo":
          this.infoModal.title = `Datos de la marca ${item.descripcion}`;
          content += `{ "Marca": "${item.descripcion}", "Estatus": "${
            item.estatus ? "Activo" : "Inactivo"
          }"}`;
          content = JSON.parse(content);
          this.infoModal.content = content;
          this.$root.$emit("bv::show::modal", this.infoModal.id, button);
          break;
        case "generarUsuario":
