<template>
  <div class="container py-5">
    <h3><i class="fas fa-hard-hat"></i> Registro de tipos de resistencias</h3>
    <div class="py-5">
      <b-form @submit="onSubmit">
        <b-row>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-2"
              label="Descripci√≥n corta *"
              label-for="txtDescripcionCorta"
            >
              <b-form-input
                id="txtDescripcionCorta"
                v-model="form.descripcionCorta"
                type="text"
                required
                placeholder="Descripci√≥n corta"
                class="text-capitalize"
              >
              </b-form-input>
            </b-form-group>
          </b-col>
          <b-col class="col-md-8 col-sm-12 col-12">
            <b-form-group
              id="input-group-3"
              label="Descripci√≥n *"
              label-for="txtDescripcion"
            >
              <b-form-input
                id="txtDescripcion"
                type="text"
                v-model="form.descripcion"
                required
                placeholder="Descripci√≥n"
                class="text-capitalize"
              >
              </b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
        <!-- <b-row>
          <b-col class="col-md-6 col-sm-12 col-12" align-self="end">
            <b-button type="submit" block variant="success" class="mr-2 mt-2"
              ><i class="fa fa-save"></i> Guardar</b-button
            >
          </b-col>
          <b-col class="col-md-6 col-sm-12 col-12" align-self="end">
            <b-button
              @click="limpiar"
              type="reset"
              block
              variant="outline-danger"
              class="mt-2"
              ><i class="fas fa-broom"></i>Limpiar</b-button
            >
          </b-col>
        </b-row> -->
        <b-row>
          <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
          <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
            <b-button type="submit" block variant="success" class="mr-2 mt-2">
              <i class="fa fa-save"></i> Guardar
            </b-button>
          </b-col>
          <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
            <b-button
              @click="cancelar"
              block
              variant="danger"
              class="mr-2 mt-2"
            >
              Cancelar
            </b-button>
          </b-col>
        </b-row>
      </b-form>
    </div>
  </div>
</template>
<script>
// import servicio from '@/services/servicio-clientes'
// import servicioAgente from '@/services/servicio-agentes'
import servicioMateriales from "@/services/servicio-materiales";
export default {
  data() {
    return {
      // selectedRazonSocial:null,
      id: 0,
      form: {
        descripcionCorta: "",
        descripcion: "",
      },
      resistencias: [{ value: null, text: "Seleccione una resistencia..." }],
    };
  },
  mounted() {
    // if (this.verificaTokenValido()) {
      this.resistencia = null;
      this.id = this.$route.params.id != undefined ? this.$route.params.id : 0;
      this.ConsultarResistencias();
    // }
  },
  computed: {
    validationNombres() {
      let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
      return this.nombre.match(letras) && this.nombre.length > 1 ? true : false;
    },
    validationApellidoP() {
      let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
      return this.apellidoP.match(letras) && this.apellidoP.length > 1
        ? true
        : false;
    },
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      this.guardarTipoResistencia();
    },
    limpiar() {
      this.form.descripcionCorta = "";
      this.form.descripcion = "";
    },
    guardarTipoResistencia() {
      let tipoResistencia = {
        codigo: this.id,
        descripcionCorta: this.form.descripcionCorta,
        descripcion: this.form.descripcion,
      };
      servicioMateriales.guardarTipoResistencia(tipoResistencia).then((r) => {
        if (r.value) {
          this.$swal.fire({
            text: `El tipo de resistencia se ha guardado exitosamente!`,
            icon: "success",
          });
          this.limpiar();
        } else {
          this.$swal.fire({
            text: `No se pudo guardar el tipo de resistencia, contacte a sistemas`,
            icon: "error",
          });
        }
      });
    },
    ConsultarResistencias() {
      servicioMateriales.resistencias().then((r) => {
        if (r.value) {
          r.data.forEach((element) => {
            console.log(element);
            let resistencia = {
              value: element.codigo,
              text: element.descripcionCorta,
            };
            this.resistencias.push(resistencia);
          });
        }
      });
    },
    cancelar() {
      let tipoActividad =
        this.id == 0
          ? "¬øDesea cancelar el registro de un nuevo tipo de resistencia?"
          : "¬øDesea cancelar la actualizaci√≥n del tipo de resistencia?";
      console.log(tipoActividad);
      this.$swal
        .fire({
          title: tipoActividad,
          text: "",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, volver al cat√°logo",
          cancelButtonText: "Cancelar",
        })
        .then((result) => {
          if (result.value) {
            window.history.back();
          }
        });
    },
  },
};
</script>
