<template>
  <div class="container py-5">
    <h3><i class="fas"></i> Registro de tipos de equipo</h3>
    <div class="py-5">
      <b-form @submit="onSubmit">
        <b-row>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-2"
              label="Nombre de Equipo"
              label-for="txtNombreEquipo"
            >
              <b-form-input
                id="txtNombreEquipo"
                v-model="nombreEquipo"
                type="text"
                required
                ref="txtNombreEquipo"
                placeholder="Cami√≥n Revolvedor"
                class="text-capitalize"
              ></b-form-input>
            </b-form-group>
          </b-col>
          <b-col class="col-md-2 col-sm-12 col-12">
            <b-col>
              <b-form-group label="Abreviaci√≥n" label-for="txtAbreviacion">
                <b-input-group size="sm">
                  <b-form-input
                    id="txtAbreviacion"
                    v-model="form.abreviacion"
                    type="text"
                    required
                    :disabled="validated == 1"
                    ref="txtAbreviacion"
                    placeholder="CR"
                    class="text-capitalize"
                  >
                  </b-form-input>
                  <b-input-group-append>
                    <b-button @click="habilitarInput">
                      <i class="fas fa-edit"></i>
                    </b-button>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-col>
          <b-col class="col-md-6 col-sm-12 col-12">
            <b-form-group
              id="input-group-3"
              label="Descripci√≥n"
              label-for="txtDescripcion"
            >
              <b-form-input
                id="txtDescripcion"
                type="text"
                v-model="form.descripcion"
                placeholder="Equipo para la producci√≥n de concreto"
              />
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
          <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
            <b-button type="submit" block variant="success" class="mr-2 mt-2">
              <i class="fa fa-save"></i> Guardar
            </b-button>
          </b-col>
          <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
            <b-button
              @click="cancelar"
              block
              variant="danger"
              class="mr-2 mt-2"
            >
              Cancelar
            </b-button>
          </b-col>
        </b-row>
      </b-form>
    </div>
  </div>
</template>
<script>
import servicio from "@/services/servicio-equipos";
export default {
  data() {
    return {
      codTipoEquipo: 0,
      validated: 1,
      form: {
        nombreEquipo: "",
        descripcion: "",
        abreviacion: "",
      },
    };
  },
  mounted() {
    // if (this.verificaTokenValido()) {
      this.codTipoEquipo = this.$route.params.id;
      this.cargarTipoRegistro();
      this.$refs.txtNombreEquipo.focus();
    // }
  },
  computed: {
      nombreEquipo: {
        get(){
          return this.form.nombreEquipo;
        },
        set(newValue){
            this.form.nombreEquipo = newValue;
            this.form.abreviacion = this.obtenerAbreviacion(newValue);
        }
    } 
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      this.guardarTipoEquipo();
    },
    habilitarInput() {
      this.validated = false;
      this.$refs.txtAbreviacion.focus();
    },
    cargarTipoRegistro() {
      this.$loading(true);
    
      if ( this.codTipoEquipo > 0) {
        servicio
          .tiposEquipo(this.codTipoEquipo)
          .then((r) => {
            console.log(r.data[0])
            this.form.nombreEquipo = r.data[0].nombre;
            this.form.descripcion = r.data[0].descripcion;
            this.form.abreviacion = r.data[0].abreviacion;
          })
          .catch((error) => {
            console.log(error);
          });
      }
      this.$loading(false);
    },
    guardarTipoEquipo() {
      let tipoEquipo = {
        codigo: this.codTipoEquipo,
        nombre: this.nombreEquipo,
        descripcion: this.form.descripcion,
        abreviacion: this.form.abreviacion,
      };
      servicio.guardarTipoEquipo(tipoEquipo).then((r) => {
        if (r.value) {
          this.$swal.fire({
            text: r.message,
            icon: "success",
          });
          window.history.back();
        } else {
          this.$swal.fire({
            text: r.message,
            icon: "error",
          });
        }
      });
    },

    obtenerAbreviacion(nombreEquipo) {
      let abreviacion = "";
      let palabras = nombreEquipo.split(" ");
      palabras.forEach((element) => {
        if (element.length > 2) {
          abreviacion =
            abreviacion + element.toString().substring(0, 1).toUpperCase();
        }
      });
      return abreviacion;
    },

    cancelar() {
      let tipoActividad =
        this.codTipoEquipo == 0
          ? "¬øDesea cancelar el registro de un nuevo tipo de equipo?"
          : "¬øDesea cancelar la actualizaci√≥n del registro?";
      console.log(tipoActividad);
      this.$swal
        .fire({
          title: tipoActividad,
          text: "",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, volver al cat√°logo",
          cancelButtonText: "Cancelar",
        })
        .then((result) => {
          if (result.value) {
            window.history.back();
          }
        });
    },
  },
};
</script>
