<template>
<div>
    <div class="container mt-3 mb-5">
        <div class="mt-3">
            <h3><i class="fas fa-clipboard-list" ></i> Detalles de la Nota de Remisi√≥n</h3>
        </div>
        <hr>

        <!-- Folios -->
        <div class="row">
            <div class="form-group col-sm-12 col-md-3 col-xl-3 mb-1" >
                <label>Folio Nota:</label>
                <input v-model="notaRemision.folio"
                        class="form-control"
                        disabled
                        />
            </div>

            <div class="form-group col-sm-12 col-md-3 col-xl-3 mb-1" >
                <label>Folio Pedido:</label>
                <input v-model="notaRemision.folioPedido"
                        class="form-control"
                        disabled
                        />
            </div>

            <div class="form-group col-sm-4 col-md-3 col-lg-2 mb-1" >
                <label>Estatus Nota:</label>
                <div>
                    <b-button variant="secondary" v-if="notaRemision.estatus == 'P'" disabled>Pendiente</b-button>
                    <b-button variant="primary" v-if="notaRemision.estatus == 'S'" disabled>Surtiendo</b-button>
                    <b-button variant="success" v-if="notaRemision.estatus == 'T'" disabled>Terminada</b-button>
                </div>
            </div>

            <div class="form-group col-sm-4 col-md-3 col-lg-2 mb-1" >
                <label>Estatus Pago:</label>
                <div>
                    <b-button variant="secondary" v-if="notaRemision.estatusPago == 'P'" disabled>Pendiente</b-button>
                    <b-button variant="primary" v-if="notaRemision.estatusPago == 'A'" disabled>Abonada</b-button>
                    <b-button variant="success" v-if="notaRemision.estatusPago == 'T'" disabled>Terminada</b-button>
                </div>
            </div>
        </div>

        <!-- Cliente, Obra -->
        <div class="row">
            <div class="form-group col-sm-12 col-md-4 col-xl-3 mb-1" >
                <label>Cliente:</label>
                <input v-model="notaRemision.cliente"
                        class="form-control"
                        disabled
                        />
            </div>
            <div class="form-group col-sm-12 col-md-4 col-xl-3 mb-1" >
                <label>Obra:</label>
                <input v-model="notaRemision.obra"
                        class="form-control"
                        disabled
                        />
            </div>

            <div class="form-group col-sm-12 col-md-4 col-xl-3 mb-1" >
                <label>Vendedor:</label>
                <input v-model="notaRemision.vendedor"
                        class="form-control"
                        disabled
                        />
            </div>
        </div>
        
        <!-- Nomenclatura, cantidad, importe -->
        <div class="row">
            <div class="form-group col-sm-12 col-lg-4 col-xl-3 mb-1" >
                <label>Nomenclatura:</label>
                <input v-model="notaRemision.producto"
                        class="form-control"
                        disabled
                        />
            </div>

            <div class="form-group col-sm-12 col-lg-4 col-xl-3 mb-1" >
                <label>Cantidad:</label>
                <input v-model="notaRemision.cantidad"
                        class="form-control"
                        disabled
                        />
            </div>
        </div>

        <!-- Producto -->
        <div class="row">
            <div class="form-group col-sm-12 mb-1" >
                <label>Producto:</label>
                <input v-model="notaRemision.productoDescripcion"
                        class="form-control"
                        disabled
                        />
            </div>
        </div>

        <!-- Importe, Abonado, Diferencia -->
        <div class="row">
            <div class="form-group col-sm-12 col-md-3 mb-1" >
                <label>Importe:</label>
                <input v-model="notaRemision.importeFormato"
                        class="form-control"
                        disabled
                        />
            </div>

            <div class="form-group col-sm-12 col-md-3 mb-1" >
                <label>Abonado:</label>
                <input v-model="notaRemision.abonadoFormato"
                        class="form-control"
                        disabled
                        />
            </div>

            <div class="form-group col-sm-12 col-md-3 mb-1" >
                <label>Diferencia:</label>
                <input v-model="notaRemision.diferenciaFormato"
                        class="form-control"
                        disabled
                        />
            </div>
        </div>

        
        <hr>

        <div v-if="detalleAbonosNotaRemision.length > 0">
            <h4>Historial de Abonos</h4>
            <!-- Main table element -->
            <b-table small
                    stacked="md"
                    head-variant="dark"
                    :hover="hover"
                    :striped="striped"
                    :bordered="bordered"
                    :items="detalleAbonosNotaRemision"
                    :fields="fields"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :filterIncludedFields="filterOn"
                    :sort-by.sync="sortBy"
                    :sort-desc.sync="sortDesc"
                    :sort-direction="sortDirection"
                    @filtered="onFiltered">

            </b-table>
        </div>




        <!-- boton cancelar -->
        <b-row>
			<b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
			<b-col class="col-md-3 col-sm-12 col-12" align-self="end"></b-col>
			<b-col class="col-md-3 col-sm-12 col-12" align-self="end">
				<b-button :to="rutaCatalogo" block variant="danger" class="mr-2 mt-2">
					Regresar
				</b-button>
			</b-col>
		</b-row>
    </div>
        
</div>
</template>

<script>

import servicio from "@/services/servicio-cobranza";
import Swal from "sweetalert2";

export default {

    data() {

        return {
            parametro: 0,
            notaRemision: {},
            detalleAbonosNotaRemision: {},
            rutaCatalogo: "/cobranza/notas-de-remision",





            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],


            fields: [
                { key: "importeFormato", label: "Importe", sortable: true, class: "text-center", },
                { key: "fecha", label: "Fecha", sortable: true, class: "text-center", },
                { key: "usuario", label: "Usuario", sortable: true, class: "text-center", },
            ],
        };

    },

    components: {
    },
    
    watch: {
        
    },

    async created() {
        this.parametro = this.$route.params.id != undefined ? this.$route.params.id : 0;
        await this.ObtenerNotasRemision();
        await this.ObtenerDetalleAbonosNotasRemision();
    },
    mounted() {},

    computed: {},
    methods: {

        async ObtenerNotasRemision() {
            this.$loading(true);
            await servicio.obtenerNotaRemisionCobranza(this.parametro)
                            .then((r) => {

                                if(r.value) {
                                    console.log(r);
                                    this.notaRemision = r.data[0];
                                    this.$loading(false);
                                } else {
                                    Swal.fire({
                                        title: "¬°ALERTA!",
                                        text: r.message,
                                        icon: "error",
                                    });
                                }
                                
                            })
                            .catch((error) => {
                                this.$loading(false);
                                console.log(error);
                            });
        },

        async ObtenerDetalleAbonosNotasRemision() {
            this.$loading(true);

            await servicio.obtenerDetalleAbonos(this.parametro)
                    .then((r) => {

                        if(r.value) {
                            console.log(r);
                            this.detalleAbonosNotaRemision = r.data;
                            this.$loading(false);
                        } else {
                            Swal.fire({
                                title: "¬°ALERTA!",
                                text: r.message,
                                icon: "error",
                            });
                        }
                        
                    })
                    .catch((error) => {
                        this.$loading(false);
                        console.log(error);
                    });
           
        },

        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },

    }
        
};
</script>

<style scoped>

    

</style>