<template>
  <div>
    <section class="login-container">
      <div class="login-header">
        <img src="https://concretos2h.com.mx/img/3447/818.png"
                alt="Logo"
                width="56%"
                class="ml-5"/>
        <hr/>
      </div>
      <article class="login-body">
        <form>
            
          <!-- usuario -->
          <b-input-group class="mb-2">
            <b-input-group-prepend is-text>
              <i class="fa fa-user fa-lg"></i>
            </b-input-group-prepend>
            <b-form-input v-model="credenciales.usuario"
                            type="text"
                            placeholder="Nombre de usuario"
                            @keyup.enter="login"
                            autocomplete="off">
            </b-form-input>
          </b-input-group>

          <!-- password -->
          <b-input-group class="mb-2 py-2">
            <b-input-group-prepend is-text>
              <i class="fa fa-lock fa-lg"></i>
            </b-input-group-prepend>
            <b-form-input v-model="credenciales.password"
                            @keyup.enter="login"
                            type="password"
                            placeholder="Contrase√±a">
            </b-form-input>
          </b-input-group>

          <!-- login -->
          <div class="py-1">
            <button class="btn btn-block btn-success-2h" 
                    @click="login"
                    type="button">
              Ingresar
            </button>
          </div>
        </form>
      </article>
    </section>
  </div>
</template>
<script>
import servicio from '@/services/servicio-login'
export default {
    data(){
        return {
            credenciales:{
                usuario: '',
                password: ''
            },
            tokenWebAdmin: {
                token:null,
                expiresAt:null,
                user:null,
                permisos:null,
                empresas:null
            }
        }
    },
    methods:{
        async login(){
            this.$loading(true);
            var r = await servicio.login(this.credenciales)
            if (r.value) {
                this.tokenWebAdmin.token = r.data.accessToken.accessToken
                this.tokenWebAdmin.expiresAt = r.data.accessToken.exp
                this.tokenWebAdmin.user = JSON.stringify(r.data.user)
                this.tokenWebAdmin.permisos = JSON.stringify(r.data.permisos) 
                // this.tokenWebAdmin.empresas = JSON.stringify(r.data.empresas)
                var decode = decodeURIComponent(encodeURI(this.tokenWebAdmin.user))
                this.tokenWebAdmin.user = decode
                window.localStorage.tokenWebAdmin = JSON.stringify(this.tokenWebAdmin)
                this.$router.push({ path: "/" })
            } else {
                this.$swal.fire({
                    text: r.message,
                    icon: "warning"
                })
            }

            this.$loading(false)
        }
    }
}
</script>

<style>
    body {
        background-color: whitesmoke !important;
        background-size: cover !important;
    }
    .card {
        background-color: rgba(255, 255, 255, 0.7);
    }
    .btn-azul-2h {
        background-color: #1b3582;
        color: whitesmoke;
    }
    .btn-azul-2h:hover {
        background-color: rgb(27, 92, 130);
        color: whitesmoke;
    }
    .btn-success-2h {
        color: white !important;
        /* background-color:#28a745 !important;         */
        background-image: linear-gradient(-180deg, #173f5f, #20639b 50%);
    }
    .btn-success-2h:hover {
        color: whitesmoke !important;
        background-color: #28a745;
        background-image: linear-gradient(-180deg, #173f5f, #20639b 90%);
    }
    .full-page {
        background-image: url("../assets/img/concretos.jpg");
        background-repeat: no-repeat;
        /* background-size: 100% 200%; */
    }
    .footer {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        background-color: gray;
        color: white;
        text-align: center;
    }
    .login-container {
        background-color: #ffffff;
        border: 1px solid #d2d2d2;
        border-radius: 8px;
        box-shadow: 0 10px 5px #888;
        height: 311px;
        margin: auto;
        overflow: auto;
        padding: 12px 12px 12px 12px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 308px;
    }
    .login-header {
        margin-top: 20px;
    }
</style>