const nextControl = {}

nextControl.install = function(Vue){
    Vue.directive('nextcontrol', {        
        inserted(el, binding){
            el.onkeyup = (e) => {
                if(e.keyCode == 13){
                    //console.log(binding.arg)
                    document.getElementById(binding.arg).focus()
                }
            }
        }
    })
}

export default nextControl