import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                      E Q U I P O S
//............................................................
servicio.equipos = (codEquipo) => {
    if (codEquipo == null || codEquipo == undefined || codEquipo == 0) {
        return base.get(`/equipos/todos`)
            .then(r => {
                return r.data;
            })
            .catch(error => {
                return error;
            })
    } else {
        return base.get(`/equipos/${codEquipo}`)
            .then(r => {
                console.log("EQUIPO ", r.data);
                return r.data;
            })
            .catch(error => {
                return error;
            })
    }
}

servicio.guardar = (equipo) => {
    console.log(equipo);
    return base.post("/equipos/guardar", equipo, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

