<template>
<div>
    <div class="container">
        
        <div>
            <h3><i class="fa fa-chart-line"></i> Reporte de Tiros Nocturnos</h3>
        </div>
        <hr>
        
        <!-- Rango de fechas -->
        <b-row>
            <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                <label>Desde:</label>
                <b-form-datepicker v-model="desde" class="mb-2" locale="es"></b-form-datepicker>
            </div>
            <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                <label>Hasta:</label>
                <b-form-datepicker v-model="hasta" class="mb-2" locale="es"
                    ></b-form-datepicker>
            </div>
        </b-row>

        <!-- Equipo -->
        <div class="row">
            <div class="form-group col-sm-12 col-md-4 mb-2">
                <label>Equipos:</label>
                <div class="input-group">
                    <select v-model="equipo"
                            class="form-control"
                            @change="restablecerDatos">
                        <option v-for="equipo in equipos " 
                                v-bind:key="equipo.value"
                                v-bind:value="equipo"
                                >  {{equipo.text}}
                        </option>
                    </select>
                </div>
            </div>
        </div>

        <div >
            <!-- botones -->
            <b-row>
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                    <b-button @click="BuscarDatosReporteNocturnos" block variant="primary" class="mr-2 mt-2">
                        <i class="fas fa-search"></i> Buscar Datos
                    </b-button>
                </b-col>
                
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end" v-if="datos">
                    <b-button @click="GenerarReporteMensual" block variant="dark" class="mr-2 mt-2">
                        <i class="fas fa-chart-bar"></i> Generar Reporte
                    </b-button>
                </b-col>

                <b-col class="col-md-3 col-sm-12 col-12" align-self="end" v-if="datos">
                    <b-button @click="exportarExcel" block variant="success" class="mr-2 mt-2">
                        <i class="fas fa-file-excel"></i> Exportar Excel
                    </b-button>
                </b-col>
            </b-row>

            <div v-if="datos">
                <hr>
                <b-row class="my-2">
                    <b-col md="4">
                        <b-form-group label="Filtrar"
                                    label-cols-sm="1"
                                    label-cols-md="2"
                                    label-size="sm"
                                    label-for="filterInput">
                            <b-input-group size="sm">
                                <b-form-input v-model="filter"
                                                type="search"
                                                id="filterInput"
                                                placeholder="Escribe el filtro..."
                                                autocomplete="off">
                                </b-form-input>
                                <b-input-group-append>
                                    <b-button :disabled="!filter" @click="filter = ''">
                                        <i class="fas fa-times"></i>
                                    </b-button>
                                </b-input-group-append>
                            </b-input-group>
                        </b-form-group>
                    </b-col>
                    <b-col md="3">
                        <b-form-group label="Por p√°gina"
                                        label-cols-sm="2"
                                        label-cols-md="2"
                                        label-cols-lg="4"
                                        label-size="sm"
                                        label-for="perPageSelect">
                            <b-form-select v-model="perPage"
                                            id="perPageSelect"
                                            size="sm"
                                            :options="pageOptions">
                            </b-form-select>
                        </b-form-group>
                    </b-col>
                </b-row>
                <!-- Main table element -->
                <b-table small
                        stacked="md"
                        head-variant="dark"
                        :hover="hover"
                        :striped="striped"
                        :bordered="bordered"
                        :items="items"
                        :fields="fields"
                        :current-page="currentPage"
                        :per-page="perPage"
                        :filter="filter"
                        :filterIncludedFields="filterOn"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :sort-direction="sortDirection"
                        @filtered="onFiltered">
                    <template v-slot:cell(estatus)="row">
                        <b-badge variant="danger" v-if="row.item.estatusNuevo == 'D'" class="p-2">Detenido Limite Credito</b-badge>
                        <b-badge variant="danger" v-if="row.item.estatusNuevo == 'DP'" class="p-2">Detenido Falta Pago</b-badge>
                    </template>
                </b-table>
                
                <b-pagination v-model="currentPage"
                                class="mt-1"
                                :total-rows="totalRows"
                                :per-page="perPage"
                                align="center"
                                aria-controls="my-table">
                </b-pagination>
            </div>
            
        </div>
    </div>
</div>
</template>

<script>

import servicioEquipos from "@/services/servicio-equipos";
import servicioReportes from '@/services/servicio-reportes';

export default {
    data() {

        const now = new Date()
        const last = new Date(now.getFullYear(), now.getMonth()-1, now.getDate())
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())

        var lastDate = last.toISOString().slice(0,10);
        var todayDate = today.toISOString().slice(0,10);


        return {
            desde: lastDate,
            hasta: todayDate,
            equipo: 0,
            equipos: [{ value: 0, text: "Todos" }],

            //
            datos: false,
            items: [],
            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],
            fields: [
                {
                    key: "notaRemision",
                    label: "Nota Remisi√≥n",
                    sortable: true,
                    sortDirection: "desc",
                },
                {
                    key: "fechaSalida",
                    label: "Fecha",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "equipo",
                    label: "Equipo",
                    sortable: true,
                    sortDirection: "desc",
                },
                {
                    key: "operador",
                    label: "Operador",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "tiempoFuera",
                    label: "Tiempo Fuera",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "kilometrosRecorridos",
                    label: "Kilometros Recorridos",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "horometrajeRecorrido",
                    label: "Horometraje Recorrido",
                    sortable: true,
                    class: "text-center",
                },
                
                // { key: "estatus", label: "Estatus", class: "text-center" },

            ],



        }

    },
    components: {
    },
    mounted() {

    },
    async created() {
        await this.ObtenerEquipos();
    },
    computed: {
        sortOptions() {
            // Create an options list from our fields
            return this.fields
            .filter((f) => f.sortable)
            .map((f) => {
            return { text: f.label, value: f.key };
            });
        },
    },
    methods: {

        async ObtenerEquipos() {
            this.$loading(true);
            await servicioEquipos.equipos(0)
                    .then((r) => {
                        if (r.value) {
                            r.data.forEach((element) => {
                                let equipo = {
                                    value: element.codigo,
                                    text: element.nombre,
                                };
                                this.equipos.push(equipo);
                            });

                            console.log(this.equipos);

                            this.equipo = this.equipos[0];
                        }
                    });
                    this.$loading(false);
        },

        async BuscarDatosReporteNocturnos() {
            this.items = [];
            console.log('Equipo -> ', this.equipo);
            this.$loading(true);
            let result = await servicioReportes.ObtenerReporteMensualNocturnos(this.desde, this.hasta, this.equipo.value);
            if(result.value) {
                this.datos = true;
                this.items = result.data;
                console.log(this.items);
                this.CargarRenglones();
                this.$loading(false);
            } else {
                this.datos = false;
                this.$loading(false);
                this.$swal.fire({
                    text: result.message,
                    icon: "warning"
                });
            }
        },

        async GenerarReporteMensual(){
            this.$loading(true);
            // console.log('Aqui se imprime el rpt');
            let result = await servicioReportes.GenerarReporteNocturnos(this.desde, this.hasta, this.equipo.value, this.items);
            if(result.value) {
                await this.ImprimirReporteNocturnos(result.data);
                this.$loading(false);
                const Toast = this.$swal.mixin({
                    toast: true,
                    position: 'top-end',
                    showConfirmButton: false,
                    timer: 3000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                        toast.addEventListener('mouseenter', this.$swal.stopTimer)
                        toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                    }
                })

                Toast.fire({
                    icon: 'success',
                    title: 'Reporte de entradas y salidas descargado.'
                });
            } else {
                // console.log(result);
                this.$loading(false);
                this.$swal.fire({
                    text: result.message,
                    icon: "warning"
                });
            }
        },
        
        async ImprimirReporteNocturnos(pdfResult) {
            const downloadLink = document.createElement("a");
            const fileName = "Reporte de Tiros Nocturnos";
            downloadLink.href = "data:application/pdf;base64," + pdfResult;
            downloadLink.download = fileName;
            await downloadLink.click();
        },

        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },

        CargarRenglones() {
            setTimeout(() => {
            this.totalRows = this.items.length;
            }, 1000);
        },

        restablecerDatos() {
            this.datos = false;
            this.items = [];
        },

        exportarExcel() {
            this.$loading(true);
            let csv = Object.keys(this.items[0]).join("‚ô™") + "\n"; //headers
                            
            for (var item in this.items) {
                csv += Object.values(this.items[item]).join("‚ô™");
                csv += "\n";
            }
            console.log('CSV #1 ', csv);
            
            csv = csv.replaceAll(',', ' ')
            csv = csv.replaceAll('‚ô™', ',')
            
            console.log('CSV #2 ', csv);

            const blob = new Blob([csv], { type: 'text/csv' });
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.setAttribute('hidden', ''); 
            a.setAttribute('href', url);
            a.setAttribute('download', 'download.csv');
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            this.$loading(false);
        }

    }
   
}
</script>

<style scoped>

</style>
