const isAutenticated = () => {
    let token = undefined;
    if (window.localStorage.tokenWebAdmin) {
        let lstoken = JSON.parse(window.localStorage.tokenWebAdmin)
        token = lstoken.token;
        var date = new Date(lstoken.expiresAt);

        if (date < Date.now()) {
            //window.localStorage.removeItem('user')
            //window.localStorage.removeItem('token')
            //window.localStorage.removeItem('expiresAt')
            //window.localStorage.removeItem('refreshToken')
            window.localStorage.removeItem('tokenWebAdmin')

            Swal.fire({
                text: 'La sesi√≥n ha caducado, por favor vuelva a autenticarse para continuar',
                icon: 'warning'
            })
        }
    }

    //return window.localStorage.token
    return token
}

router.beforeEach((to, from, next) => {
    // console.log("to => ",to, "from => ", from)
    // console.log("isAutenticated => ", to.name == 'login' && isAutenticated())

    if (!isAutenticated() && to.name != "login") {
        // console.log("RouteTo => ", localStorage.getItem("RouteTo"))
        if (!localStorage.getItem("RouteTo")) {
            localStorage.setItem("RouteTo", to.path)
        }
    }

    if (!to.meta.isPublic && !isAutenticated()) {
        return next({ path: '/login' })
    }

    if (to.name == 'login' && isAutenticated()) {
        return next({ path: '/' })
    }

    if (isAutenticated() && localStorage.getItem("RouteTo")) {
        let path = localStorage.getItem("RouteTo")
        localStorage.removeItem("RouteTo")
        return next({ path: path })
    }
    return next()
})


export default router