<template>
  <div class="container py-5">
    <h3><i class="fas fa-users"></i> Registro de empleados</h3>
    <div class="py-5">
      <b-form @submit="onSubmit">
        <b-row>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group id="input-group-1" label="Nombre *" label-for="txtNombre">
              <b-form-input
                id="txtNombre"
                type="text"
                required
                placeholder="Nombre(s)"
                v-model="form.nombre"
                class="text-capitalize"
              >
              </b-form-input>
              <!-- <b-form-invalid-feedback :state="validationNombres">
                                El nombre deber√° ser m√°s largo...
                            </b-form-invalid-feedback>
                            <b-form-valid-feedback :state="validationNombres">
                                Correcto
                            </b-form-valid-feedback> -->
            </b-form-group>
          </b-col>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-2"
              label="Apellido Paterno *"
              label-for="txtApellidoP"
            >
              <b-form-input
                id="txtApellidoP"
                v-model="form.apellidoP"
                type="text"
                required
                placeholder="Apellido Paterno"
                class="text-capitalize"
              >
              </b-form-input>
            </b-form-group>
          </b-col>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-3"
              label="Apellido Materno *"
              label-for="txtApellidoM"
            >
              <b-form-input
                id="txtApellidoM"
                type="text"
                v-model="form.apellidoM"
                required
                placeholder="Apellido Materno"
                class="text-capitalize"
              >
              </b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group id="input-group-4" label="RFC" label-for="txtRFC">
              <b-form-input
                id="txtRFC"
                type="text"
                v-model="form.rFC"
                placeholder="RFC..."
                class="text-uppercase"
                :state="form.rFCValidate.esValido"
              >
              </b-form-input>
              <b-form-invalid-feedback id="input-live-feedback">
                Usa el formato v√°lido para el RFC
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-4"
              label="Estatus de empleado *"
              label-for="cboEstatus"
            >
              <b-form-select
                v-model="form.estatus"
                :options="optionsEstatusEmplados"
                id="cboEstatus"
                aria-placeholder="Seleccione..."
              >
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-4"
              label="Tipo/√Årea de empleado *"
              label-for="cboTipoEmpleado"
            >
              <b-form-select
                v-model="form.codigoTipoEmpleado"
                :options="optionsTiposEmpleados"
                id="cboTipoEmpleado"
                aria-placeholder="Seleccione..."
                @change="consultaSubTipo"
              >
              </b-form-select>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-4"
              label="N√∫mero de tel√©fono *"
              label-for="txtTelefono *"
            >
              <b-form-input
                id="txtTelefono"
                type="text"
                v-model="form.telefono"
                required
                placeholder="N√∫mero de tel√©fono..."
              >
              </b-form-input>
            </b-form-group>
          </b-col>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-4"
              label="N√∫mero de celular *"
              label-for="txtCelular"
            >
              <b-form-input
                id="txtCelular"
                type="text"
                v-model="form.celular"
                required
                placeholder="N√∫mero de celular..."
              >
              </b-form-input>
            </b-form-group>
          </b-col>
          <b-col class="col-md-4 col-sm-12 col-12">
            <b-form-group
              id="input-group-4"
              label="Correo electr√≥nico *"
              label-for="txtCorreoE"
            >
              <b-form-input
                id="txtCorreoE"
                type="text"
                v-model="form.correo"
                required
                placeholder="Correo electr√≥nico..."
              >
              </b-form-input>
            </b-form-group>
          </b-col>
        </b-row>

        <div class="row">
          <div class="col-12 ">
            <div class="card text-center">
              <h5 class="card-header text-left">Comisiones</h5>
              <div class="card-body">
                <div class="row">
                  <div
                    class="custom-control col-3"
                    v-for="(item, key, index) of comisiones"
                    :key="index"
                  >
                    <input
                      :id="key"
                      v-model="comisiones[key].seleccionado"
                      type="checkbox"
                      class="custom-control-input"
                    />
                    <label class="custom-control-label" :for="key">{{
                      item.descripcion
                    }}</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- direccion -->
        <b-row class="mt-3">
          <b-col class="col-md-12 col-sm-12 col-12">
            <div>
              <b-card bg-variant="light">
                <b-form-group
                  label-cols-lg="3"
                  label="Direcci√≥n"
                  label-size="lg"
                  label-class="font-weight-bold pt-0"
                  class="mb-0"
                >
                  <b-form-group
                    label-cols-sm="3"
                    label="C√≥digo Postal:"
                    label-align-sm="right"
                    label-for="txtCP"
                  >
                    <b-form-input
                      id="txtCP"
                      v-model="form.direccion.cP"
                      :disabled="disabledCP"
                      max="99999"
                    >
                    </b-form-input>
                  </b-form-group>

                  <b-form-group
                    label-cols-sm="3"
                    label="Municipio:"
                    label-align-sm="right"
                    label-for="txtMunicipio"
                  >
                    <b-form-input
                      id="txtMunicipio"
                      :disabled="disabledMunicipio"
                      v-model="form.direccion.municipio"
                    >
                    </b-form-input>
                  </b-form-group>

                  <b-form-group
                    label-cols-sm="3"
                    label="Colonia:"
                    label-align-sm="right"
                    label-for="txtColonia"
                  >
                    <b-form-select
                      v-model="form.direccion.codColonia"
                      :options="colonias"
                      id="cboColonia"
                      aria-placeholder="Seleccione..."
                    >
                    </b-form-select>
                  </b-form-group>
                  <b-form-group
                    label-cols-sm="3"
                    label="Calle y n√∫mero:"
                    label-align-sm="right"
                    label-for="txtCalle"
                  >
                    <b-form-input id="txtCalle" v-model="form.direccion.calleNumero">
                    </b-form-input>
                  </b-form-group>
                  <!-- <b-row>
                    <b-col>
                      <b-button
                        @click="limpiarDireccion"
                        block
                        variant="outline-danger"
                        class="mt-2"
                        ><i class="fas fa-broom"></i>Limpiar direcci√≥n</b-button
                      >
                    </b-col>
                  </b-row> -->
                </b-form-group>
              </b-card>
            </div>
          </b-col>
        </b-row>
        <!-- <b-row>
          <b-col class="col-md-6 col-sm-12 col-12" align-self="end">
            <b-button type="submit" block variant="success" class="mr-2 mt-2"
              ><i class="fa fa-save"></i> Guardar</b-button
            >
          </b-col>
          <b-col class="col-md-6 col-sm-12 col-12" align-self="end">
            <b-button
              @click="limpiar"
              type="reset"
              block
              variant="outline-danger"
              class="mt-2"
              ><i class="fas fa-broom"></i>Limpiar</b-button
            >
          </b-col>
        </b-row> -->
        <b-row>
          <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
          <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
            <b-button type="submit" block variant="success" class="mr-2 mt-2">
              <i class="fa fa-save"></i> Guardar
            </b-button>
          </b-col>
          <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
            <b-button @click="cancelar" block variant="danger" class="mr-2 mt-2">
              Cancelar
            </b-button>
          </b-col>
        </b-row>
      </b-form>
    </div>
  </div>
</template>
<script>
import servicioEmpleados from "@/services/servicio-empleados";
import servicioComisiones from "@/services/servicio-comisiones";
import servicioGenerales from "@/services/servicio-general";
export default {
  data() {
    return {
      // selectedRazonSocial:null,
      id: 0,
      comisiones: [],
      optionsEstatusEmplados: [
        { value: null, text: "Seleccione estatus..." },
        { value: true, text: "Activo" },
        { value: false, text: "Inactivo" },
      ],
      form: {
        nombre: "",
        apellidoP: "",
        apellidoM: "",
        rFC: "",
        domicilio: "",
        codigoTipoEmpleado: null,
        codigoSubTipo: null,
        estatus: null,
        telefono: "",
        celular: "",
        correo: "",
        fechaRegistro: "",
        direccion: {
          cP: "",
          codEstado: 0,
          estado: "",
          codMunicipio: 0,
          municipio: "",
          codColonia: 0,
          colonia: "",
          calleNumero: "",
        },
        rFCValidate: {
          msg: "",
          esValido: null,
        },
        codigo: "",
        tipo: "",
      },
      colonias: [{ value: null, text: "Seleccione una colonia..." }],
      disabledSubtipos: true,
      disabledMunicipio: true,
      disabledColonia: true,
      disabledCP: false,
      optionsTiposEmpleados: [{ value: null, text: "Seleccione un tipo/√°rea..." }],
      optionsSubTiposEmpleados: [{ value: null, text: "Seleccione un subtipo..." }],
    };
  },
  async mounted() {
    // if (this.verificaTokenValido()) {
    this.id = this.$route.params.id != undefined ? this.$route.params.id : 0;
    this.consultaTipoArea();
    this.cargaEmpleado();
    await this.comisionesExistentes();
    // }
  },
  computed: {
    validationNombres() {
      let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
      return this.nombre.match(letras) && this.nombre.length > 1 ? true : false;
    },
    validationApellidoP() {
      let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
      return this.apellidoP.match(letras) && this.apellidoP.length > 1 ? true : false;
    },
  },
  watch: {
    "form.direccion.cP": function (newValue, oldValue) {
      if (newValue != oldValue) {
        this.form.direccion.codColonia = null;
        if (newValue.length == 5) {
          this.obtenerDatosCP();
        } else {
          this.colonias = [{ value: null, text: "Seleccione una colonia..." }];
          this.form.direccion.municipio = "";
        }
      }
    },
    "form.rFC": function (newValue) {
      this.form.rFC = this.form.rFC.toUpperCase();
      if (
        /^([A-Z,√ë,&]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1])[A-Z|\d]{3})$/.test(
          newValue
        )
      ) {
        this.form.rFCValidate.esValido = true;
        this.form.rFCValidate.msg = "El RFC es v√°lido";
      } else {
        this.form.rFCValidate.esValido = false;
        this.form.rFCValidate.msg = "El RFC NO es v√°lido";
      }
    },
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      this.guardarEmpleado();
    },
    limpiar() {
      this.form.nombre = "";
      this.form.apellidoP = "";
      this.form.apellidoM = "";
      this.form.rFC = "";
      this.form.domicilio = "";
      this.form.codigoTipoEmpleado = null;
      this.form.codigoSubTipo = null;
      this.form.estatus = null;
      this.form.telefono = "";
      this.form.celular = "";
      this.form.correo = "";
      this.form.fechaRegistro = "";
    },
    limpiarDireccion() {
      this.form.direccion.cP = "";
      this.form.direccion.codEstado = 0;
      this.form.direccion.estado = "";
      this.form.direccion.codMunicipio = 0;
      this.form.direccion.municipio = "";
      this.form.direccion.codColonia = null;
      this.form.colonia = "";
      this.disabledCP = false;
      this.form.direccion.calleNumero = "";
      this.colonias = [{ value: null, text: "Seleccione una colonia..." }];
    },
    guardarEmpleado() {
      let empleado = {
        nombre: this.form.nombre,
        nombreCompleto: "",
        apellidoP: this.form.apellidoP,
        apellidoM: this.form.apellidoM,
        rFC: this.form.rFC,
        codigoTipoEmpleado: this.form.codigoTipoEmpleado,
        estatus: this.form.estatus,
        telefono: this.form.telefono,
        celular: this.form.celular,
        correo: this.form.correo,
        direccion: this.form.direccion,
        codigo: this.id,
        comisiones: this.comisiones
      };
      // console.log("El empleado-> ",this.form)
      // console.log('Empleado', empleado);
      console.log(empleado);
      servicioEmpleados.guardarEmpleado(empleado).then((r) => {
        if (r.value) {
          this.$swal.fire({
            text: r.message,
            icon: "success",
          });
          this.limpiar();
          window.history.back();
        } else {
          this.$swal.fire({
            text: r.message,
            icon: "success",
          });
        }
      });
    },
    consultaTipoArea() {
      servicioEmpleados.tipos().then((r) => {
        // console.log(r);
        if (r.value) {
          r.data.forEach((element) => {
            let tipo = { value: element.codigo, text: element.descripcion };
            this.optionsTiposEmpleados.push(tipo);
          });
        }
      });
    },
    consultaSubTipo() {
      this.optionsSubTiposEmpleados = [{ value: null, text: "Seleccione un subtipo..." }];
      this.codSubTipoEmpleado = null;
      servicioEmpleados.subtipo(this.form.codigoTipoEmpleado).then((r) => {
        if (r.value) {
          this.disabledSubtipos = r.data.length == 0;
          r.data.forEach((element) => {
            let subTipo = {
              value: element.codSubTipoEmpleado,
              text: element.descripcion,
            };
            this.optionsSubTiposEmpleados.push(subTipo);
          });
        }
      });
    },
    obtenerDatosCP() {
      this.$loading(true);
      servicioGenerales.obtenerDatosCP(this.form.direccion.cP).then((r) => {
        if (r.value) {
          r.data.forEach((x) => {
            this.colonias.push({ value: x.codigoColonia, text: x.colonia });
            this.$loading(false);
          });
          this.form.direccion.codEstado = r.data[0].codigoEstado;
          this.form.direccion.codMunicipio = r.data[0].codigoMunicipio;
          this.form.direccion.municipio = r.data[0].municipio;
          this.disabledCP = true;
        } else {
          this.$loading(false);
          this.$swal.fire({
            text: r.message,
            icon: "warning",
          });
        }
      });
    },

    cargaEmpleado() {
      if (this.id > 0) {
        this.$loading(true);
        servicioEmpleados.obtenerEmpleado(this.id).then((r) => {
          if (r.value) {
            // console.log('DATA obtenerEmpleado', r.data);
            this.form.nombre = r.data.empleado[0].nombre;
            this.form.apellidoP = r.data.empleado[0].apellidoP;
            this.form.apellidoM = r.data.empleado[0].apellidoM;
            this.form.rFC = r.data.empleado[0].rFC;
            this.form.estatus = r.data.empleado[0].estatus;
            // this.form.tipoEmpleado = r.data.empleado[0].codigotipoEmpleado;
            // this.form.alias = r.data.empleado[0].alias;
            this.form.telefono = r.data.empleado[0].telefono;
            this.form.celular = r.data.empleado[0].celular;
            this.form.correo = r.data.empleado[0].correo;
            this.form.credito = r.data.empleado[0].creditoFormat;
            this.form.codigo = r.data.empleado[0].codigo;
            this.form.tipo = r.data.empleado[0].tipo;
            this.form.codigoTipoEmpleado = r.data.empleado[0].codigoTipoEmpleado;

            // this.form.codVendedor = r.data.empleado[0].codEmpleadoVendedor;
            // this.form.razonSocial = r.data.empleado[0].razonSocial;
            // this.itemsDirecciones = r.data.direccionesXempleado;
            this.$loading(false);
            console.log("EMPLEADO CARGADO->", this.form);
          } else {
            this.$loading(false);
            this.$swal.fire({
              text: `${r.message}`,
              icon: "error",
            });
            setTimeout(() => {
              this.$router.push({ path: "." });
            }, 1000);
          }
        });
      }
    },
    cancelar() {
      let tipoActividad =
        this.id == 0
          ? "¬øDesea cancelar el registro de un nuevo empleado?"
          : "¬øDesea cancelar la actualizaci√≥n del empleado?";
      console.log(tipoActividad);
      this.$swal
        .fire({
          title: tipoActividad,
          text: "",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, volver al cat√°logo",
          cancelButtonText: "Cancelar",
        })
        .then((result) => {
          if (result.value) {
            window.history.back();
          }
        });
    },
    async comisionesExistentes() {
      let r = await servicioComisiones.comisiones();
      console.log(r);
      this.comisiones = r.data;
    },
  },
};
</script>
<style scoped></style>
