import '@babel/polyfill'
import 'mutationobserver-shim'
import Vue from 'vue'
import './plugins/bootstrap-vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/assets/site.css';
import * as VueGoogleMaps from "vue2-google-maps";
import VueSidebarMenu from 'vue-sidebar-menu';
import 'vue-sidebar-menu/dist/vue-sidebar-menu.css';
import VueFeedbackReaction from 'vue-feedback-reaction';
import * as GoogleDistanceMatrix from 'google-distance-matrix';
// import * as TurfDistance from '@turf/distance';


const keyGoogle = "AIzaSyAbG_eyVgmdfq7xuPTvidPbj36Vf-Tfjnk";
const keyGoogleDistance ="AIzaSyDN3U-3Wii-C4MU_Poy7MncZgUdt7N2cU4";
Vue.use(VueFeedbackReaction)
Vue.use(VueSidebarMenu)
Vue.use(VueGoogleMaps, {
  load: {
      // key: "AIzaSyAmL0upEnvS1IFkgHfvkKumBNXk00K-lf4",
      key: keyGoogle,
      libraries: 'places,drawing,visualization'
  }
});

Vue.use(GoogleDistanceMatrix, {
  load:{
    key:keyGoogleDistance
  }
});

// Vue.use(TurfDistance)

//css
import '@fortawesome/fontawesome-free/css/all.css'

//plugins

import Swal from '@/plugins/swal'
import Msg from '@/plugins/msg'
import FloatTheadPlugin from '@/plugins/floatThead'

import Vuelidate from 'vuelidate'
import 'bootstrap/dist/css/bootstrap.css';
import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css';
import VueToast from 'vue-toast-notification'
import 'vue-toast-notification/dist/theme-default.css';
import VuePdfApp from "vue-pdf-app";

Vue.use(Vuelidate)
Vue.use(Swal)
Vue.use(Msg)
Vue.use(VueToast)
Vue.use(FloatTheadPlugin)
Vue.component("vue-pdf-app", VuePdfApp);

//mixins
import Globales from '@/mixins/globales'
import Notificacion from '@/mixins/notificacion'

Vue.mixin(Globales)
Vue.mixin(Notificacion)

//directivas
import VFocus from '@/directives/focus'
import VSelectOnFocus from '@/directives/selectOnFocus'
import VNextControl from '@/directives/nextControl'
import VFloatThead from '@/directives/floatThead'

Vue.use(VFocus)
Vue.use(VSelectOnFocus)
Vue.use(VNextControl)
Vue.use(VFloatThead)

//filtros
import formatDate from '@/filters/format-date'
import formatNumber from '@/filters/format-number'

Vue.use(formatDate)
Vue.use(formatNumber)

const $ = require('jquery')
window.$ = $
window.jQuery = $

Vue.config.productionTip = false

Vue.prototype.$loading = function(isLoading){
  this.$store.commit('setLoading', isLoading)
}

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')


