import swal from 'sweetalert2'

const msg = {}

msg.install = (Vue) => {
    Vue.prototype.$msg = {
        async question(mensaje) {
            let r = await swal.fire({
                text: mensaje,
                icon: 'question',
                showCancelButton: true,
                confirmButtonText: 'S√≠',
                cancelButtonText: 'No'        
              })

              return r
        },
        async show(message, value, falseValueType="warning"){
            await swal.fire({
                text: message,
                icon: value ? 'success' : falseValueType
            })
        }
    }
}

export default msg