<template>
    <div class="container py-5">
        <h3><i class="fas fa-hard-hat"></i> Registro de Tama√±os M√°ximos de Agregados</h3>
        <div class="py-5">
            <b-form @submit="onSubmit">
                <b-row>                    
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-2"
                        label="Descripci√≥n corta *"
                        label-for="txtDescripcionCorta">
                            <b-form-input
                            id="txtDescripcionCorta"          
                            v-model="form.descripcionCorta"
                            type="text"
                            required
                            placeholder="Descripci√≥n corta" 
                            class="text-capitalize"
                            >
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-6 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-3"
                        label="Descripci√≥n *"
                        label-for="txtDescripcion">
                            <b-form-input
                            id="txtDescripcion"          
                            type="text"
                            v-model="form.descripcion"
                            required
                            placeholder="Descripci√≥n"
                            class="text-capitalize">
                            </b-form-input>
                        </b-form-group>
                    </b-col>                    
                    <b-col class="col-md-2 col-sm-12 col-12">
                        <b-form-group label="Estatus:">
                            <b-form-checkbox
                            id="chckEstatus"
                            v-model="form.estatus"
                            name="chckEstatus"                        
                            switch
                            >
                            {{form.estatus ? 'Activo':'Inactivo'}}
                            </b-form-checkbox>
                        </b-form-group>
                    </b-col>
                </b-row>                
                <b-row>
                    <b-col class="col-md-6 col-sm-12 col-12" align-self="end">                        
                        <b-button type="submit" block variant="success" class="mr-2 mt-2"><i class="fa fa-save"></i> Guardar</b-button>
                    </b-col>
                    <b-col class="col-md-6 col-sm-12 col-12" align-self="end">
                        <b-button @click="limpiar" type="reset" block variant="outline-danger" class="mt-2"><i class="fas fa-broom"></i>Limpiar</b-button>                        
                    </b-col>
                </b-row>
            </b-form>                
        </div>
    </div>
</template>
<script>
// import servicio from '@/services/servicio-clientes'
// import servicioAgente from '@/services/servicio-agentes'
import servicioMateriales from '@/services/servicio-materiales'
export default{
    data(){
        return{
            // selectedRazonSocial:null,            
            form:{                
                descripcionCorta:'',
                descripcion:'' ,
                estatus: false              
            },     
            resistencias:[{value:null,text:'Seleccione una resistencia...'}],
        }
    },
    mounted(){        
        this.resistencia = null;
        // this.verificaTokenValido();
        this.cosnsultarResistencias();
    },
    computed:{
        validationNombres(){
            let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
            return this.nombre.match(letras) && this.nombre.length > 1 ? true : false;
        },
        validationApellidoP(){
            let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
            return this.apellidoP.match(letras) && this.apellidoP.length > 1 ? true : false;
        }        
    },
    methods:{
        onSubmit(evt) {
            evt.preventDefault();
            this.guardarTMA();
        },
        limpiar(){                
                this.form.descripcionCorta = "";
                this.form.descripcion = "";                
            },
        guardarTMA(){   
            let tma = {
                "codigo":0,                
                "descripcionCorta":this.form.descripcionCorta,
                "descripcion":this.form.descripcion,                
                "estatus":this.form.estatus
            }                        
            servicioMateriales.guardarTMA(tma)
            .then(r=>{                                
                if(r.value){
                    this.$swal.fire({
                        text: `El TMA se ha guardado exitosamente!`,
                        icon: "success"
                    });
                    this.limpiar();
                }else{
                    this.$swal.fire({
                        text: `No se pudo guardar el TMA, contacte a sistemas`,
                        icon: "error"
                    });
                }
            })
        },
        cosnsultarResistencias(){
            servicioMateriales.resistencias()
            .then(r=> {                
                if(r.value){
                    r.data.forEach(element => {
                        console.log(element)
                        let resistencia = {value: element.codigo, text:element.descripcionCorta}
                        this.resistencias.push(resistencia);
                    });
                }
            })
        }
    }
}
</script>
