<template>
<div>
    <div class="container">
        
        <div class="">
            <h3><i class="fa fa-chart-line"></i> Reporte Mensual de Productos</h3>
        </div>
        <hr>

        <!-- Rango de fechas -->
        <b-row>
            <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                <label>Desde:</label>
                <b-form-datepicker v-model="desde" class="mb-2" locale="es"></b-form-datepicker>
            </div>
            <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                <label>Hasta:</label>
                <b-form-datepicker v-model="hasta" class="mb-2" locale="es"
                    ></b-form-datepicker>
            </div>
        </b-row>

        <!-- Agente -->
		<div class="row">
			<div class="form-group col-sm-12 col-md-4 mb-2">
				<label>Agente:</label>
				<div class="input-group">
                    <b-form-select id="cboVendedor"
									v-model="reporte.codVendedor"
									:options="vendedores">
					</b-form-select>
                </div>
            </div>
        </div>

        <!-- Producto / Formula -->
        <div class="row">
            <div class="form-group col-sm-12 mb-1">
                <label>F√≥rmula</label>
                <div class="row">
                    <div class="input-group col-xl-10">
                    <input
                        ref="refFormula"
                        type="text"
                        class="form-control"
                        autocomplete="off"
                        v-model="state"
                        @input="filterState"
                        @focus="focusInputFormula"
                    />
                    <span class="input-group-btn">
                        <b-button variant="outline-primary"
                                    class="input-group-4"
                                    @click="limpiarProducto">
                                    <i class="fa fa-times" aria-hidden="true"></i>
                        </b-button>
                    </span>
                    </div>
                   
                </div>
                <div v-if="formulasFiltradas && modal">
                    <ul class="list-group items-body">
                    <li class="list-group-item py-2 items-body-content"
                        v-for="formula in formulasFiltradas"
                        v-bind:key="formula.value"
                        @click="setState(formula)">
                        <span> {{ formula.text }}</span>
                        <i class="fa fa-angle-right"></i>
                    </li>
                    </ul>
                </div>
                <transition name="fade">
                    <label v-show="mostrarProducto"><span class="labelSize">Nomenclatura: </span>{{ reporte.producto }}</label>
                </transition>
            </div>
        </div>

        <!-- botones -->
        <b-row>
            <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                <b-button @click="BuscarDatosReporteMensual" block variant="primary" class="mr-2 mt-2">
                    <i class="fas fa-search"></i> Buscar Datos
                </b-button>
            </b-col>
            
            <b-col class="col-md-3 col-sm-12 col-12" align-self="end" v-if="datos">
                <b-button @click="GenerarReporteMensual" block variant="dark" class="mr-2 mt-2">
                    <i class="fas fa-chart-bar"></i> Generar Reporte
                </b-button>
            </b-col>

            <b-col class="col-md-3 col-sm-12 col-12" align-self="end" v-if="datos">
                <b-button @click="exportarExcel" block variant="success" class="mr-2 mt-2">
                    <i class="fas fa-file-excel"></i> Exportar Excel
                </b-button>
            </b-col>
        </b-row>

        <div v-if="datos">
            <hr>
            <b-row class="my-2">
                <b-col md="4">
                    <b-form-group label="Filtrar"
                                label-cols-sm="1"
                                label-cols-md="2"
                                label-size="sm"
                                label-for="filterInput">
                        <b-input-group size="sm">
                            <b-form-input v-model="filter"
                                            type="search"
                                            id="filterInput"
                                            placeholder="Escribe el filtro..."
                                            autocomplete="off">
                            </b-form-input>
                            <b-input-group-append>
                                <b-button :disabled="!filter" @click="filter = ''">
                                    <i class="fas fa-times"></i>
                                </b-button>
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>
                </b-col>
                <b-col md="3">
                    <b-form-group label="Por p√°gina"
                                    label-cols-sm="2"
                                    label-cols-md="2"
                                    label-cols-lg="4"
                                    label-size="sm"
                                    label-for="perPageSelect">
                        <b-form-select v-model="perPage"
                                        id="perPageSelect"
                                        size="sm"
                                        :options="pageOptions">
                        </b-form-select>
                    </b-form-group>
                </b-col>
            </b-row>
            <!-- Main table element -->
            <b-table small
                    stacked="md"
                    head-variant="dark"
                    :hover="hover"
                    :striped="striped"
                    :bordered="bordered"
                    :items="items"
                    :fields="fields"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :filterIncludedFields="filterOn"
                    :sort-by.sync="sortBy"
                    :sort-desc.sync="sortDesc"
                    :sort-direction="sortDirection"
                    @filtered="onFiltered">
                <template v-slot:cell(estatus)="row">
                    <b-badge variant="danger" v-if="row.item.estatusNuevo == 'D'" class="p-2">Detenido Limite Credito</b-badge>
                    <b-badge variant="danger" v-if="row.item.estatusNuevo == 'DP'" class="p-2">Detenido Falta Pago</b-badge>
                </template>

                <template v-slot:cell(grafica)="row">
                    <b-col align-self="end" v-if="datos">
                        <b-badge @click="verGraficaDemanda(row)" block variant="warning" class="mr-2 mt-2">Ver Gr√°fica</b-badge>
                    </b-col>
                </template>
                
            </b-table>
            
            <b-pagination v-model="currentPage"
                            class="mt-1"
                            :total-rows="totalRows"
                            :per-page="perPage"
                            align="center"
                            aria-controls="my-table">
            </b-pagination>
        </div>

        <!-- componentes -->
        <div>
            <pm-grafica-demanda :datasets="datasets"
                                :mostrar="mostrarGraficaDemanda"
                                :descripcion="itemsArticulos.length == 0 ? [] : itemsArticulos[indexArticulos].descripcion"
                                :itemsCedis="itemsCedis"
                                :cedisMostrarGrafica="cedisMostrarGrafica"
                                @consultarGraficaCedis="consultarGraficaCedis"
                                @cerrar="cerrarGrafica">

            </pm-grafica-demanda>

        </div>

    </div>
</div>
</template>

<script>

import servicioReportes from '@/services/servicio-reportes';
import servicioAgente from "@/services/servicio-agentes";

import servicioDosificador from "@/services/servicio-dosificador.js";
import PmGraficaDemanda from "@/components/MostrarGraficasDemanda"

export default {
    data() {

        const now = new Date()
        const last = new Date(now.getFullYear(), now.getMonth()-1, now.getDate())
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())

        var lastDate = last.toISOString().slice(0,10);
        var todayDate = today.toISOString().slice(0,10);

        return {
            desde: lastDate,
            hasta: todayDate,
            agente: 0,

            reporte: {
                codVendedor: 0,
                codProducto: 0,
                producto: '',
            },

            vendedores: [{ value: 0, text: "Todos" }],
            obras: [{ value: 0, text: "Todas" }],

            disabledFormula: true,
            nuevaFormula: false,
            mostrarProducto: false,
            state: "",
            formulasFiltradas: [],
            formulas: [],

            // grafica
            itemsArticulos: [], //{ detalleArticulo: [] }
            mostrarGraficaDemanda: false,
            datasets:[],
            itemsCedis:[],
            chartdata: null,
            cedisMostrarGrafica:0,
            
            modal: false,
            datos: false,
            items: [],
            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],
            fields: [
                {
                    key: "codProducto",
                    label: "Codigo",
                    sortable: true,
                    sortDirection: "desc",
                },
                {
                    key: "nomenclatura",
                    label: "Nomenclatura",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "descripcion",
                    label: "Descripcion",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "grafica",
                    sortable: true,
                    class: "text-center",
                },
            ],

            datosExportar: [],



        }

    },
    components: {
        PmGraficaDemanda,
    },
    watch: {
		state: function (val) {
			val.length >= 1 && this.reporte.codProducto == 0
				? (this.modal = true)
				: (this.modal = false);

			if (val.length === 0) {
				this.reporte.producto = 0;
				this.reporte.codProducto = 0;
				this.mostrarProducto = false;
			}
		},
	},
    mounted() {
        this.datosExportar = [];
        
    },
    async created() {
        await this.obtenerVendedores();
        await this.cargarFormulas(0);
    },
    computed: {
        sortOptions() {
            // Create an options list from our fields
            return this.fields
            .filter((f) => f.sortable)
            .map((f) => {
            return { text: f.label, value: f.key };
            });
        },
    },
    methods: {

        //agentes
        async obtenerVendedores() {
            this.$loading(true);
            await servicioAgente.agentesCombo().then((r) => {
                if (r.value) {
                    r.data.forEach((element) => {
                        let agente = {
                            value: element.codVendedor,
                            text: element.nombreVendedor,
                        };
                        this.vendedores.push(agente);
                    });

                    this.reporte.codVendedor = this.vendedores[0].value;
                }
            });
            this.$loading(false);
        },

        //formulas
        async cargarFormulas(codigo) {
            this.$loading(true);
            let r = await this.obtenerFormulas(codigo);
            if (r.value) {
                r.data.forEach((element) => {
                let tipo = {
                    value: element.codigo,
                    text: element.descripcion,
                    producto: element.nomenclatura,
                };
                this.formulas.push(tipo);
                });

            }
            this.$loading(false);
        },

        async obtenerFormulas(codigo) {
            return await servicioDosificador.obtenerFormulas(codigo);
        },

        filterState() {
            this.formulasFiltradas = this.formulas.filter((item) => {
                const descripcion = item.text.toString().toUpperCase();
                const nomenclatura = item.producto.toString().toUpperCase();
                const searchTerm = this.state.trim().toUpperCase();

                return descripcion.includes(searchTerm) || nomenclatura.includes(searchTerm)
            });
        },

        focusInputFormula() {
            if (this.state.length > 0) {
                this.modal = true;
            }
        },

        setState(formula) {
            this.reporte.codProducto = formula.value;
            this.state = formula.text;
            this.reporte.producto = formula.producto;
            this.modal = false;
            this.mostrarProducto = true;
            this.disabledFormula = true;
        },

        limpiarProducto() {
            this.reporte.producto = 0;
            this.reporte.codProducto = 0;
            this.mostrarProducto = false;
            this.disabledFormula = false;
            this.state = "";
            this.$nextTick(() => {
                this.$refs.refFormula.focus();
            });
        },

        async BuscarDatosReporteMensual(){
            this.items = [];
            this.$loading(true);
            let result = await servicioReportes.ObtenerReporteMensualProductos(this.desde, this.hasta, this.reporte);
            if(result.value) {
                this.datos = true;
                this.items = result.data;
                console.log(this.items);
                this.CargarRenglones();
                this.$loading(false);
            } else {
                this.$loading(false);
                this.$swal.fire({
                    text: result.message,
                    icon: "warning"
                });
            }
        },

        async GenerarReporteMensual(){
            this.$loading(true);
            // console.log('Aqui se imprime el rpt');
            let result = await servicioReportes.GenerarReporteMensualProductos(this.desde, this.hasta, this.items);
            if(result.value) {
                await this.ImprimirReporteMensualProductos(result.data);
                this.$loading(false);
                const Toast = this.$swal.mixin({
                    toast: true,
                    position: 'top-end',
                    showConfirmButton: false,
                    timer: 3000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                        toast.addEventListener('mouseenter', this.$swal.stopTimer)
                        toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                    }
                })

                Toast.fire({
                    icon: 'success',
                    title: 'Reporte mensual de clientes descargado.'
                });
            } else {
                // console.log(result);
                this.$loading(false);
                this.$swal.fire({
                    text: result.message,
                    icon: "warning"
                });
            }
        },
        
        // async GenerarReporteMensual(){
        //     this.$loading(true);
        //     let result = await servicioReportes.ObtenerReporteMensualProductos(this.desde, this.hasta, this.reporte);
        //     if(result.value) {
        //         await this.ImprimirReporteMensualProductos(result.data);
        //         this.$loading(false);
        //         const Toast = this.$swal.mixin({
        //             toast: true,
        //             position: 'top-end',
        //             showConfirmButton: false,
        //             timer: 3000,
        //             timerProgressBar: true,
        //             didOpen: (toast) => {
        //                 toast.addEventListener('mouseenter', this.$swal.stopTimer)
        //                 toast.addEventListener('mouseleave', this.$swal.resumeTimer)
        //             }
        //         })

        //         Toast.fire({
        //             icon: 'success',
        //             title: 'Reporte mensual de productos descargado.'
        //         });
        //     } else {
        //         this.$loading(false);
        //         this.$swal.fire({
        //             text: result.message,
        //             icon: "warning"
        //         });
        //     }

        // },
        
        async ImprimirReporteMensualProductos(pdfResult) {
            const downloadLink = document.createElement("a");
            const fileName = "Reporte Mensual de Productos";
            downloadLink.href = "data:application/pdf;base64," + pdfResult;
            downloadLink.download = fileName;
            await downloadLink.click();
        },

        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },

        CargarRenglones() {
            setTimeout(() => {
            this.totalRows = this.items.length;
            }, 1000);
        },

        async verGraficaDemanda(cedis) {

            console.log('CEDIS -> ', cedis);
            this.datasets=[]
            this.mostrarGraficaDemanda = true
            // this.consultarSucursales()
            await this.ObtenerDemandaArticulo(cedis)
            let datosSurtido = []
            let datosNegado = []
            let datosDemanda = []
            let anoAnt = 0
            let ano = 0
            for(let i = 0; i< this.chartdata.length; i++){
                anoAnt = this.chartdata[i].ano
                let anoGuardar = ano
                if(ano != 0 && ano != anoAnt || i == this.chartdata.length - 1){
                    ano = this.chartdata[i].ano
                    
                    if(i == this.chartdata.length - 1){
                        datosSurtido.push(this.chartdata[i].surtido)
                        datosNegado.push(this.chartdata[i].negado)
                        datosDemanda.push(this.chartdata[i].demanda)
                    }

                    this.datasets.push( 
                        {
                            label: "Surtido " + anoGuardar,
                            data: datosSurtido,
                            backgroundColor: "transparent",
                            borderColor: this.getRandomRgb(),
                            pointBackgroundColor: this.getRandomRgb()
                        },
                        // {
                        //     label: "Negado " + anoGuardar,
                        //     data: datosNegado,
                        //     backgroundColor: "transparent",
                        //     borderColor: this.getRandomRgb(),
                        //     pointBackgroundColor: this.getRandomRgb()
                        // },
                        // {
                        //     label: "Demanda " + anoGuardar,
                        //     data: datosDemanda,
                        //     backgroundColor: "transparent",
                        //     borderColor: this.getRandomRgb(),
                        //     pointBackgroundColor: this.getRandomRgb()
                        // }
                        )
                    //inicio ++
                    datosSurtido=[]
                    datosNegado = []
                    datosSurtido.push(this.chartdata[i].surtido)
                    datosNegado.push(this.chartdata[i].negado)
                    datosDemanda.push(this.chartdata[i].demanda)
                }
                else{
                    //console.log(ano, anoAnt)
                    if(ano == 0){
                        ano = this.chartdata[i].ano
                    }

                    datosSurtido.push(this.chartdata[i].surtido)
                    datosNegado.push(this.chartdata[i].negado)
                    datosDemanda.push(this.chartdata[i].demanda)
                    
                }
            }
        },	

        async ObtenerDemandaArticulo(){
            this.chartdata = []
            await servicioReportes.ObtenerDemandaArticulo()
                .then((r)=>{
                    console.log('ObtenerDemandaArticulo -> ', r.data)
                    this.chartdata = r.data	
                })
                .catch((err)=>{
                    console.log('ObtenerDemandaArticulo', err)
                })
        },	

        //Modales
        cerrarGrafica(){
            this.mostrarGraficaDemanda = false
        },


        consultarGraficaCedis(item){
            this.cedisMostrarGrafica = item
            console.log('MOSTAR ->', this.cedisMostrarGrafica);
            this.verGraficaDemanda(item)
        },

        getRandomRgb() {
            var num = Math.round(0xffffff * Math.random());
            var r = num >> 16;
            var g = num >> 8 & 255;
            var b = num & 255;
            return 'rgb(' + r + ', ' + g + ', ' + b + ')';
        },

        exportarExcel() {
            this.$loading(true);
            let csv = Object.keys(this.items[0]).join("‚ô™") + "\n"; //headers
                            
            for (var item in this.items) {
                csv += Object.values(this.items[item]).join("‚ô™");
                csv += "\n";
            }
            console.log('CSV #1 ', csv);
            
            csv = csv.replaceAll(',', ' ')
            csv = csv.replaceAll('‚ô™', ',')
            
            console.log('CSV #2 ', csv);

            const blob = new Blob([csv], { type: 'text/csv' });
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.setAttribute('hidden', ''); 
            a.setAttribute('href', url);
            a.setAttribute('download', 'download.csv');
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            this.$loading(false);
        }

    }
   
}
</script>

<style scoped>

    .labelSize {
        font-size: 20px;
        font-weight: bold;
    }

</style>
