<template>

<div class="container mt-3">
    <div class="mt-3">
        <h3><i class="fas fa-user-tie"></i> Vendedores</h3>
    </div>

    <div>
        <table-sortable class="mt-3"
            :items="items"
            :fields="fields"
            :rutaNuevo="rutaNuevo"
            :permisoEscritura="permisoEscritura"
        ></table-sortable>
    </div>
</div>

</template>

<script>

import TableSortable from "@/components/TableSortable";
import servicioVendedores from '@/services/servicio-vendedores';

export default {
    data(){
        return{
            items: [],
            fields: [
                { key: "nombreCompleto", label: "Nombre", sortable: true, sortDirection: "desc", },
                { key: "tipoEmpleado", label: "Tipo", sortable: true, sortDirection: "desc", },
                { key: "estatus", label: "Estatus", sortable: true, sortDirection: "desc", },
                { key: "actions", label: "", class: "text-center" },
            ],
            permisoEscritura: true,
            rutaNuevo: "vendedores/registro/",
        }
    },
    components: {
        TableSortable,
    },
    mounted() {
        this.ObtenerVendedores();
    },
    created() {
    },
    computed: {
    },
    methods: {
        
        async ObtenerVendedores() {
            this.$loading(true);
            await servicioVendedores
                    .obtenerVendedores(0)
                    .then((r) => {
                        this.items = r.data;
                        this.$loading(false);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
        },

        
    }
}
</script>

