import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                    O P E R A D O R E S
//............................................................
servicio.obtenerOperadores = (codOperador) => {
    return base.get(`/operadores/todos/${codOperador}`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}


servicio.obtenerOperadoresEntradasSalidas = (entrada) => {
    return base.get(`/operadores/entradasSalidas/${entrada}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.obtenerTiposOperadores = () => {
    return base.get(`/operadores/tipos`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.guardarOperador = (operador) => {
    return base.post(`/operadores/guardar`, operador, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

export default servicio