<template>
<div>
	<div class="container mt-3">
		<div>
			<h3 v-if="parametro == 0"><i class="fas fa-users" ></i> Registrar Operador</h3>
			<h3 v-if="parametro > 0"><i class="fas fa-users" ></i> Actualizar Operador</h3>
			<hr>
		</div>
		<b-form @submit="onSubmit">
			<!-- Nombre y Apellidos -->
			<b-row>
				<b-col class="col-md-4 col-sm-12 col-12">
					<b-form-group id="input-group-1"
									label="Nombre *"
									label-for="txtNombre">
						<b-form-input id="txtNombre"
										type="text"
										required
										placeholder="Nombre(s)"
										v-model="form.nombre"
										class="text-capitalize">
						</b-form-input>
					</b-form-group>
				</b-col>
				<b-col class="col-md-4 col-sm-12 col-12">
					<b-form-group id="input-group-2"
									label="Apellido Paterno *"
									label-for="txtApellidoP">
						<b-form-input id="txtApellidoP"
										v-model="form.apellidoP"
										type="text"
										required
										placeholder="Apellido Paterno"
										class="text-capitalize">
						</b-form-input>
					</b-form-group>
				</b-col>
				<b-col class="col-md-4 col-sm-12 col-12">
					<b-form-group id="input-group-3"
									label="Apellido Materno *"
									label-for="txtApellidoM">
						<b-form-input id="txtApellidoM"
										type="text"
										v-model="form.apellidoM"
										required
										placeholder="Apellido Materno"
										class="text-capitalize">
						</b-form-input>
					</b-form-group>
				</b-col>
			</b-row>
			<!-- RFC, Celular y Correo -->
			<b-row>
				<b-col class="col-md-4 col-sm-12 col-12">
					<b-form-group id="input-group-4" label="RFC" label-for="txtRFC">
						<b-form-input id="txtRFC"
										type="text"
										v-model="form.rFC"
										placeholder="RFC..."
										class="text-uppercase"
										:state="form.rFCValidate.esValido">
						</b-form-input>
						<b-form-invalid-feedback id="input-live-feedback" v-if="form.rFC != ''">
							Usa el formato v√°lido para el RFC
						</b-form-invalid-feedback>
					</b-form-group>
				</b-col>
				<b-col class="col-md-4 col-sm-12 col-12">
					<b-form-group id="input-group-4"
									label="N√∫mero de celular *"
									label-for="txtCelular">
						<b-form-input id="txtCelular"
										type="text"
										v-model="form.celular"
										required
										placeholder="N√∫mero de celular...">
						</b-form-input>
					</b-form-group>
				</b-col>
				<b-col class="col-md-4 col-sm-12 col-12">
					<b-form-group id="input-group-4"
									label="Correo electr√≥nico *"
									label-for="txtCorreoE">
						<b-form-input id="txtCorreoE"
										type="email"
										v-model="form.correo"
										required
										placeholder="Correo electr√≥nico...">
						</b-form-input>
					</b-form-group>
				</b-col>
				
			</b-row>
			<b-row>
				<b-col class="col-md-4 col-sm-12 col-12">
					<b-form-group id="input-group-4"
									label="Tipo de operador *"
									label-for="cboTipoEmpleado">
						<b-form-select v-model="form.codigoTipoEmpleado"
										:options="tiposOperador"
										aria-placeholder="Seleccione un tipo de operador"
										required>
						</b-form-select>
					</b-form-group>
				</b-col>
			</b-row>
			
			<!-- Guardar y Cancelar -->
			<b-row class="mt-3">
				<b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
				<b-col class="col-md-3 col-sm-12 col-12" align-self="end">
					<b-button type="submit"
								block
								variant="success"
								class="mr-2 mt-2"
								>
						<i class="fa fa-save"></i> Guardar
					</b-button>
				</b-col>
				<b-col class="col-md-3 col-sm-12 col-12" align-self="end">
					<b-button @click="cancelar"
								block
								variant="danger"
								class="mr-2 mt-2">
						Cancelar
					</b-button>
				</b-col>
			</b-row>
		</b-form>
	</div>
</div>
</template>

<script>

import servicioOperadores from "@/services/servicio-operadores";
import Swal from "sweetalert2";

export default {

	data() {
		return {
			form: {
				nombre: "",
				apellidoP: "",
				apellidoM: "",
				rFC: "",
				estatus: 1,
				codigo: 0,
				tipoEmpleado: '',
				celular: "",
				correo: "",
				codigoTipoEmpleado: 0,
				rFCValidate: {
					msg: "",
					esValido: null,
				},
			},
			parametro: 0,
			tiposOperador: [{ value: 0, text: "Seleccione un tipo de operador" }],
			

		};

	},

	components: {},

	mounted() {
		this.parametro = this.$route.params.id != undefined ? this.$route.params.id : 0;
		console.log('P->', this.parametro);
		this.obtenerTiposOperadores();

		if(this.parametro > 0) {
			this.CargarOperador();
		}
	},

	created() {
	},

	watch: {
		"form.rFC": function (newValue) {
			console.log(this.form.rFC);
			if(this.form.rFC != '') {
				this.form.rFC = this.form.rFC.toUpperCase();
				if (
					/^([A-Z,√ë,&]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1])[A-Z|\d]{3})$/.test(
					newValue
					)
				) {
					this.form.rFCValidate.esValido = true;
					this.form.rFCValidate.msg = "El RFC es v√°lido";
				} else {
					this.form.rFCValidate.esValido = false;
					this.form.rFCValidate.msg = "El RFC NO es v√°lido";
				}
			} else {
				this.form.rFCValidate.esValido = null;
				this.form.rFCValidate.msg = "";
			}
			
		},
	},

	computed: {},
	methods: {
		
		async obtenerTiposOperadores() {
			await servicioOperadores.obtenerTiposOperadores()
				.then((r) => {
					if (r.value) {
						console.log(r);
						r.data.forEach((element) => {
							let tipo = { value: element.codigo, text: element.descripcion };
							this.tiposOperador.push(tipo);
						});
					}
			});
		},

		onSubmit(evt) {
			evt.preventDefault();
			this.guardarOperador();
		},

		async guardarOperador() {

			let r = await Swal.fire({
                title: "¬°ATENCI√ìN!",
                text: "¬øDesea guardar el Operador?",
                showCancelButton: true,
                icon: 'question',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Aceptar",
                cancelButtonText: "Cancelar",
            });

            if (r.value) {
				
				this.$loading(true);
				await servicioOperadores
						.guardarOperador(this.form)
						.then(r=>{
							if(r.value) {
								Swal.fire(
									'¬°EXITO!',
									'Se guardo correctamente el Operador',
									'success'
								);
								window.history.back();

							}
							else {
								Swal.fire('¬°ATENCI√ìN!', r.message, 'error' );
							}
							this.$loading(false);
						}).catch(error=>{
							console.log(error); 
							this.$loading(false);
						});
			}

        },

		async cancelar() {
			let tipoActividad = this.parametro == 0 ? "¬øDesea cancelar el registro de un nuevo cliente?" : "¬øDesea cancelar la actualizaci√≥n del cliente?";
			console.log(tipoActividad);

			let r = await Swal.fire({
                title: "¬°ATENCI√ìN!",
                text: tipoActividad,
                showCancelButton: true,
                icon: 'question',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Aceptar",
                cancelButtonText: "Cancelar",
			});

			if (r.value) {
				window.history.back();
            }
			
			
		},

		async CargarOperador() {


			this.$loading(true);
				await servicioOperadores
					.obtenerOperadores(this.parametro)
					.then(r=>{
						if(r.value) {
							this.form.codigo = r.data[0].codigo;
							this.form.nombre = r.data[0].nombre;
							this.form.apellidoP = r.data[0].apellidoP;
							this.form.apellidoM = r.data[0].apellidoM;
							this.form.rFC = r.data[0].rFC;
							this.form.celular = r.data[0].celular;
							this.form.correo = r.data[0].correo;
							this.form.codigoTipoEmpleado = r.data[0].codigoTipoEmpleado;
						}
						this.$loading(false);
					}).catch(error=>{
						console.log(error); 
						this.$loading(false);
					});

		}

		
	},
};
</script>

<style scoped>

	.hasError {
		border-color: red;
	}

	.labelSize {
		font-size: 20px;
		font-weight: bold;
	}

	.fade-enter-active,
	.fade-leave-active {
		transition: opacity 0.5s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
		opacity: 0;
	}

	.bounce-enter-active {
		animation: bounce-in 0.4s;
	}

	.bounce-leave-active {
		animation: bounce-in 0.5s reverse;
	}

	@keyframes bounce-in {
		0% {
			transform: scale(0);
		}
		100% {
			transform: scale(1);
		}
	}

</style>