import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                     V E N D E D O R E S
//............................................................
servicio.obtenerVendedores = (codVendedor) => {
    return base.get(`/vendedores/todos/${codVendedor}`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.guardarVendedor = (vendedor) => {
    return base.post(`/Vendedores/guardar`, vendedor, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

export default servicio