<template>
  <div>
    <div class="row">
      <div class="col-12">
        <input
          @keyup.enter="buscar"
          class="form-control form-control-sm"
          ref="texto-buscar"
          type="text"
          :placeholder="placeholder"
          v-model="txtBuscar"
          v-on:focus="focustxt"
          @blur="handleBlur"
        />
      </div>
    </div>
    <div class="row my-2">
      <div class="col-12 text-right">
        <button class="btn btn-sm btn-primary" @click="buscar">Aceptar</button>
      </div>
    </div>

    <div
      :class="{ 'overlay-collapsed': !mostrar, 'overlay-show': mostrar }"
      class="overlay"
    >
      <div class="row">
        <div
          class="col-xl-6 col-lg-6 col-md-8 offset-xl-3 offset-lg-3 offset-md-2"
        >
          <div class="card m-lg-5 m-2 p-3 bg-white">
            <div class="mt">
              <div class="form-inline">
                <label class="text-left justify-content-lg-start"
                  >Buscar :</label
                >

                <input
                  v-show="mostrar"
                  @keyup.enter="seleccionarItem()"
                  @keyup.esc="cancelar"
                  @keyup.up="nextItem"
                  @keyup.down="nextItem"
                  class="form-control form-control-sm ml-1"
                  placeholder="Buscar ..."
                  ref="modal-texto-buscar"
                  type="text"
                  v-model="txtBuscar"
                />

                <button
                  type="button"
                  class="btn btn-primary ml-1"
                  @click="buscar"
                >
                  Buscar
                </button>
              </div>

              <div>
                <table class="col table-sm mt-3 table-hover" id="tablaClientes">
                  <thead>
                    <tr class="table-primary">
                      <td
                        :key="colIndex"
                        v-for="(columna, colIndex) in columnas"
                      >
                        {{ columna.header }}
                      </td>
                    </tr>
                  </thead>
                  <tbody ref="tablaClientesBody">
                    <tr
                      :key="index"
                      :id="'row' + index"
                      :class="{ tableSelected: currentItem === index }"
                      @click="seleccionarItemClick(index)"
                      class="cursor-pointer borderBottom"
                      v-for="(item, index) in items"
                    >
                      <slot
                        name="row"
                        v-bind:item="item"
                        :class="{ active: currentItem === index }"
                      >
                      </slot>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    "columnas",
    "items",
    "limpiarTexto",
    "focoTexto",
    "buscarBlur",
    "placeholder",
  ],
  data() {
    return {
      //textoBuscar:'',
      mostrar: false,
      txtBuscar: "", //this.textoBuscar,
      itemSeleccionado: null,
      currentItem: 0,
      scrolVertical: 0,
    };
  },
  mounted() {
    window.addEventListener("keydown", (e) => {
      // 	//@keyup.enter="seleccionarItem()"
      if (e.key == "Escape") {
        this.cancelar();
      }
      // 	else if (e.key == 'Enter') {
      // 		this.seleccionarItem()
      // 	}
      // 	else if (e.keyCode === 38 && this.currentItem >= 1) {
      // 		this.nextItem()
      // 	}
      // 	else if (e.keyCode === 40 && this.currentItem < this.items.length - 1) {
      // 		this.nextItem()
      // 	}
    });
  },
  methods: {
    nextItem() {
      var containerBody = this.$refs.tablaClientesBody;
      if (event.keyCode === 38 && this.currentItem >= 1) {
        this.currentItem--;

        this.scrolVertical = this.scrolVertical - 33;
        containerBody.scrollTop = this.scrolVertical;
      } else if (
        event.keyCode === 40 &&
        this.currentItem < this.items.length - 1
      ) {
        this.currentItem++;

        this.scrolVertical = this.scrolVertical + 33;
        containerBody.scrollTop = this.scrolVertical;
      }
    },
    buscar() {
      if (this.txtBuscar != "") {
        this.mostrar = true;
        this.currentItem = 0;
        this.scrolVertical = 0;

        this.$emit("buscar", this.txtBuscar);

        this.$nextTick(() => {
          this.$refs["modal-texto-buscar"].focus();
          var containerBody = this.$refs.tablaClientesBody;
          containerBody.scrollTop = 0;
        });
      }
    },
    seleccionarItemClick(index) {
      this.mostrar = false;
      this.$emit("seleccionar", this.items[index]);

      if (this.limpiarTexto) {
        //this.textoBuscar = ""
        this.txtBuscar = "";
      }

      if (this.focoTexto) {
        this.$refs["texto-buscar"].focus();
      }
    },
    seleccionarItem() {
      this.mostrar = false;
      this.$emit("seleccionar", this.items[this.currentItem]);

      if (this.limpiarTexto) {
        //this.textoBuscar = ""
        this.txtBuscar = "";
      }

      if (this.focoTexto) {
        this.$refs["texto-buscar"].focus();
      }
    },
    focustxt() {
      this.$refs["texto-buscar"].select();
    },
    cancelar() {
      this.$emit("cancelar");
      this.mostrar = false;
    },
    handleBlur() {
      if ((this.$refs["texto-buscar"].value != "") & this.buscarBlur) {
        this.buscar();
      }
    },
  },
  watch: {
    items(newValue) {
      if (newValue.length == 1) {
        this.seleccionarItemClick(0);
      }
    },
  },
};
</script>

<style>
.overlay-collapsed {
  opacity: 0;
  display: none;
  /*transition: all 50ms ease-out;*/
}

.tableSelected {
  background-color: cornflowerblue;
}
.overlay-show {
  opacity: 1;
  transition: all 100ms ease-out;
  z-index: 10001;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
}

.my-custom-scrollbar {
  position: relative;
  height: 250px;
  overflow: auto;
}
.table-wrapper-scroll-y {
  display: block;
}

.cursor-pointer {
  cursor: pointer;
}
.displayBlockWidth {
  /* display: inline-block; 
		width: 100%; */
  float: left;
  width: 100%;
  text-align: right;
}
.tableScroll {
  height: 600px;
  overflow-y: scroll;
}
.borderBottom {
  border-bottom: 1px solid rgb(221, 221, 221);
}
#tablaClientes td:first-child {
  width: 80px;
}
</style>