<template>
    <div>
        <div style="max-width: 800px; margin: 0 auto; display: flex; align-items: center; justify-content: space-between">
            <div>
                <!-- <h1>Your coordinates:</h1>
                <p>{{ myCoordinates.lat }} Latitude, {{ myCoordinates.lng }} Longitude</p> -->
            </div>
            <div>
                <!-- <h1>Map coordinates:</h1>
                <p>{{ mapCoordinates.lat }} Latitude, {{ mapCoordinates.lng }} Longitude</p> -->
            </div>
        </div>
        <div class="row">
            <div class="col-12 col-md-12">
                <label>Lugar especif√≠co <span class="text-danger">*</span></label>
                <gmap-autocomplete
                    class="form-control col-md-12 col-12"
                    @place_changed="setPlace"
                    :value="searchLocation">
                </gmap-autocomplete>
                <br>
                <!-- <button type="button" @click="addMarker" class="btn btn-primary btn-flat col-12 col-md-12">Buscar</button> -->
            </div>
        </div>
        <GmapMap
            :center="myCoordinates"
            :zoom="zoom"
            style="width:100%; height:360px; margin: 32px auto;"
            ref="mapRef"
            @dragend="handleDrag"
        >
            <!-- <gmap-info-window :opened="infoOpened" @closeclick="infoOpened=false">
                {{infoContent}}
            </gmap-info-window> -->
            <gmap-marker                                
                :position="marker.position"
                :draggable="true"   
                
                @dragend="handleDragMarker"                
            ></gmap-marker>



        </GmapMap>
    </div>
</template>
<script>
    export default {
        props:["searchLocation","currentLocation"],
        data() {
            return {
                map: null,                
                myCoordinates: {
                    lat: 0,
                    lng: 0
                },
                zoom: 7,
                center: { lat: 45.508, lng: -73.587 },
                marker:{position:{}},
                places: [],
                currentPlace: null,
                infoOpened:false,
                infoContent:""   
            }
        },
        created() {
            // does the user have a saved center? use it instead of the default
            if(localStorage.center) {
                this.myCoordinates = JSON.parse(localStorage.center);
            } else {
                // get user's coordinates from browser request
                this.$getLocation({})
                    .then(coordinates => {
                        this.myCoordinates = coordinates;
                    })
                    .catch(error => alert(error));
            }

            // does the user have a saved zoom? use it instead of the default
            if(localStorage.zoom) {
                this.zoom = parseInt(localStorage.zoom);
            }
        },
        mounted() {
            // add the map to a data object
            this.$refs.mapRef.$mapPromise.then(map => this.map = map);
            this.addMarker();
        },
        methods: {
            handleDrag() {
                // get center and zoom level, store in localstorage
                // let center = {
                //     lat: this.map.getCenter().lat(),
                //     lng: this.map.getCenter().lng()
                // };
                let zoom = this.map.getZoom();                
                // localStorage.center = JSON.stringify(center);
                localStorage.zoom = zoom;
                this.addMarker()
            },
            handleDragMarker(){
                
            },
            setPlace(place) {                
                this.currentPlace = place;
                this.addMarker();
            },
            addMarker() {
                if (this.currentPlace) {
                    const marker = {
                        lat: this.currentPlace.geometry.location.lat(),
                        lng: this.currentPlace.geometry.location.lng()
                    };
                    this.myCoordinates = marker;
                    localStorage.center = JSON.stringify(marker);
                    this.marker = { position: marker };                    
                    this.places.push(this.currentPlace);
                    this.center = marker;                    
                }else{
                    const marker = localStorage.getItem("center");
                    this.marker={position : JSON.parse(marker)};
                    console.log(this.marker)                    
                }
            },            
            geolocate: function() {
                navigator.geolocation.getCurrentPosition(position => {
                    this.center = {
                        lat: position.coords.latitude,
                        lng: position.coords.longitude
                    };

                });
            },
            toggleInfo: function(marker) {
                this.infoPosition = this.getPosition(marker);
                
                // this.infoContent = marker.full_name;
                this.infoContent = "Obra 6 de enero";
                // if (this.infoCurrentKey == key) {
                this.infoOpened = !this.infoOpened;
                // } else {
                    // this.infoOpened = true;
                    // this.infoCurrentKey = key;
                // }
            },
            getPosition: function() {                
                return {
                    currentLocation: {
                        lat:parseFloat(this.marker?.position?.lat),
                        long: parseFloat(this.marker?.position?.lng)
                    }
                }
            },

        },
        computed: {
            mapCoordinates() {
                if(!this.map) {
                    return {
                        lat: 0,
                        lng: 0
                    };
                }

                return {
                    lat: this.map.getCenter().lat().toFixed(4),
                    lng: this.map.getCenter().lng().toFixed(4)
                }
            }
        }
    }
</script>