<template>
  <div class="container">
    <div class="row">
      <div class="col-12">
        <h3><i class="fas fa-clipboard-list"></i> Lista de Precios</h3>
      </div>
    </div>
    <div class="row">
      <div class="col-12 mt-3 text-center" v-if="items.length == 0">
        <div class="alert alert-warning" role="alert">
          <h4>No existen clientes asignados al Agente</h4>
        </div>
      </div>
      <div class="col-12 my-3" v-if="items.length > 0">
        <b-table :items="items" :fields="fields" striped fixed borderer responsive="sm">
          <template v-slot:cell(actions)="{ item }">
            <button class="btn btn-primary btn-sm col-12" @click="toggleDetails(item)">
              {{ item._showDetails ? "Ocultar" : "Mostrar productos" }}
            </button>
          </template>
          <template #cell(agregar_producto)="{ item }">
            <button class="btn btn-primary btn-sm col-12" @click="agregarProductos(item)">
              Agregar Producto
            </button>
          </template>

          <template v-slot:row-details="{ item: { details, indice } }">
            <div class="alert alert-info" role="alert" v-if="details.length > 0">
              <table class="table table-striped table-sm">
                <thead>
                  <tr>
                    <th scope="col">Producto</th>
                    <th scope="col" class="text-center">Precio Sin Iva</th>
                    <th scope="col" class="text-center">Precio Con Iva</th>
                    <th scope="col" class="text-center">Iva</th>
                    <th scope="col"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(producto, index) in details" :key="index">
                    <td class="font-14">
                      <b>{{ producto.nomenclatura }}</b> -
                      {{ producto.descripcionProducto }}
                    </td>
                    <td class="col-2">
                      <input
                        v-selectonfocus
                        v-model="producto.precioSinIva"
                        maxlength="9"
                        @keypress="numerosDecimales"
                        :disabled="producto.disabled"
                        class="form-control text-right"
                        v-on:blur.stop="calculoSinIva(index, indice, false)"
                        v-on:keyup.enter="calculoSinIva(index, indice, false)"
                        @keydown.tab.stop="bloqTab"
                      />
                    </td>
                    <td class="col-2">
                      <input
                        v-selectonfocus
                        v-model="producto.precioConIva"
                        :readonly="producto.disabled"
                        @keypress="numerosDecimales"
                        maxlength="9"
                        class="form-control text-right"
                        v-on:blur.stop="calculoConIva(index, indice, false)"
                        v-on:keyup.enter="calculoConIva(index, indice, false)"
                        @keydown.tab.stop="bloqTab"
                      />
                    </td>
                    <td class="col-2 text-right">
                      <input
                        v-selectonfocus
                        v-model="producto.iva"
                        @keypress="numerosDecimales"
                        maxlength="9"
                        :readonly="true"
                        class="form-control text-right"
                        @keydown.tab.stop="bloqTab"
                      />
                    </td>
                    <td
                      v-if="producto.disabled"
                      class="text-center p-2"
                      @click="editItem(index, indice)"
                    >
                      <span class="icon-edit">
                        <i class="fa fa-edit" aria-hidden="true"></i>
                      </span>
                    </td>
                    <td
                      v-if="!producto.disabled"
                      class="text-center p-2"
                      @click="editItem(index, indice, producto)"
                    >
                      <span class="icon-edit">
                        <i class="fa fa-save" aria-hidden="true"></i>
                      </span>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>

            <div
              v-if="details.length == 0"
              class="alert alert-info pt-3 pb-0"
              role="alert"
            >
              <div class="alert alert-warning text-center pt-1 pb-0" role="alert">
                <h5>No hay productos asignados.</h5>
              </div>
            </div>
          </template>
        </b-table>
      </div>
    </div>

    <b-modal v-model="modalShow" centered size="lg" hide-footer>
      <div class="row" v-if="nuevoProducto.length == 0">
        <div class="col-12">
          <typeahead
            :enabled-enter-search="false"
            :min-car-length="minimoCaracteres"
            @selectitem="returnedItem"
            input-label="Buscar Producto: "
            place-holder="N2002012"
            v-bind:data-source="datasource"
            v-bind:limpiar-control="limpiarControl"
          ></typeahead>
        </div>
      </div>
      <div v-else>
        <div class="row">
          <div class="col-12 text-center">
            <label for="">
              <b>{{ nuevoProducto.nomenclatura }}</b></label
            >
          </div>
          <div class="col-12 text-center">
            <label>
              {{ nuevoProducto.descripcion }}
            </label>
          </div>
        </div>

        <div class="row my-2">
          <div class="col-12 col-md-4">
            <label for="precio">Precio sin IVA:</label>
            <input
              v-selectonfocus
              type="text"
              class="form-control"
              v-model="productoSeleccionado.precioSinIva"
              @keypress="numerosDecimales"
              v-on:blur="calculoSinIva(0, 0, true)"
              v-on:keyup.enter="calculoSinIva(0, 0, true)"
              @keydown.tab.stop="bloqTab"
            />
          </div>
          <div class="col-12 col-md-4">
            <label for="precio">Precio con IVA:</label>
            <input
              v-selectonfocus
              type="text"
              class="form-control"
              v-model="productoSeleccionado.precioConIva"
              @keypress="numerosDecimales"
              v-on:blur="calculoConIva(0, 0, true)"
              v-on:keyup.enter="calculoConIva(0, 0, true)"
              @keydown.tab.stop="bloqTab"
            />
          </div>
          <div class="col-12 col-md-4">
            <label for="precio">IVA:</label>
            <input
              v-selectonfocus
              v-model="productoSeleccionado.Iva"
              type="text"
              class="form-control"
              :readonly="true"
            />
          </div>
        </div>

        <div class="row text-right">
          <div class="col-12 mt-3">
            <button
              class="btn btn-sm btn-secondary col-12 col-md-3"
              @click="limpiarDatosProducto"
            >
              Cancelar
            </button>
            <button
              ref="btnModalGuardar"
              class="btn btn-sm btn-primary col-12 col-md-3 ml-2"
              @click="guardarPrecioProducto"
            >
              Guardar
            </button>
          </div>
        </div>
      </div>
    </b-modal>
  </div>
</template>

<script>
import servicio from "@/services/servicio-agentes";
import servicioDosificador from "@/services/servicio-dosificador.js";
import servicioGenerales from "@/services/servicio-general";
import Typeahead from "@/components/Typeahead";

export default {
  data() {
    return {
      precioAnterior: 0,
      guardarCambioPrecioCte: false,
      modalShow: false,
      productoSeleccionadoBand: false,
      nuevoProducto: [],
      fields: [
        { key: "cliente", label: "CLIENTES" },
        { key: "actions", label: "" },
        { key: "agregar_producto", label: "" },
      ],
      items: [],
      productos: [],
      productosXCliente: [],
      productoSeleccionado: "",
      clienteSeleccionado: [],
      productoBuscar: "",
      editedUser: null,
      Iva: 0,
      minimoCaracteres: 2,
      datasource: "/dosificador/formulas/",
      limpiarControl: false,
    };
  },
  components: {
    Typeahead,
  },
  computed: {
    filterStateProductos: function () {
      return this.productos.filter((item) => {
        const nomenclatura = item.nomenclatura.toString();

        const searchTerm = this.productoBuscar.trim().toUpperCase();

        return nomenclatura.includes(searchTerm);
      });
    },
  },
  async mounted() {
    this.Iva = await this.obtenerParametro("Iva");
    this.obtenerClientes();
    this.cargarProductos(0);
  },
  created() {},
  methods: {
    bloqTab($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (keyCode == 9) $event.preventDefault();
    },
    returnedItem(item) {
      this.limpiarControl = false;
      this.nuevoProducto = item;
    },
    calculoSinIva(index, indice, isModal) {
      if (isModal) {
        let precioNormal = this.productoSeleccionado.precioSinIva;
        let precioIva = precioNormal * (1 + Number(this.Iva / 100));
        this.productoSeleccionado.precioConIva = precioIva.toFixed(2);
        this.productoSeleccionado.Iva = (precioIva - precioNormal).toFixed(2);
        this.$refs["btnModalGuardar"].focus();
      } else {
        let precioNormal = Number(this.items[indice].details[index].precioSinIva);
        let precioIva = precioNormal * (1 + Number(this.Iva / 100));
        this.$set(this.items[indice], "_showDetails", false);
        this.items[indice].details[index].precioSinIva = precioNormal.toFixed(2);
        this.items[indice].details[index].precioConIva = precioIva.toFixed(2);
        this.items[indice].details[index].iva = (precioIva - precioNormal).toFixed(2);
        this.$set(this.items[indice], "_showDetails", true);
      }
    },
    calculoConIva(index, indice, isModal) {
      if (isModal) {
        let precioIva =  this.productoSeleccionado.precioConIva;
        let iva = precioIva * (Number(this.Iva / 100));
        console.log(precioIva, " -- " , iva);
        this.productoSeleccionado.precioConIva = precioIva.toFixed(2);
        this.productoSeleccionado.precioSinIva = (precioIva - iva).toFixed(2);
        this.productoSeleccionado.Iva = iva.toFixed(2);
        this.$refs["btnModalGuardar"].focus();

      } else {
        this.$set(this.items[indice], "_showDetails", false);
        let precioIva = Number(this.items[indice].details[index].precioConIva);
        let precioSinIva = precioIva / (1 + Number(this.Iva / 100));
        this.items[indice].details[index].precioConIva = precioIva.toFixed(2);
        this.items[indice].details[index].precioSinIva = precioSinIva.toFixed(2);
        this.items[indice].details[index].iva = (precioIva - precioSinIva).toFixed(2);
        this.$set(this.items[indice], "_showDetails", true);
      }
    },
    async editItem(index, indice, producto) {
      if (!this.items[indice].details[index].disabled) {
        let data = {
          codProductoXCliente: producto.codigo,
          codCliente: producto.codCliente,
          codProducto: producto.codProducto,
          precioSinIva: producto.precioSinIva,
          precioConIva: producto.precioConIva,
          Iva: producto.iva,
        };

        let r = await this.$swal.fire({
          title: "¬øDesea actualizar el precio del producto asignado al cliente?",
          showCancelButton: true,
          cancelButtonColor: "#d33",
          confirmButtonText: "Aceptar",
          cancelButtonText: "Cancelar",
        });

        if (r.value) {
          let r = await servicio.guardarPrecioProducto(data);
          if (r.value) {
            this.modalShow = false;
            this.clienteSeleccionado = [];
            this.mostrarListaProductos = false;
            this.$set(this.items[indice], "_showDetails", false);
            this.items[indice].details[index].disabled = true;
            this.$set(this.items[indice], "_showDetails", true);
            this.$swal.fire({
              text: r.message,
              icon: "info",
            });
          } else {
            this.$swal.fire({
              text: r.message,
              icon: "info",
            });
          }
        } else {
          this.$set(this.items[indice], "_showDetails", false);
          this.items[indice].details[index].precioSinIva = this.precioAnterior;
          this.items[indice].details[index].disabled = true;
          this.$set(this.items[indice], "_showDetails", true);
        }
      } else {
        this.precioAnterior = this.items[indice].details[index].precioSinIva;
        this.$set(this.items[indice], "_showDetails", false);
        this.items[indice].details[index].disabled = false;
        this.$set(this.items[indice], "_showDetails", true);
      }
    },
    numerosDecimales($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
        $event.preventDefault();
      } else {
        this.disabled = true;
      }
    },
    async obtenerParametro(valor) {
      let r = await servicioGenerales.obtenerParametro(valor);
      if (r.value) {
        return r.data.valor;
      }
    },

    async obtenerProductosXCliente(codCliente) {
      this.productosXCliente = [];
      let r = await servicio.obtenerProductosXCliente(codCliente);

      if (r.value) {
        this.productosXCliente = r.data;
      }
    },
    agregarProductos(row) {
      this.limpiarDatosProducto();
      this.nuevoProducto = [];
      this.modalShow = true;
      this.clienteSeleccionado = row;
      this.mostrarListaProductos = true;
    },
    async obtenerProductos(codigo) {
      return await servicioDosificador.obtenerFormulas(codigo);
    },
    cancelarBusquedaPrecio() {
      this.mostrarListaProductos = true;
      this.productoBuscar = "";
    },
    SeleccionaProducto(producto) {
      this.productoSeleccionado = producto;
      this.mostrarListaProductos = false;
    },
    async guardarPrecioProducto() {
      let data = {
        codProductoXCliente: 0,
        codCliente: this.clienteSeleccionado.codigo,
        codProducto: this.nuevoProducto.codigo,
        precioSinIva: this.productoSeleccionado.precioSinIva,
        precioConIva: this.productoSeleccionado.precioConIva,
        Iva: this.productoSeleccionado.iva,
      };

      let r = await this.$swal.fire({
        title: "¬øDesea guardar el precio del producto asignado al cliente?",
        showCancelButton: true,
        cancelButtonColor: "#d33",
        confirmButtonText: "Aceptar",
        cancelButtonText: "Cancelar",
      });
      if (r.value) {
        this.$loading(true);
        var result = await servicio.guardarPrecioProducto(data);
        if (result.value) {
          this.$loading(false);
          await this.$swal.fire({
            text: result.message,
            icon: "info",
          });

          this.modalShow = false;
          this.mostrarListaProductos = false;
          this.$set(this.clienteSeleccionado, "_showDetails", false);
          let r = await servicio
            .obtenerProductosXCliente(this.clienteSeleccionado.codigo)
            .catch((ex) => {
              console.error("ha ocurrido un error", ex);
            });
          if (r.data.length > 0) {
            const prod = r.data;
            this.clienteSeleccionado.details = prod;
            this.nuevoProducto = [];
            this.limpiarDatosProducto();
            this.$set(this.clienteSeleccionado, "_showDetails", true);
          }
        } else {
          this.$loading(false);
          this.$swal.fire({
            text: result.message,
            icon: "info",
          });
        }
        this.$loading(false);
      }
    },

    async cargarProductos(codigo) {
      this.$loading(true);
      let r = await this.obtenerProductos(codigo);
      if (r.value) {
        this.$loading(false);
        r.data.forEach((element) => {
          let tipo = {
            value: element.codigo,
            text: element.descripcion,
            nomenclatura: element.nomenclatura,
          };
          this.productos.push(tipo);
        });
      }
      this.$loading(false);
    },

    async obtenerClientes() {
      this.$loading(true);
      await servicio
        .obtenerClientes()
        .then((r) => {
          this.$loading(false);
          let data = r.data;
          r.data.forEach((element, index) => {
            data[index].cliente = element.nombreCompleto;
          });
          this.items = data;
        })
        .catch((error) => {
          this.$loading(false);
          console.error(error);
        });
    },

    async toggleDetails(item) {
      this.$loading(true);
      if (item._showDetails) {
        this.$set(item, "_showDetails", false);
      } else if (item.details) {
        this.$set(item, "_showDetails", true);
      } else {
        let r = await servicio.obtenerProductosXCliente(item.codigo).catch((ex) => {
          console.error("ha ocurrido un error", ex);
        });

        if (r) {
          const prod = r.data;
          item.details = prod;
          this.$set(item, "_showDetails", true);
        }
      }
      this.$loading(false);
    },
    limpiarDatosProducto() {
      this.productoSeleccionado = {
        precioConIva: 0,
        precioSinIva: 0,
        iva: 0,
      };
    },
  },
};
</script>

<style>
.centrado {
  /* text-align: center; */
  align-content: center;
}

.icon-edit {
  color: var(--color-blanco);
}

.icon-edit:hover {
  color: rgb(141, 19, 19);
  cursor: pointer;
}

.tabletipoproducto {
  border: 2px solid #5b9bd5;
  border-radius: 3px;
  background-color: #fff;
  width: 100%;
}

table {
  border: 1px solid #c2c2c2;
  border-radius: 3px;
  background-color: #fff;
}

th {
  background-color: #5b9bd5;
  color: rgb(255, 255, 255);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
}

.hover {
  cursor: pointer;
}

input {
  margin: 0 auto;
}

.active {
  color: red;
}

/* td {
		background-color: #f9f9f9;
	} */
</style>
