// export default servicio;

import base from '@/services/servicio-base'

const servicio = {}

servicio.login = (usuario, password) => {
    return base.post('/seguridad/login', { usuario, password })
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}
//ESTE SERVICIO REGRESA TRUE O FALSE
servicio.verificaPermiso = (codUsuario, modulo)=>{
    return base.post(`/seguridad/verificaPermisoModulo/${modulo}/${codUsuario}`)
    .then(r=>{
        return r.data
    })
    .catch(err=>{
        console.log(err)
    })
}

export default servicio