  </div>
</template>
<script>
import TableSortable from "@/components/TableSortable";
import servicio from "@/services/servicio-clientes";
import servicioGenerales from "@/services/servicio-general";
import servicioMateriales from "@/services/servicio-materiales";
import GoogleMap from "@/components/GoogleMap";
import servicioAgentes from '@/services/servicio-agentes';
import servicioSeguridad from '@/services/servicio-seguridad'
export default {
  data() {
    return {
    //se deja un espacio en blanco para el boton
    fieldsContactos: ['','Nombre', 'Tel√©fono', 'Telefono M√≥vil','Correo Electr√≥nico'],        
    currentLocation:{
        lat:0,
        lng:0
      },
      activeGeneral:false,
      activeDirecciones: false,
      activeCredito:false,
      activeContactos:false,
      disabledVendedor: false,
      permisoCobranza:false,
      mostrarDirecciones:false,
      mostrarDireccionFiscal:false,
      mostrarContactos:true,
      optionsRS: [
        { value: null, text: "Seleccione Regimen Fiscal..." },
        { value: 'PM', text: "Persona Moral" },
        { value: 'PF', text: "Persona F√≠sica" },
      ],
      form: {
        nombre: "",
        apellidoP: "",
        apellidoM: "",
        rFC: "",
        estatus: null,
        tipoEmpleado: null,
        alias: "",
        telefono: "",
        celular: "",
        correo: "",
        credito: 0,
        codVendedor: null,
        regimenFiscal: null,
        razonSocial:"",
        rfcGenerico:false,
        nombreComercial:"",
        rFCValidate:{
            esValido:false,
            msg:""
        },
        contacto:{
            codContacto:0,            
            nombreContacto:'',
            telefono:'',
            telefonoMovil:'',
            correo:'',
            contactoPrincipal:false
        },
        direccionObra: {
          cP: "",
          codEstado: 0,
          estado: "",
          codTipoObra: 0,
          codMunicipio: 0,
          municipio: "",
          ciudad: "Culiac√°n Rosales",
          codColonia: null,
          colonia: "",
          calleNumero: "",
          latitud: 0,
          longitud: 0,
          referencia: "",
          uRLGoogleMaps : "",
          entrega:true,
          fiscal:false
        //   uRLGoogleMaps: `https://www.google.com/maps/search/?api=1&query=${}58.698017,-152.522067`,
        },
        direccionFiscal:{
          cP: "",
          codEstado: 0,
          estado: "",
          codTipoObra: 0,
          codMunicipio: 0,
          municipio: "",
          ciudad: "Culiac√°n Rosales",
          codColonia: null,
          colonia: "",
          calleNumero: "",
          latitud: 0,
          longitud: 0,
          referencia: "",
          uRLGoogleMaps : "",
          entrega:false,
          fiscal:true
        }
      },
      vendedores: [{ value: null, text: "Seleccione un vendedor..." }],
      //FORMULARIO DIRECCION
      colonias: [{ value: null, text: "Seleccione una colonia..." }],    
      coloniasFiscales:[{value:null, text:"Seleccione una colonia..."}],
      tiposObras: [{ value: null, text: "Seleccione el tipo de obra..." }],
      disabledSubtipos: true,
      disabledMunicipio: true,
      disabledColonia: true,
      disabledCP: false,      
      //FINALIZA FORM DIRECCION
      //TABLA DE OBRAS
      rutaNuevo: "TiposConcreto/Registro",
      permisoEscritura: true,
      itemsDireccionesObras: [],      
      itemsDireccionesFiscales: [],
      itemsContactos: [],
      fieldsObras: [
        {
          key: "colonia",
          label: "Colonia",
          sortable: true,
          class: "text-center",
        },
        {
          key: "domicilio",
          label: "Domicilio",
          sortable: true,
          sortDirection: "desc",
        },
        { key: "apiGoogleMaps", label: "Accion" },
        // { key: 'show_details', label: 'Detalles' }
      ],      
      //finaliza tabla de obras
      //TABLA DE DIRECCIONES
        fieldsDireccionesFiscales: [
        {
            key: "colonia",
            label: "Colonia",
            sortable: true,
            class: "text-center",
        },
        {
            key: "domicilio",
            label: "Domicilio",
            sortable: true,
            sortDirection: "desc",
        },
        { key: "apiGoogleMaps", label: "Accion" },
            // { key: 'show_details', label: 'Detalles' }
        ],      

      //finaliza tabla de direcciones
    };
  },
  components: {
    TableSortable,
    GoogleMap,
  },
  async mounted() {
      this.activeGeneral = true;      
    // if (this.verificaTokenValido()) {
      this.options = null;
      this.id = this.$route.params.id != undefined ? this.$route.params.id : 0;      
      this.cargaCliente();      
    //   this.consultaTiposObras();
      this.permisoEscritura = true;
      this.$loading(true);
        await servicioSeguridad.verificaPermiso(this.usuario.idUsuario,"cobranza").then(r=>{
          this.permisoCobranza = r.data;
        });
      this.$loading(false);

      // this.permisoEscritura = this.verificaPermiso("clientes");
    // }
  },
  async created() {       
        if(this.usuario.agente === 1) {
            this.vendedores = [{ value: this.usuario.codEmpleado, text: this.usuario.nombre }];
            this.form.codVendedor = this.vendedores[0].value;
            this.disabledVendedor = true;            
        } else {
            this.disabledVendedor = false;
            await this.obtenerVendedores();
        }
        await this.ObtenerPermisos();      

    },
  computed: {
    validationNombres() {
      let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
      return this.nombre.match(letras) && this.nombre.length > 1 ? true : false;
    },
    validationApellidoP() {
      let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
      return this.apellidoP.match(letras) && this.apellidoP.length > 1
        ? true
        : false;
    },
    searchLocationObra() {
      let colonia = "";
      let currentCodColonia = this.form.direccionObra.codColonia;
      if (currentCodColonia != null) {
        colonia = this.colonias.find((el) => {
          return el.value == currentCodColonia;
        });
      }
      return `${this.form.direccionObra.calleNumero} ${
        colonia?.text == undefined ? "" : colonia?.text
      }`;
    },
    searchLocationFiscal(){
        let colonia = "";
        let currentCodColonia = this.form.direccionFiscal.codColonia;        
        if (currentCodColonia != null) {
            colonia = this.coloniasFiscales.find((el) => {
                return el.value == currentCodColonia;
            });
        }
        return `${this.form.direccionFiscal.calleNumero} ${
            colonia?.text == undefined ? "" : colonia?.text
        }`;
    }
  },
  watch: {
    "form.direccionFiscal.cP": function(newValue, oldValue){
        if(newValue != oldValue){
            this.form.direccionFiscal.codColonia = null;
            if(newValue.length == 5){                
                this.obtenerDatosCP(true);
            }else{
                this.coloniasFiscales = [{ value: null, text: "Seleccione una colonia..." }];                                              
                this.form.direccionFiscal.municipio = "";
            }
        }
    },
    "form.direccionObra.cP": function (newValue, oldValue) {
      if (newValue != oldValue) {
        this.form.direccionObra.codColonia = null;
        if (newValue.length == 5) {                   
            this.obtenerDatosCP(false);
        } else {
            this.colonias = [{ value: null, text: "Seleccione una colonia..." }];                                        
            this.form.direccionFiscal.municipio = "";
        }
      }
    },
    "form.rFC": function (newValue) {
      if (this.form.rFC != undefined) {
        this.form.rFC = this.form.rFC.toUpperCase();
        if (
          /^([A-Z,√ë,&]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1])[A-Z|\d]{3})$/.test(
            newValue
          )
        ) {
          this.form.rFCValidate.esValido = true;
          this.form.rFCValidate.msg = "El RFC es v√°lido";
        } else {
          this.form.rFCValidate.esValido = false;
          this.form.rFCValidate.msg = "El RFC NO es v√°lido";
        }
      }
    },
    "form.rfcGenerico":function (newValue){
        this.form.rFC = newValue ? 'XAXX010101000' : '';
    }
  },
  methods: {      
    onSubmit(evt) {
      evt.preventDefault();
      this.guardarCliente();
    },
    limpiar() {
      this.$swal
        .fire({
          title: "¬ø Est√° seguro ?",
          text: "Se limpiar√°n todos los campos del formulario...",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, limpiar!",
        })
        .then((result) => {
          if (result.value) {
            this.form.nombre = "";
            this.form.apellidoP = "";
            this.form.apellidoM = "";
            this.form.rFC = "";
            this.form.regimenFiscal = null;
            this.form.alias = "";
            this.form.telefono = "";
            this.form.celular = "";
            this.form.correo = "";
            this.form.fechaRegistro = "";
            this.form.credito = null;
            this.form.codVendedor = null;
            // this.money = "";
          }
        });
    },
    MostrarAgregarDireccionesFiscas(e){
        this.mostrarDireccionFiscal = !this.mostrarDireccionFiscal;
        e.preventDefault();
    },
    guardarCliente() {
      let cliente = {
        codigo: this.id,
        nombre: this.form.nombre,
        nombreCompleto: "",
        apellidoP: this.form.apellidoP,
        apellidoM: this.form.apellidoM,
        rFC: this.form.rFC,
        regimenFiscal: this.form.regimenFiscal,
        alias: this.form.alias,
        telefono: this.form.telefono,
        celular: this.form.celular,
        correo: this.form.correo,     
        nombreComercial: this.form.nombreComercial,
        razonSocial: this.form.razonSocial,   
        // "fechaRegistro":this.form.fechaRegistro,
        // credito: this.form.credito.match(/(\d)/g).join("") / 100,
        // "credito":this.form.credito.match(/^[0-9]+\.[0-9]+$/).join(""),
        codEmpleadoVendedor: this.form.codVendedor,
      };
      servicio.guardarCliente(cliente).then((r) => {
        if (r.value) {
            var codUltimoCliente = r.data[0];
            // alert(codUltimoCliente);                             
            this.$swal.fire({            
                text: `El cliente ${this.form.nombre} se ha guardado exitosamente!`,
                icon: "success",
            });
          
        //   window.history.back();
        this.id = codUltimoCliente;
        this.mostrarDirecciones = true;
        this.activeDirecciones = true;

        } else {
          this.$swal.fire({
            text: `No se pudo guardar el cliente, contacte a sistemas`,
            icon: "error",
          });
        }
      });
    },
    async guardarContacto(){
        this.$loading(true);    
        let contactoAGuardar = {
            codCliente:this.id,
            codContacto:this.form.contacto.codContacto,
            nombreContacto:this.form.contacto.nombreContacto,
            telefonoMovil: this.form.contacto.telefonoMovil,
            correo:this.form.contacto.correo,
            contactoPrincipal:this.form.contacto.contactoPrincipal,
        };
        await servicio.guardarContacto(contactoAGuardar).then((r) => {
            console.log(r);
            var alertType = r.value ? 'success' : 'error';
            this.$swal.fire({                
                text:r.message,
                icon:alertType
            });
            this.cargaCliente();
        });
        this.$loading(false);
    },
    // vendedores
    async obtenerVendedores() {
        // this.vendedores = [{ value: 0, text: "Seleccione el vendedor" }];
        this.$loading(true);
        await servicioAgentes.agentesCombo().then((r) => {
            if (r.value) {
                r.data.forEach((element) => {
                    let agente = {
                        value: element.codVendedor,
                        text: element.nombreVendedor,
                    };
                    this.vendedores.push(agente);
                });
            }
        });
        this.$loading(false);
    },
    consultaTiposObras() {
      servicioMateriales.tiposObras().then((r) => {
        if (r.value) {
          r.data.forEach((element) => {
            let tipoObra = {
              value: element.codigo,
              text: element.descripcionCorta,
            };
            this.tiposObras.push(tipoObra);
          });
        }
      });
    },
    cargaCliente() {
      if (this.id > 0) {
        this.$loading(true);
        servicio.clientes(this.id).then((r) => {
          if (r.value) {
            this.mostrarDirecciones = true;
            this.form.nombreComercial = r.data.cliente[0].nombreComercial;
            this.form.razonSocial = r.data.cliente[0].razonSocial;
            this.form.nombre = r.data.cliente[0].nombre;
            this.form.apellidoP = r.data.cliente[0].apellidoP;
            this.form.apellidoM = r.data.cliente[0].apellidoM;
            this.form.rFC = r.data.cliente[0].rFC;
            this.form.estatus = r.data.cliente[0].estatus;
            this.form.tipoEmpleado = r.data.cliente[0].tipoEmpleado;
            this.form.alias = r.data.cliente[0].alias;
            this.form.telefono = r.data.cliente[0].telefono;
            this.form.celular = r.data.cliente[0].celular;
            this.form.correo = r.data.cliente[0].correo;
            this.form.credito = r.data.cliente[0].creditoFormat;
            this.form.codVendedor = r.data.cliente[0].codEmpleadoVendedor;
            this.form.regimenFiscal = r.data.cliente[0].regimenFiscal;                        
            //Itero sobre las direcciones que me trae del servicio para agregarlas a fiscal o a obras
            this.itemsDireccionesFiscales = [];
            this.itemsDireccionesObras = [];
            console.log("Direcciones -> ",r.data?.direccionesXCliente)
            r.data?.direccionesXCliente.forEach(dir=>{
                if(dir.fiscal && !dir.entrega){
                    this.itemsDireccionesFiscales.push(dir);
                }else if(dir.entrega && !dir.fiscal){                    
                    this.itemsDireccionesObras.push(dir);
                }
            });



            this.itemsContactos = r.data.contactosXCliente;
            this.$loading(false);
          } else {
            this.$loading(false);
            this.$swal.fire({
              text: `${r.message}`,
              icon: "error",
            });
            setTimeout(() => {
              this.$router.push({ path: "." });
            }, 1000);
          }
        });
      }
    },

    async addContacto(button){
        // let contacto = [];
        this.$root.$emit("bv::show::modal", 'modalContactos', button);        
    },

    /*
    async CapturarObservacion() {

        let observacion;

        const { value: formValues } = await Swal.fire({
            title: 'Ingrese una observaci√≥n:',
            html:
                '<textarea id="swal-input2" class="swal2-input" placeholder="Capture una observaci√≥n" style="margin-top: 20px; margin-bottom: 20px; height: 120px;">',
            focusConfirm: false,
            showCancelButton: true,
            confirmButtonText: 'Aceptar',
            cancelButtonText: 'Cancelar',
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',

            preConfirm: () => {

                observacion = Swal.getPopup().querySelector('#swal-input2').value

                let inputObservacion = Swal.getPopup().querySelector('#swal-input2');

                inputObservacion.style.borderColor = "";

                if (observacion === '') {
                    inputObservacion.style.borderColor = "red";
                    Swal.showValidationMessage(`Favor de ingresar una observaci√≥n.`)
                }
            },

        })

        if (formValues) {
            this.observacion = observacion;
            await this.AutorizarPedidoDetenido();
        } else {
            this.procederGuardado = false;
            this.errorUsuario = false;
        }

