<template>
    <div class="container py-5">
        <h3><i class="fas fa-truck"></i> Registro de equipos </h3>
        <div class="py-5">
            <!-- <h6 class="text-danger">Los campos marcados con ( * ) son requeridos.</h6> -->
            <b-form @submit="onSubmit">
                
                <b-row>                    
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Marca *"
                        label-for="cboMarca">
                            <b-form-select 
                                v-model="form.codigoMarca" 
                                :options="marcas" 
                                required
                                id="cboMarca"
                                aria-placeholder="Seleccione...">
                            </b-form-select>                           
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Modelo *"
                        label-for="cboModelo">
                            <b-form-select 
                                v-model="form.codigoModelo" 
                                :options="modelos" 
                                id="cboModelo"
                                required
                                aria-placeholder="Seleccione...">
                            </b-form-select>                           
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Tipo *"
                        label-for="cboTipo">
                            <b-form-select 
                                v-model="form.codigoTipoEquipo" 
                                :options="tiposEquipo" 
                                id="cboTipo"
                                required
                                aria-placeholder="Seleccione...">
                            </b-form-select>                           
                        </b-form-group>
                    </b-col>
                </b-row>

                <b-row>
                    <!-- <b-col class="col-md-2 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Tipo de concreto"
                        label-for="cboConcreto">
                            <b-form-select 
                                v-model="form.tipoConcreto" 
                                :options="concretos" 
                                id="cboConcreto"
                                aria-placeholder="Seleccione...">
                            </b-form-select>                           
                        </b-form-group>
                    </b-col> -->
                    <!-- <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-2"
                        label="Nombre del equipo *"
                        label-for="txtNombreEquipo">
                            <b-form-input
                            id="txtNombreEquipo" 
                            v-model="form.nombre"
                            type="text"
                            required
                            placeholder="Nombre de equipo" 
                            class="text-capitalize"
                            autocomplete="off"                            
                            >                            
                            </b-form-input>
                        </b-form-group>
                    </b-col> -->

                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-3"
                        label="N√∫mero de serie"
                        label-for="txtNumeroSerie">
                            <b-form-input
                            id="txtNumeroSerie"          
                            type="text"
                            v-model="form.numeroSerie"
                            placeholder="# de serie"
                            class="text-capitalize"
                            autocomplete="off">
                            </b-form-input>
                        </b-form-group>
                    </b-col> 
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-3"
                        label="Descripci√≥n *"
                        label-for="txtDescripcion">
                            <b-form-input
                            id="txtDescripcion"          
                            type="text"
                            v-model="form.descripcion"
                            required
                            placeholder="Descripci√≥n"
                            class="text-capitalize"
                            autocomplete="off">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-3"
                        label="Numero Economico *"
                        label-for="txtIdentificador">
                            <b-form-input
                            id="txtIdentificador"          
                            type="text"
                            v-model="form.identificador"
                            required
                            placeholder="Numero Economico"
                            class="text-capitalize"
                            autocomplete="off">
                            </b-form-input>
                        </b-form-group>
                    </b-col>                                     
                </b-row> 

                <b-row>
                    
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-3"
                        label="N√∫mero de serie de motor"
                        label-for="txtNumeroSerieMotor">
                            <b-form-input
                            id="txtNumeroSerieMotor"
                            type="text"
                            v-model="form.numeroSerieMotor"
                            placeholder="# de serie de motor"
                            class="text-capitalize"
                            autocomplete="off">
                            </b-form-input>
                        </b-form-group>                        
                    </b-col>
                    <!-- check -->
                    <b-col class="col-md-4 col-sm-12 col-12" v-if="parametro > 0">
                        <b-form-group label="Estatus:">
                            <b-form-checkbox
                            id="chckEstatus"
                            v-model="form.estatus"
                            name="chckEstatus"                        
                            switch
                            size="lg"
                            >
                            {{form.estatus ? 'Activo':'Inactivo'}}
                            </b-form-checkbox>
                        </b-form-group>
                    </b-col>
                </b-row>                
                
                <b-row>
                    <b-col class="col-md-4 col-sm-12 col-12" align-self="end"/>                        

                    <b-col class="col-md-4 col-sm-12 col-12" align-self="end">                        
                        <b-button type="submit" block variant="success" class="mr-2 mt-2"><i class="fa fa-save"></i> Guardar</b-button>
                    </b-col>
                    <!-- <b-col class="col-md-4 col-sm-12 col-12" align-self="end">
                        <b-button @click="limpiar" type="reset" block variant="outline-danger" class="mt-2"><i class="fas fa-broom"></i>Limpiar</b-button>                        
                    </b-col> -->
                    <b-col class="col-md-4 col-sm-12 col-12" align-self="end">
                        <b-button @click="cancelar" block variant="danger" class="mt-2"><i class="fas fa-arrow-left"></i> Cancelar</b-button>
                    </b-col>
                </b-row>
            </b-form>                
        </div>
    </div>
</template>
<script>
// import servicio from '@/services/servicio-clientes'
// import servicioAgente from '@/services/servicio-agentes'
import servicioEquipos from '@/services/servicio-equipos.js'
import Swal from "sweetalert2";

export default{
    data(){
        return{
            // selectedRazonSocial:null,            
            // tipoConcreto:null,
            form:{
                codigo:0,
                nombre:'',
                descripcion:'',
                identificador:'',
                estatus:true,
                codigoMarca:null,
                codigoModelo:null,
                numeroSerie:'',
                numeroSerieMotor:'',
                codigoTipoEquipo:null
            },     
            marcas:[{value:null,text:'Seleccione marca...'}],
            modelos:[{value:null,text:'Seleccione modelo...'}],
            tiposEquipo:[{value:null,text:'Seleccione el tipo...'}],
            parametro: 0,
        }
    },
    mounted(){        
        this.$loading(true);
        this.parametro = this.$route.params.id != undefined ? this.$route.params.id : 0;
        console.log('P->', this.parametro);
        this.resistencia = null;
        // this.verificaTokenValido();
        this.consultaMarcas();
        this.consultarModelos();
        this.cosnsultarTipos();
        this.cargaEquipo();
        this.$loading(false);
    },
    computed:{
        validationNombres(){
            let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
            return this.nombre.match(letras) && this.nombre.length > 1 ? true : false;
        },
        validationApellidoP(){
            let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
            return this.apellidoP.match(letras) && this.apellidoP.length > 1 ? true : false;
        }        
    },
    methods:{
        onSubmit(evt) {
            evt.preventDefault();
            this.guardarEquipo();
        },
        
        limpiar(){
                this.form.tipoResistencia = null;
                this.form.descripcionCorta = "";
                this.form.descripcion = "";           
                this.form.estatus = false;
        },  

        consultaMarcas(){
            servicioEquipos.marcasCombo()
            .then(r=> {                
                if(r.value){
                    r.data.forEach(element => {                        
                        let marca = {value: element.codigo, text:element.descripcion}
                        this.marcas.push(marca); 
                    });
                }
            })
        },

        consultarModelos(){
            servicioEquipos.modelosCombo()
            .then(r=> {                
                if(r.value){
                    r.data.forEach(element => {                        
                        let modelo = {value: element.codigo, text:element.descripcion}
                        this.modelos.push(modelo); 
                    });
                }
            })
        },
        
        cosnsultarTipos(){
            servicioEquipos.tipoEquipoCombo()
            .then(r=> {                
                if(r.value){
                    r.data.forEach(element => {
                        console.log(element)
                        let tipo = {value: element.codigo, text:element.descripcion}
                        this.tiposEquipo.push(tipo); 
                    });
                }
            })
        },

        cargaEquipo(){

            if(this.parametro > 0) {
                this.$loading(true);
                servicioEquipos.equipos(this.parametro)
                .then(r=>{
                    if(r.value){                                            
                        this.form.nombre = r.data[0].nombre;
                        this.form.descripcion = r.data[0].descripcion;
                        this.form.identificador = r.data[0].identificador;
                        this.form.codigoModelo = r.data[0].codigoModelo;
                        this.form.codigoMarca = r.data[0].codigoMarca;
                        this.form.codigoTipoEquipo = r.data[0].codigoTipoEquipo;
                        this.form.numeroSerie = r.data[0].numeroSerie;
                        this.form.numeroSerieMotor = r.data[0].numeroSerieMotor;
                        this.form.estatus = r.data[0].estatus;
                        this.$loading(false);
                    }else{
                        this.$loading(false);
                        this.$swal.fire({
                            text: `${r.message}`,
                            icon: "error"
                        });
                        setTimeout(() => {
                            this.$router.push({ path: "." });
                        }, 1000);
                    }
                });  

            }

            
        },
        
        async guardarEquipo() {
            let r = await Swal.fire({
                title: "¬°ATENCI√ìN!",
                text: "¬øDesea guardar el equipo?",
                showCancelButton: true,
                icon: 'question',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Aceptar",
                cancelButtonText: "Cancelar",
            });
        
            if (r.value) {
                // let id = isNaN(parseInt(this.getUrlParameter("id"))) ? 0 : parseInt(this.getUrlParameter("id"));  
                let equipo = {
                    "codigo":this.parametro,
                    "nombre":this.form.nombre,
                    "descripcion":this.form.descripcion,                
                    "codigoModelo":this.form.codigoModelo,
                    "codigoMarca":this.form.codigoMarca,
                    "codigoTipoEquipo":this.form.codigoTipoEquipo,
                    "numeroSerie":this.form.numeroSerie,
                    "identificador":this.form.identificador,
                    "numeroSerieMotor":this.form.numeroSerieMotor,
                    "estatus": this.form.estatus
                }                        
                await servicioEquipos.guardar(equipo)
                .then(r=>{                                
                    if(r.value){
                        Swal.fire({
                            text: `${r.message}`,
                            icon: "success"
                        });
                        this.$router.push({ path: "/catalogos/equipos" })
                        // this.$router.push({ path: "." });
                        // setTimeout(() => {
                        //     this.$router.push({ path: "." });
                        // }, 1500);
                    }else{
                        Swal.fire({
                            text: `No se pudo guardar, contacte a sistemas`,
                            icon: "error"
                        });
                    }
                })

            }


            
        },



        //cancelar
		async cancelar() {
			let r = await Swal.fire({
				title: "¬°ATENCI√ìN!",
				text: "¬øDesea cancelar el registro del equipo?",
				showCancelButton: true,
				icon: 'question',
				confirmButtonColor: "#3085d6",
				cancelButtonColor: "#d33",
				confirmButtonText: "Aceptar",
				cancelButtonText: "Cancelar",
			});
			
			if (r.value) {
				window.history.back();
			}
		},
    }
}
</script>
