<template>
  <div :class="contentClass">
    <label v-if="inputLabel != ''">{{ inputLabel }}</label>
    <div class="input-group">
      <input
        type="text"
        class="form-control"
        v-model="buscar"
        :disabled="disabled || disabledInput"
        ref="inputTypeahead"
        :placeholder="[[placeHolder]]"
        v-bind:class="{ 'is-invalid': error, 'form-control-sm': sm }"
        @keyup.enter="enterSearch"
        @keyup.up="nextItem"
        @keyup.down="nextItem"
        @keyup.esc="hideList"
      />
      <div class="input-group-append">
        <button
          class="btn btn-danger"
          :class="{ 'btn-sm': sm }"
          type="button"
          id="btnLimpiar"
          @click="clear"
          title="Limpiar busqueda"
        >
          <i class="far fa-times-circle fa-lg"></i>
        </button>
      </div>
      <div v-if="error" class="invalid-feedback">Este campo es requerido</div>
    </div>
    <ul
      v-if="visible"
      class="vue-bootstrap-typeahead list-group"
      ref="searchList"
    >
      <li
        class="list-group-item font-12"
        v-for="(item, index) in items"
        v-bind:key="index"
        @click="selectItem(item)"
        @key="index"
        :class="{ active: currentItem === index }"
      >
        {{ item.descripcion }}
      </li>
    </ul>
  </div>
</template>

<script>
import servicio from "@/services/servicio-typeahead";
export default {
  data() {
    return {
      visible: false,
      selected: false,
      disabled: false,
      currentItem: null,
      timeoutId: 0,
      query: "",
      valueInterno: String,
      selectedItemInterno: null,
      items: [],
      buscar: "",
      error: false,
    };
  },
  props: {
    contentClass: { type: Array, defaultValue: ["mb-3"] },
    sm: { type: Boolean },
    dataSource: { type: String },
    minCarLength: { type: Number },
    enabledEnterSearch: { type: Boolean },
    inputLabel: { type: String },
    placeHolder: { type: String },
    selectedItem: { type: Object },
    limpiarControl: { type: Boolean },
    disabledInput: { type: Boolean },
  },
  methods: {
    hideList() {
      this.items = [];
    },
    nextItem() {
      if (event.keyCode === 38 && this.currentItem >= 1) {
        this.currentItem--;
      } else if (
        event.keyCode === 40 &&
        this.currentItem <= this.items.length
      ) {
        this.currentItem++;
      }

      var eloffset = 49 * this.currentItem;
      var viewPort =
        this.$refs.searchList.scrollTop + this.$refs.searchList.clientHeight;

      if (
        eloffset === this.$refs.searchList.scrollTop ||
        eloffset - this.$refs.searchList.scrollTop <= 5
      ) {
        this.$refs.searchList.scrollTop = this.$refs.searchList.scrollTop - 294;
      } else if (
        eloffset < this.$refs.searchList.scrollTop ||
        eloffset > viewPort
      ) {
        this.$refs.searchList.scrollTop = eloffset - 49;
      }
    },
    enterSearch: function () {
      if (this.visible) {
        this.selectItem();
        return;
      }

      if (!this.enabledEnterSearch === true) {
        return;
      }

      this.searchText(this.buscar);
    },
    searchText: function (newQuery) {
      if (
        newQuery === "" ||
        this.selected === true ||
        newQuery.length <= this.minCarLength
      )
        return;

      var obj = this;

      clearTimeout(this.timeoutId);
      this.timeoutId = setTimeout(function () {
        servicio.buscar(obj.dataSource + newQuery).then((response) => {
          if (response.data !== undefined) {
            obj.items = response.data;
            obj.visible = true;
          } else {
            obj.items = [];
          }
        });
      }, 500);
    },
    selectItem: function (item) {
   
      if (item === undefined) {
        if (this.items.length > 0) 
          item = this.items[this.currentItem];
         else 
          return;
        
      }

      this.visible = false;
      this.selected = true;
      this.query = item.descripcion;
      this.buscar = item.descripcion;
      this.disabled = true;
      this.selectedItemInterno = item;
      this.valueInterno = this.buscar;
      this.$emit("input", this.valueInterno);
      this.$emit("selectitem", item);
    },
    clear: function () {
      this.selectedItemInterno = null;
      this.query = "";
      this.buscar = "";
      this.items = [];
      this.disabled = false;
      this.selected = false;
      this.valueInterno = "";
      this.$refs.inputTypeahead.focus();
      this.$emit("input", this.valueInterno);
      this.$emit("selectitem", null);
    },
  },
  watch: {
    // Cuando la variable buscar cambien, se validar√° si ir por nuevos resultados a la API
    buscar(newQuery) {
      if (this.enabledEnterSearch === true) {
        this.visible = false;
        return;
      }

      this.searchText(newQuery);
    },
    //Cada que cambie el arreglo con nuevos valores, el item seleccionado ser√° el primero
    items(newItems) {
      if (newItems) {
        console.log("");
      }
      this.currentItem = 0;
    },
    //limpia el control en caso de que no se quiera cerrar con la x
    limpiarControl(newValue) {
      if (newValue) {
        this.clear();
      }
    },
  },
};
</script>
<style>
li {
  cursor: pointer;
}
li:hover {
  background-color: rgb(149, 199, 255);
  color: white;
}
</style>