import servicioNotificacion from '@/services/servicio-notificacion';

const notificacion = {
    methods:{
        obtenerNotificaciones(){
            return servicioNotificacion.obtenerNotificaciones()
            .then(notificaciones=> {                       
                this.$emit("actualizarNotificaciones",notificaciones)                
                return notificaciones;
            })
            .catch(e=> {
                return e;
            })
        }
    }
}

export default notificacion;