<template>
  <div id="app">
    <div class="container">
      <div>
        <h3><i class="fas fa-users"></i> Clientes</h3>
      </div>
      
      <table-sortable
        :items="items"
        :fields="fields"
      ></table-sortable>
    </div>
  </div>
</template>

<script>
import TableSortable from "@/components/TableSortable";
import servicioClientes from "@/services/servicio-clientes";
export default {
  data() {
    return {
      items: [],
      fields: [
        {
          key: "nombreComercial",
          label: "Nombre",
          sortable: true,
          sortDirection: "desc",
        },
        {
          key: "creditoFormat",
          label: "Cr√©dito",
          sortable: true,
          class: "text-center",
        },
        {
          key: "fechaCorta",
          label: "Registro",
          sortable: true,
          class: "text-center",
        },
        
        { key: "estatusCliente", label: "Estatus", class: "text-center" },
        { key: "actionsClientes", label: "", class: "text-center" },
        
      ],
    };
  },
  components: {
    TableSortable,
  },
  mounted() {
    // if (this.verificaTokenValido()) {
    //   this.verificaPermiso("catalogos/clientes");
    //   this.consultarClientes();
    // }
      this.consultarClientes();

  },
  methods: {
    consultarClientes() {
      this.$loading(true);
      servicioClientes
        .obtenerClientesCobranza()
        .then((r) => {
          console.log(r)
          this.items = r.data.cliente;
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
          this.$loading(false);
        });
    },
  },
};
</script>
