<template>

<div class="container mt-3">
    <div class="mt-3">
        <h3><i class="fas fa-truck"></i> Operadores</h3>
    </div>

    <div>
        <table-sortable class="mt-3"
            :items="items"
            :fields="fields"
            :rutaNuevo="rutaNuevo"
            :permisoEscritura="permisoEscritura"
        ></table-sortable>
    </div>
</div>

</template>

<script>

import TableSortable from "@/components/TableSortable";
import servicioOperadores from '@/services/servicio-operadores';

export default {
    data(){
        return{
            items: [],
            fields: [
                { key: "nombreCompleto", label: "Nombre", sortable: true, sortDirection: "desc", },
                { key: "tipoEmpleado", label: "Tipo", sortable: true, sortDirection: "desc", },
                { key: "celular", label: "Celular", sortable: true, sortDirection: "desc", },
                { key: "estatus", label: "Estatus", sortable: true, sortDirection: "desc", },
                { key: "actions", label: "", class: "text-center" },
            ],
            permisoEscritura: true,
            rutaNuevo: "operadores/registro/",
        }
    },
    components: {
        TableSortable,
    },
    mounted() {
        this.ObtenerOperadores();
    },
    created() {
    },
    computed: {
    },
    methods: {

        async ObtenerOperadores() {
            this.$loading(true);
            await servicioOperadores
                    .obtenerOperadores(0)
                    .then((r) => {
                        this.items = r.data;
                        console.log('R->', this.items);
                        this.$loading(false);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
        },

        
    }
}
</script>

