<template>
  <div class="container">
    <div class="row">
      <div class="col-12">
        <h3><i class="fas fa-clipboard"></i> Notas de Remisi√≥n</h3>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-12 col-sm-6 col-md-3">
        <label>Folio Command B.</label>
        <input
          type="text"
          class="form-control"
          :disabled="true"
          v-model="notaRemision.folioGinco"
        />
      </div>
      <div class="col-12 col-sm-6 col-md-4">
        <label>Folio Nota Remisi√≥n</label>
        <input
          type="text"
          class="form-control"
          :disabled="true"
          v-model="notaRemision.folio"
        />
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-12 col-sm-4 col-md-3">
        <label>Folio del Pedido</label>
        <div class="input-group">
          <input
            v-selectonfocus
            type="text"
            class="form-control"
            v-on:keyup.enter="buscaFolioPedido"
            v-model="notaRemision.folioPedido"
            v-cleave="{
              numeral: true,
              numeralPositiveOnly: true,
              numeralDecimalScale: 0,
              numeralDecimalMark: '',
              delimiter: '',
            }"
          />
          <span class="input-group-btn">
            <button
              :disabled="disabledlimpiarProductos"
              class="input-group-4 btn btn-outline-primary"
              @click="mostrarBuscadorPedidos"
            >
              <i class="fa fa-search" aria-hidden="true"></i>
            </button>
          </span>
        </div>
      </div>
      <div class="col-12 col-sm-4 col-md-4">
        <label>Hora Salida</label>
        <input
          class="form-control"
          type="time"
          :disabled="sinPedido"
          v-model="notaRemision.horaSalida"
        />
      </div>

      <div class="col-12 col-sm-4 col-md-3">
        <label>Cantidad</label>
        <div class="input-group">
          <input
            v-selectonfocus
            class="form-control"
            :disabled="disabledCantidad"
            id="cantidad"
            ref="refCantidad"
            type="text"
            v-on:keyup.enter="verificarCantidad"
            v-model="notaRemision.cantidad"
            v-bind:class="{ hasError: hasErrorCantidad }"
            @keypress="onlyForCurrencyCant"
            precision="2"
          />
          <span class="input-group-text">m<sup>3</sup></span>
        </div>
      </div>

      <div class="col-12 col-md-2">
        <label for=""></label>
        <div class="input-group mt-2">
          <button
            :disabled="sinPedido"
            class="btn btn-primary col-12 d-lg-none"
            @click="habilitarCantidad"
          >
            <i class="fa fa-edit" aria-hidden="true"></i>
          </button>
          <button
            :disabled="sinPedido"
            class="btn btn-primary d-none d-lg-block"
            @click="habilitarCantidad"
          >
            <i class="fa fa-edit" aria-hidden="true"></i>
            Modificar
          </button>
        </div>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-12 col-sm-12 col-md-10">
        <label>F√≥rmula</label>
        <div class="input-group">
          <input
            ref="refFormula"
            type="text"
            class="form-control"
            autocomplete="off"
            v-model="state"
            :disabled="disabledFormula"
            @input="filterState"
            @focus="focusInputFormula"
            v-bind:class="{ hasError: hasErrorProducto }"
          />
          <span class="input-group-btn">
            <button
              :disabled="disabledFormula"
              class="btn btn-outline-primary input-group-4"
              @click="limpiarProducto"
            >
              <i class="fa fa-times" aria-hidden="true"></i>
            </button>
          </span>
        </div>
      </div>
      <div class="col-12 col-md-2">
        <label for=""></label>
        <div class="input-group">
          <button
            :disabled="sinPedido"
            class="btn btn-primary col-12 d-lg-none mt-2"
            @click="habilitarFormula"
          >
            <i class="fa fa-edit" aria-hidden="true"></i>
          </button>
          <button
            :disabled="sinPedido"
            class="btn btn-primary d-none d-lg-block mt-2"
            @click="habilitarFormula"
          >
            <i class="fa fa-edit" aria-hidden="true"></i>
            Modificar
          </button>
        </div>
      </div>
    </div>
    <div class="row">
      <div v-if="formulasFiltradas && modal">
        <ul class="list-group items-body">
          <li
            class="list-group-item py-2 items-body-content"
            v-for="formula in formulasFiltradas"
            v-bind:key="formula.value"
            @click="setState(formula)"
          >
            <span> {{ formula.text }}</span>
            <i class="fa fa-angle-right"></i>
          </li>
        </ul>
      </div>
    </div>

    <transition name="fade">
      <label v-show="mostrarProducto">
        <span class="labelSize">Nomenclatura: </span>
        {{ notaRemision.producto }}</label
      >
    </transition>

    <div class="row mt-3">
      <div class="col-12 col-sm-6 col-md-4">
        <div class="custom-control custom-switch">
          <input
            v-model="notaRemision.chKBombeable"
            type="checkbox"
            class="custom-control-input"
            id="bombeable"
            :disabled="true"
          />
          <label class="custom-control-label" for="bombeable"
            >ES BOMBEABLE</label
          >
        </div>
      </div>
      <div class="col-6 col-sm-3 offset-md-3">
        <div class="custom-control custom-switch">
          <input
            v-model="notaRemision.chKFibra"
            type="checkbox"
            class="custom-control-input"
            id="fibra"
            :disabled="sinPedido"
          />
          <label class="custom-control-label" for="fibra">FIBRA</label>
        </div>
      </div>

      <div class="col-6 col-sm-3 col-md-2">
        <div class="custom-control custom-switch">
          <input
            v-model="notaRemision.chKImper"
            type="checkbox"
            class="custom-control-input"
            id="imper"
            :disabled="sinPedido"
          />
          <label class="custom-control-label" for="imper">IMPER</label>
        </div>
      </div>
    </div>

    <div class="row mt-4">
      <div class="col-12 col-sm-8 offset-md-6 col-md-4">
        <div class="custom-control custom-switch">
          <input
            type="checkbox"
            class="custom-control-input"
            id="parametroEspecial"
            v-model="mostrarParametroEspeciales"
            :disabled="sinPedido"
          />

          <label class="custom-control-label" for="parametroEspecial"
            >PARAMETROS ESPECIALES</label
          >
          <i
            class="fas fa-circle fa-xs point"
            v-if="
              notaRemision.parametrosEspeciales.iva > 0 ||
              notaRemision.parametrosEspeciales.reubicado > 0
            "
          ></i>
        </div>
      </div>

      <div class="col-12 col-sm-4 col-md-2">
        <div class="custom-control custom-switch">
          <input
            type="checkbox"
            class="custom-control-input"
            id="foraneo"
            v-model="notaRemision.foraneo"
            :disabled="sinPedido"
          />
          <label class="custom-control-label" for="foraneo">FORANEO</label>
        </div>
      </div>
    </div>
    <div class="row mt-3" v-show="mostrarOperadores">
      <div class="col-12">
        <div class="card">
          <h5 class="card-header">Tiro Directo</h5>
          <div class="card-body">
            <div class="row">
              <div class="col-12 col-sm-12 col-md-6 mb-2">
                <div
                  class="
                    alert alert-dark
                    col-12
                    d-block d-sm-none
                    mb-0
                    mt-0
                    pb-0
                  "
                >
                  <label>Operador</label>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend d-none d-sm-block">
                    <label class="input-group-text">Operador</label>
                  </div>
                  <select
                    class="form-control"
                    :disabled="operadorCR"
                    v-model="notaRemision.codOperador_1"
                    v-on:change="hasErrorOperadorCR = false"
                    v-bind:class="{ hasError: hasErrorOperadorCR }"
                  >
                    <option
                      v-for="opcion of operador_1"
                      :value="opcion.value"
                      v-bind:key="opcion.value"
                    >
                      {{ opcion.text }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-12 col-sm-12 col-md-6">
                <div
                  class="
                    alert alert-dark
                    col-12
                    d-block d-sm-none
                    mb-0
                    mt-0
                    pb-0
                  "
                >
                  <label>Equipo</label>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend d-none d-sm-block">
                    <label class="input-group-text">Equipo</label>
                  </div>
                  <select
                    class="form-control"
                    v-model="notaRemision.codEquipo_CR"
                    :disabled="operadorCR"
                    v-on:change="hasErrorEquipoCR = false"
                    v-bind:class="{ hasError: hasErrorEquipoCR }"
                  >
                    <option
                      v-for="equipoCR of equipos_CR"
                      :value="equipoCR.value"
                      v-bind:key="equipoCR.value"
                    >
                      {{ equipoCR.text }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row mt-3" v-show="notaRemision.chKBombeable">
      <div class="col-12">
        <div class="card">
          <h5 class="card-header">Tiro Bombeado</h5>
          <div class="card-body">
            <div class="row">
              <div class="col-12 col-sm-12 col-md-6 mb-2">
                <div
                  class="
                    alert alert-dark
                    col-12
                    d-block d-sm-none
                    mb-0
                    mt-0
                    pb-0
                  "
                >
                  <label>Operador</label>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend d-none d-sm-block">
                    <label class="input-group-text">Operador</label>
                  </div>
                  <select
                    class="form-control"
                    v-model="notaRemision.codOperador_2"
                    :options="operador_2"
                    v-bind:class="{ hasError: hasErrorOperadorB }"
                  >
                    <option
                      v-for="opcion of operador_2"
                      :value="opcion.value"
                      v-bind:key="opcion.value"
                    >
                      {{ opcion.text }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-12 col-sm-12 col-md-6">
                <div
                  class="
                    alert alert-dark
                    col-12
                    d-block d-sm-none
                    mb-0
                    mt-0
                    pb-0
                  "
                >
                  <label>Equipo</label>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend d-none d-sm-block">
                    <label class="input-group-text">Equipo</label>
                  </div>
                  <select
                    class="form-control"
                    v-model="notaRemision.codEquipo_BB"
                    v-on:change="hasErrorEquipoB = false"
                    v-bind:class="{ hasError: hasErrorEquipoB }"
                  >
                    <option
                      v-for="equipoBB of equipos_BB"
                      :value="equipoBB.value"
                      v-bind:key="equipoBB.value"
                    >
                      {{ equipoBB.text }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <transition name="fade">
      <div class="row mt-3" v-if="mostrarParametroEspeciales">
        <div class="col-12">
          <div class="card">
            <h5 class="card-header">Parametros Especiales</h5>
            <div class="card-body">
              <div class="row mt-4">
                <div class="col-12 col-sm-3 col-md-2 text-right pt-2">
                  <label>Volumen Reubicado: </label>
                </div>
                <div class="col-12 col-sm-3 col-md-4">
                  <div class="input-group">
                    <input
                      v-selectonfocus
                      class="form-control"
                      type="text"
                      v-model="notaRemision.parametrosEspeciales.reubicado"
                      @keypress="onlyForCurrencyReubicado"
                      precision="2"
                    />
                    <span class="input-group-text">m<sup>3</sup></span>
                  </div>
                </div>
                <div class="col-12 col-sm-3 offset-md-1 col-md-5">
                  <div class="input-group">
                    <div class="input-group-prepend d-none d-sm-block">
                      <label class="input-group-text">Operador</label>
                    </div>
                    <select
                      class="form-control"
                      v-model="notaRemision.codOperador_1"
                    >
                      <option
                        v-for="opcion of operador_1"
                        :value="opcion.value"
                        v-bind:key="opcion.value"
                      >
                        {{ opcion.text }}
                      </option>
                    </select>
                  </div>
                </div>
              </div>
              <div class="row mt-4">
                <div class="col-12 col-sm-3 col-md-2 text-right pt-2">
                  <label>Descuento Virtual (IVA): </label>
                </div>
                <div class="col-12 col-sm-3 col-md-4">
                  <div class="input-group">
                    <input
                      v-selectonfocus
                      class="form-control"
                      type="text"
                      v-model="notaRemision.parametrosEspeciales.iva"
                      @keypress="onlyForCurrencyIva"
                      precision="2"
                    />
                    <span class="input-group-text">m<sup>3</sup></span>
                  </div>
                </div>
                <div class="col-12 col-sm-3 offset-md-3 col-md-3">
                  <button
                    type="button"
                    class="btn btn-primary col-12 mb-1"
                    @click="aceptarParametrosEspeciales"
                  >
                    Aceptar
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <div class="row mt-5">
      <div class="col-12 offset-md-8 col-md-4 offset-lg-9 col-lg-3">
        <button
          type="button"
          class="btn btn-primary col-12 mb-1"
          @click="addNotaRemison"
          :disabled="sinPedido"
        >
          <i class="fa fa-plus"></i> A√±adir Nota
        </button>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-12 offset-md-4 col-md-4 offset-lg-6 col-lg-3">
        <button type="button" class="btn btn-danger col-12" @click="cancelar">
          Cancelar
        </button>
      </div>
      <div class="col-12 col-md-4 col-lg-3">
        <button
          type="button"
          class="btn btn-success col-12"
          @click="guardar"
          :disabled="sinPedido"
        >
          <i class="fa fa-save"></i> Guardar
        </button>
      </div>
    </div>

    <div v-show="notasRelacionadas != ''">
      <div
        style="margin-left: 15px; max-height: 240px; overflow-y: auto"
        class="table-responsive table-sm my-2"
      >
        <table
          class="tabletipoproducto table-striped table-bordered"
          style="margin-top: 10px"
        >
          <thead>
            <tr>
              <th>Folio Pedido</th>
              <th>Vendedor</th>
              <th>Cliente</th>
              <th>Domicilio</th>
              <th>Cantidad</th>
              <th style="text-align: center">Modificar</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in notasRelacionadas" :key="index">
              <td v-text="item.folioPedido"></td>
              <td v-text="item.vendedor"></td>
              <td v-text="item.cliente"></td>
              <td v-text="item.domicilio"></td>
              <td>
                <input
                  :disabled="modificarCantidadNT"
                  v-text="item.cantidad"
                  v-model="item.cantidad"
                  v-selectonfocus
                  style="width: 100px; text-align: right"
                  maxlength="9"
                  class="form-control form-control-sm"
                />
              </td>
              <td
                v-if="modificarCantidadNT"
                class="d-none d-md-table-cell py-2 td-center text-center"
                @click="modificarCantidadNT = false"
              >
                <span class="icon-edit">
                  <i class="fa fa-edit" aria-hidden="true"></i>
                </span>
              </td>
              <td
                v-if="!modificarCantidadNT"
                class="d-none d-md-table-cell py-2 td-center text-center"
                @click="modificarCantidad(item)"
              >
                <span class="icon-edit">
                  <i class="fas fa-check"></i>
                </span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <b-modal
      v-model="mostrarPdf"
      centered
      size="lg"
      hide-header
      hide-footer
      hide-header-close
    >
      <div class="container pt-0 mb-1">
        <div class="row justify-content-end">
          <div class="col-3 text-right pr-0">
            <button
              class="btn btn-sm btn-secondary ml-2"
              data-toggle="tooltip"
              data-placement="top"
              title="Abrir"
              @click="abrirPdfNotaRemision"
            >
              <i class="fas fa-file"></i>
            </button>
            <button
              class="btn btn-sm btn-secondary ml-2"
              data-toggle="tooltip"
              data-placement="top"
              title="Descargar"
              @click="descargarPdfNotaRemision"
            >
              <i class="fas fa-file-download"></i>
            </button>
          </div>
        </div>
      </div>
      <vue-pdf-app
        style="height: 100vh"
        :pdf="pdf"
        :config="config"
      ></vue-pdf-app>
    </b-modal>

    <b-modal v-model="modalShow" centered  hide-footer>
      <buscadorPedidos @seleccionar="seleccionar"> </buscadorPedidos>
    </b-modal>
  </div>
</template>

<script>
import servicioDosificador from "@/services/servicio-dosificador.js";
import servicioGenerales from "@/services/servicio-general";
// import servicioPedidos from "@/services/servicio-pedidos";
import buscadorPedidos from "@/components/general/BuscadorPedidos";
import Swal from "sweetalert2";
import Cleave from "cleave.js";

export default {
  name: "NotaRemisionRegistro",
  data() {
    return {
      modificarCantidadNT: true,
      mostrarParametroEspeciales: false,
      config: {
        toolbar: false,
      },
      mostrarPdf: false,
      pdf: "",
      items: [],
      notasRelacionadas: [],
      modal: false,
      operadorCR: false,
      hasErrorProducto: false,
      hasErrorOperadorCR: false,
      hasErrorEquipoCR: false,
      hasErrorOperadorB: false,
      hasErrorEquipoB: false,
      hasErrorCantidad: false,
      disabledCantidad: true,
      disabledlimpiarProductos: false,
      disabledProductos: false,
      disabledFormula: true,
      nuevaFormula: false,
      sinPedido: true,
      mostrarOperadores: false,
      mostrarProducto: false,
      modalShow: false,
      state: "",
      pedidoBuscar: "",
      formulasFiltradas: [],
      pedidosFiltrados: [],
      notasRemisionEspecial: [],
      pedidos: [],
      NotaRemisionPadre: 0,
      formulas: [],
      cantidadNotasConPedido: 0,
      cantidadPedido: 0,
      sumaCantidades: 0,
      cantidadMaxima: 0,
      pedido: [],
      notaRemisionSeleccionada: [],
      notaRemision: {
        idNotasRemisionEnc: 0,
        folio: 0,
        folioGinco: 0,
        folioPadre: 0,
        horaSalida: "12:00",
        folioPedido: "",
        codProducto: 0,
        codOperador_1: 0,
        codOperador_2: 0,
        codEquipo_CR: 0,
        codEquipo_BB: 0,
        codEquipo: 0,
        cantidad: 0,
        cantidadRestantePedido: 0,
        chKBombeable: false,
        chKFibra: false,
        chKImper: false,
        producto: "",
        foraneo: false,
        parametrosEspeciales: {
          codOperador: 0,
          iva: 0,
          reubicado: 0,
        },
      },
      notaEspecial: false,
      formulaNueva: "",
      listaOperadores: [],
      productos: [{ value: 0, text: "Seleccione la formula del producto" }],
      operador_1: [{ value: 0, text: "Seleccione el operador" }],
      operador_2: [{ value: 0, text: "Seleccione el operador" }],
      equipos_CR: [{ value: 0, text: "Seleccione el equipo" }],
      equipos_BB: [{ value: 0, text: "Seleccione el equipo" }],
    };
  },
  directives: {
    cleave: {
      inserted: (el, binding) => {
        el.cleave = new Cleave(el, binding.value || {});
      },
      update: (el) => {
        const event = new Event("input", { bubbles: true });
        setTimeout(function () {
          //el.value = el.cleave.properties.result
          el.dispatchEvent(event);
        }, 100);
      },
    },
  },
  components: { buscadorPedidos },
  async mounted() {
    this.obtenerUltimoFolioGinco();
    this.obtenerUltimoFolioNotaRemision();
    this.obtenerParametro();
    await this.cargarFormulas(0);
    // await this.obtenerPedidos();
    let hora = `${new Date().getHours() + 1}`;

    var horaActual = hora == 24 ? `${hora - 1}:00` : `${hora}:00`;
    this.notaRemision.horaSalida = horaActual;

    if (this.notaRemision.folioPedido > 0) {
      this.buscaFolioPedido();
    }
  },

  async created() {
    this.notaRemision.folioPedido =
      this.$route.params.id != undefined ? this.$route.params.id : 0;
  },
  watch: {
    state: function (val) {
      val.length >= 1 && this.notaRemision.codProducto == 0
        ? (this.modal = true)
        : (this.modal = false);

      if (val.length === 0) {
        this.notaRemision.producto = 0;
        this.notaRemision.codProducto = 0;
        this.mostrarProducto = false;
      }
    },
  },
  computed: {
    filterStatePedidos: function () {
      return this.pedidos.filter((item) => {
        const folio = item.folioPedido.toString();
        const vendedor = item.vendedor.toString().toUpperCase();
        const cliente = item.cliente.toString().toUpperCase();

        const searchTerm = this.pedidoBuscar.trim().toUpperCase();

        return (
          folio.includes(searchTerm) ||
          vendedor.includes(searchTerm) ||
          cliente.includes(searchTerm)
        );
      });
    },
  },
  methods: {
    seleccionar(item) {
      this.modalShow = false;
      this.notaRemision.folioPedido = item.folioPedido;
      if (this.notaRemision.folioPedido > 0) {
        this.buscaFolioPedido();
      }
    },
    onlyForCurrencyCant($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (
        (keyCode < 48 || keyCode > 57) &&
        (keyCode !== 46 || this.notaRemision.cantidad.indexOf(".") != -1)
      ) {
        $event.preventDefault();
      }
    },
    onlyForCurrencyReubicado($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (
        (keyCode < 48 || keyCode > 57) &&
        (keyCode !== 46 ||
          this.notaRemision.parametrosEspeciales.reubicado.indexOf(".") != -1)
      ) {
        $event.preventDefault();
      }
    },
    onlyForCurrencyIva($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (
        (keyCode < 48 || keyCode > 57) &&
        (keyCode !== 46 ||
          this.notaRemision.parametrosEspeciales.iva.indexOf(".") != -1)
      ) {
        $event.preventDefault();
      }
    },
    async modificarCantidad(nota) {
      let data = {
        idNotasRemisionEnc: nota.idNotasRemisionEnc,
        cantidad: nota.cantidad,
        HoraSalida: "12:00",
        parametrosEspeciales: {
          reubicado: 0,
          iva: 0,
          codOperador: 0,
        },
      };
      let r = await servicioDosificador.agregarNotaRemisionEspecial(data);

      if (r.value) {
        this.modificarCantidadNT = true;
        this.$swal.fire({
          text: r.message,
          icon: "info",
        });
      } else {
        this.$swal.fire({
          text: r.message,
          icon: "warning",
        });
      }
    },
    focusMyElement() {
      // this.$refs.focusThis.focus();
      //this.$nextTick(() => this.$refs.focusThis && this.$refs.focusThis.focus())
    },
    seleccionaPedido(pedido) {
      this.notaRemision.folioPedido = pedido.folioPedido;
      this.modalShow = false;
      this.buscaFolioPedido();
    },
    async obtenerParametro() {
      let r = await servicioGenerales.obtenerParametro("CantidadCamion");
      if (r.value) {
        this.cantidadMaxima = r.data.valor;
      }
    },
    async obtenerUltimoFolioGinco() {
      //   let loader = this.$loading.show();
      let r = await servicioDosificador.obtenerUltimoFolioGinco();
      if (r.value) {
        this.notaRemision.folioGinco = r.data[0];
      }
      //   loader.hide();
    },
    async obtenerUltimoFolioNotaRemision() {
      //   let loader = this.$loading.show();
      let r = await servicioDosificador.obtenerUltimoFolioNR();

      if (r.value) {
        this.notaRemision.folio = r.data[0];
      }
      //   loader.hide();
    },
    filterState() {
      this.formulasFiltradas = this.formulas.filter((item) => {
        const descripcion = item.text.toString().toUpperCase();
        const nomenclatura = item.producto.toString().toUpperCase();
        const searchTerm = this.state.trim().toUpperCase();
        return (
          descripcion.includes(searchTerm) || nomenclatura.includes(searchTerm)
        );
      });
    },

    async setState(formula) {
      this.formulaBombeable(formula.producto);
      this.notaRemision.codProducto = formula.value;
      this.state = formula.text;
      this.notaRemision.producto = formula.producto;
      this.modal = false;
      this.mostrarProducto = true;
      this.hasErrorProducto = false;
      this.mostrarOperadores = true;
      this.disabledFormula = true;
    },
    obtenerFormulaProducto(idProducto) {
      let formula = this.formulas.filter((c) => c.value == idProducto);
      return formula[0];
      // this.setState(formula[0]);
    },
    limpiarProducto() {
      this.notaRemision.producto = 0;
      this.notaRemision.codProducto = 0;
      this.mostrarProducto = false;
      this.disabledFormula = false;
      this.state = "";
      this.$nextTick(() => {
        this.$refs.refFormula.focus();
      });
    },
    focusInputFormula() {
      if (this.state.length > 0) {
        this.modal = true;
      }
    },
    async buscaFolioPedido() {
      if (this.notaRemision.folioPedido != "") {
        let r = await servicioDosificador.obtenerPedido(
          this.notaRemision.folioPedido
        );
        if (r.value) {
          this.pedido = r.data[0];
          let hayNotasPendientes = await this.verificarNotasPorPedido();
          await this.obtenerNotasRelacionadas();
          if (hayNotasPendientes.value) {
            this.notaRemision.cantidad =
              hayNotasPendientes.data.cantidadRecomendar;
            this.notaRemision.cantidadRestantePedido =
              hayNotasPendientes.data.cantidadRestantePedido;
            let msgCantidad = await this.$swal.fire({
              icon: "info",
              html:
                `El pedido tiene asignada una cantidad de <b> ${this.pedido.cantidad} m<sup>3</sup></b> <BR>` +
                `Quedan pendientes por capturar <b> ${this.notaRemision.cantidadRestantePedido.toFixed(
                  1
                )} m<sup>3</sup></b>`,
            });

            if (msgCantidad.value) {
              let formula = await this.obtenerFormulaProducto(
                this.pedido.idCatFormula
              );
              this.mostrarProducto = true;
              let res = await this.ObtenerOperadoresCR();
              if (res.value) {
                this.llenarComboOperadores(res.data);
                await this.obtenerEquipoCamionRevolvedor(0, false);
                this.mostrarOperadores = true;
                this.notaRemision.codProducto = formula.value;
                this.state = formula.text;
                this.notaRemision.producto = formula.producto;
                this.cantidadNotasConPedido = this.pedido.cantidad;
                this.notaRemision.codCliente = this.pedido.codCliente;
                this.notaRemision.codObra = this.pedido.codObra;
                this.notaRemision.codVendedor = this.pedido.codVendedor;
                this.notaRemision.folioPedido = this.pedido.folioPedido;
                this.sinPedido = false;
              } else {
                this.$swal.fire({
                  text: res.message,
                  icon: "info",
                });
              }
              if (
                formula.producto.substring(formula.producto.length - 1) == "B"
              ) {
                console.log("Entro aqui");
                this.notaRemision.chKBombeable = true;
                let res = await this.ObtenerOperadoresBombeable();
                if (res.value) {
                  this.llenarComboOperadoresCamionBombleable(res.data);
                  await this.obtenerEquipoBombeable(0, true);
                }
              } else {
                this.notaRemision.chKBombeable = false;
              }
            }
          } else {
            this.notasRemisionExcedidas(hayNotasPendientes.message);
          }
        } else {
          this.$swal.fire({
            text: r.message,
            icon: "info",
          });
        }
      }
    },
    async verificarNotasPorPedido() {
      return servicioDosificador.verificarPedido(this.notaRemision.folioPedido);
    },
    async notasRemisionExcedidas(mensaje) {
      this.$swal.fire({
        text: mensaje,
        icon: "info",
      });
    },
    habilitarCantidad() {
      this.disabledCantidad = false;
      this.$nextTick(() => {
        this.$refs.refCantidad.focus();
      });
    },
    habilitarFormula() {
      this.disabledFormula = false;
      this.notaRemision.producto = 0;
      this.notaRemision.codProducto = 0;
      this.mostrarProducto = false;
      this.state = "";
      this.$nextTick(() => {
        this.$refs.refFormula.focus();
      });
    },
    llenarComboOperadoresCamionBombleable(operadores) {
      this.hasErrorOperadorCR = false;
      this.operador_2 = [{ value: 0, text: "Seleccione el operador" }];
      operadores.forEach((element) => {
        let tipo = {
          value: element.codigo,
          text: element.nombreCompleto,
        };
        this.operador_2.push(tipo);
      });
    },
    cancelarNuevoProducto() {
      this.disabledProductos = false;
      this.formulaNueva = "";
      this.nuevaFormula = false;
    },

    async agregarNvaFormula() {
      let r = await Swal.fire({
        title: "¬øDesea crear una nueva f√≥rmula de Producto?",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Aceptar",
        cancelButtonText: "Cancelar",
      });

      if (r.value) {
        this.notaRemision.codProducto = 0;
        this.nuevaFormula = true;
        this.disabledProductos = true;
        this.$nextTick(() => {
          this.$refs.nvaFormula.focus();
        });
      }
    },
    async aceptarNuevaFormula() {
      //   let loader = this.$loading.show();
      await this.obtenerProducto(this.formulaNueva);
      let formula = {
        nomenclatura: this.formulaNueva,
        descripcion: this.notaRemision.producto,
      };
      await servicioDosificador.guardarFormula(formula).then((r) => {
        if (r.value && r.data != null) {
          let codFormulaNva = r.data[0];
          this.$swal.fire({
            text: r.message,
            icon: "success",
          });
          this.disabledProductos = false;
          this.cargarFormulas(0);
          this.notaRemision.codProducto = codFormulaNva;
        } else {
          this.$swal.fire({
            text: r.message,
            icon: "error",
          });
        }
        this.nuevaFormula = false;
      });
      //   loader.hide();
    },

    async cargarFormulas(codigo) {
      //   let loader = this.$loading.show();

      let r = await this.obtenerFormulas(codigo);

      if (r.value) {
        r.data.forEach((element) => {
          let tipo = {
            value: element.codigo,
            text: element.descripcion,
            producto: element.nomenclatura,
          };
          this.formulas.push(tipo);
          // this.productos.push(tipo);
        });
      }
      //   loader.hide();
    },

    async obtenerFormulas(codigo) {
      return await servicioDosificador.obtenerFormulas(codigo);
    },
    async obtenerOperadores(bombeable) {
      return await servicioDosificador.obtenerOperadores(bombeable);
    },
    async ObtenerOperadoresCR() {
      return await servicioDosificador.obtenerOperadoresCamionRevolvedor();
    },
    async ObtenerOperadoresBombeable() {
      return await servicioDosificador.obtenerOperadoresCamionBombeable();
    },
    llenarComboOperadores(operadores) {
      (this.operador_1 = [{ value: 0, text: "Seleccione el operador" }]),
        operadores.forEach((element) => {
          let tipo = {
            value: element.codigo,
            text: element.nombreCompleto,
          };
          this.operador_1.push(tipo);
        });
    },
    async obtenerEquipoCamionRevolvedor(codOperador, esBombeable) {
      this.equipos_CR = [{ value: 0, text: "Seleccione el equipo" }];
      // this.equipos_BB = [{ value: 0, text: "Seleccione el equipo" }];
      await servicioDosificador
        .obtenerEquipo(codOperador, esBombeable)
        .then((r) => {
          if (r.value) {
            console.log("1");
            r.data.forEach((element) => {
              let tipo = { value: element.codigo, text: element.identificador };
              this.equipos_CR.push(tipo);
            });
          }
        });
    },
    async obtenerEquipoBombeable(codOperador, esBombeable) {
      // this.equipos_CR = [{ value: 0, text: "Seleccione el equipo" }];
      this.equipos_BB = [{ value: 0, text: "Seleccione el equipo" }];
      await servicioDosificador
        .obtenerEquipo(codOperador, esBombeable)
        .then((r) => {
          if (r.value) {
            console.log("2");
            r.data.forEach((element) => {
              let tipo = { value: element.codigo, text: element.nombre };
              this.equipos_BB.push(tipo);
            });
          }
        });
    },

    async generarDatosFormula() {
      let r = await this.obtenerFormulas(this.notaRemision.codProducto);
      if (r.value) {
        if (r.data[0].descripcion.toString().length != 0) {
          this.notaRemision.producto = r.data[0].descripcion;
          this.mostrarProducto = true;
          let select = document.getElementById("cboProducto");
          let formula = select.options[select.selectedIndex].text;
          this.obtenerProducto(formula);
        } else {
          let select = document.getElementById("cboProducto");
          let formula = select.options[select.selectedIndex].text;
          this.obtenerProducto(formula);
        }
      }
    },
    async formulaBombeable(formula) {
      this.notaRemision.chKBombeable =
        formula.substring(formula.length - 1) == "B" ? true : false;
      let r = await this.obtenerOperadores(this.notaRemision.chKBombeable);

      if (r.value) {
        this.llenarComboOperadores(r.data);
        // await this.obtenerEquipo(0, true);
      }
    },
    obtenerProducto(formula) {
      var producto = `Concreto premezclado `;
      let arrayFormula = formula.split("-");

      let norma = arrayFormula[0];
      let resistencia = arrayFormula[1];
      let tma = arrayFormula[2];
      // let origen = arrayFormula[3];
      let edad = arrayFormula[4];
      let revestimiento = arrayFormula[5];
      let pump = arrayFormula[6];
      // let tipoCemento = arrayFormula[7];
      // let variante = arrayFormula[8];

      switch (norma) {
        case "1":
          producto += " f'c ";
          producto += resistencia + " KG/CM2 " + this.getTMA(tma);
          break;

        case "P":
          if (resistencia < 100) {
            producto += " MR-";
            producto += resistencia;
          } else {
            producto += " f'c";
            producto += resistencia + " KG/CM2";
          }
          break;
        case "L":
          producto += " f'c";
          producto += resistencia + " KG/CM2";
          break;
      }

      if (pump == 1) {
        this.notaRemision.chKBombeable = true;
        producto += " BOMBEADO ";
        this.obtenerOperadores(true);
        // this.obtenerEquipo(0, true);
      } else {
        this.notaRemision.chKBombeable = false;
        producto += " TIRO DIRECTO ";
        this.obtenerOperadores(false);
        // this.obtenerEquipo(0, false);
      }

      if (edad) producto += " Resistencia " + edad + " Dias ";
      if (revestimiento) producto += " Revenimiento " + revestimiento + "+-2";
      this.notaRemision.producto = producto;
      this.mostrarProducto = true;
    },

    getTMA(tma) {
      let valorTMA = "";
      switch (tma) {
        case "1":
          valorTMA += " T.M.A. 3/8 ";
          break;
        case "2":
          valorTMA += " T.M.A 3/4 ";
          break;
        case "4":
          valorTMA += " T.M.A 1 1/2 ";
          break;
      }
      return valorTMA;
    },
    async guardar() {
      if (!this.validarRegistros()) {
        let r = await Swal.fire({
          title: "¬øDesea guardar esta nota de remisi√≥n?",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Aceptar",
          cancelButtonText: "Cancelar",
        });

        this.notaRemision.cantidadRestantePedido =
          this.notaRemision.cantidadRestantePedido - this.notaRemision.cantidad;
        if (r.value) {
          this.$loading(true);
          let result = await servicioDosificador.guardarNotaRemision(
            this.notaRemision,
            this.parametrosEspeciales
          );
          this.$loading(false);
          if (result.value) {
            let r = await this.$swal.fire({
              text: result.message,
              icon: "success",
            });
            if (r.value) {
              await this.ImprimirNota(result.data).then(() => {
                console.log("ENTRO");
                this.inicializarDatos();
                this.obtenerUltimoFolioGinco();
                this.obtenerUltimoFolioNotaRemision();
              });
            }
          } else {
            if (result.message == "FOLIOS REPETIDOS") {
              let r = await this.$swal.fire({
                text: "Los folios ya se encuentras registrados, se actualizaran.",
                icon: "error",
              });
              if (r.value) {
                await this.obtenerUltimoFolioGinco();
                await this.obtenerUltimoFolioNotaRemision();
              }
            } else {
              this.$swal.fire({
                text: result.message,
                icon: "error",
              });
            }
          }
        } else {
          this.$swal.fire({
            text: "Hay campos requeridos por completar",
            icon: "error",
          });
        }
      }
    },
    async addNotaRemison() {
      this.notaRemision.cantidadRestantePedido =
        this.notaRemision.cantidadRestantePedido - this.notaRemision.cantidad;

      if (!this.validarRegistros()) {
        let result = await servicioDosificador.agregarNotaRemisionEspecial(
          this.notaRemision
        );

        if (result.value) {
          this.nuevaNotaRemisionEspecial(result.data);
        } else {
          this.$swal.fire({
            text: result.message,
            icon: "warning",
          });
        }
      }
    },
    async nuevaNotaRemisionEspecial(notaRemision) {
      this.operadorCR = true;
      this.notaRemision.folioPedido = "";
      this.notaRemision.cantidad = 0;
      this.notaRemision.codProducto = notaRemision.codProducto;
      this.notaRemision.folioGinco = notaRemision.folioGinco;
      this.notaRemision.folioPadre = notaRemision.folioPadre;
      this.obtenerUltimoFolioNotaRemision();
      await this.obtenerNotasRelacionadas();
    },

    verificarCantidad() {
      if (this.notaRemision.cantidad > this.cantidadMaxima) {
        this.mostrarErrorCantidad(
          `La cantidad maxima permitida por un cami√≥n revolvedor es de ${this.cantidadMaxima}m3`
        );
      } else if (
        this.notaRemision.cantidad > this.notaRemision.cantidadRestantePedido
      ) {
        this.mostrarErrorCantidad(
          `La cantidad no puede ser mayor al restante del pedido de ${this.notaRemision.cantidadRestantePedido}m3`
        );
      } else {
        this.hasErrorCantidad = false;
        this.disabledCantidad = true;
      }
    },
    async mostrarErrorCantidad(mensaje) {
      let r = await this.$swal.fire({
        text: mensaje,
        icon: "warning",
      });

      if (r.value) {
        this.hasErrorCantidad = true;
        this.$nextTick(() => {
          this.$refs.refCantidad.focus();
        });
      }
    },
    validarRegistros() {
      var erroresEncontrados = false;
      if (this.notaRemision.cantidad > this.cantidadMaxima) {
        this.hasErrorCantidad = true;
        erroresEncontrados = true;
      }
      if (this.notaRemision.codProducto === 0) {
        this.hasErrorProducto = true;
        erroresEncontrados = true;
      }
      if (this.notaRemision.codOperador_1 === 0) {
        this.hasErrorOperadorCR = true;
        erroresEncontrados = true;
      }
      if (this.notaRemision.codEquipo_CR === 0) {
        this.hasErrorEquipoCR = true;
        erroresEncontrados = true;
      }
      if (this.notaRemision.chKBombeable) {
        if (this.notaRemision.codOperador_2 === 0) {
          this.hasErrorOperadorB = true;
          erroresEncontrados = true;
        }
        if (this.notaRemision.codEquipo_BB === 0) {
          this.hasErrorEquipoB = true;
          erroresEncontrados = true;
        }
      }
      return erroresEncontrados;
    },
    cancelar() {
      this.$swal
        .fire({
          title: "¬øDesea cancelar el registro de una nota de remisi√≥n?",
          text: "",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, volver al cat√°logo",
          cancelButtonText: "Cancelar",
        })
        .then((result) => {
          if (result.value) {
            window.history.back();
          }
        });
    },

    inicializarDatos() {
      this.state = "";
      this.mostrarOperadores = false;
      let hora = `${new Date().getHours() + 1}`;
      var horaActual = hora == 24 ? `${hora - 1}:00` : `${hora}:00`;
      this.notaRemision.horaSalida = horaActual;
      this.notaRemision = {
        folio: 0,
        folioGinco: 0,
        horaSalida: "12:00",
        codCliente: 0,
        codProducto: 0,
        codObra: 0,
        codVendedor: 0,
        codOperador_1: 0,
        codOperador_2: 0,
        codEquipo_CR: 0,
        codEquipo_BB: 0,
        codEquipo: 0,
        cantidad: 0,
        chKBombeable: false,
        chKFibra: false,
        chKImper: false,
        producto: "",
        folioPedido: "",
        foraneo: false,
        parametrosEspeciales: {
          codOperador: 0,
          iva: 0,
          reubicado: 0,
        },
      };
    },

    async ImprimirNota(idNotaRemision) {
      this.$loading(true);
      let result = await servicioDosificador.obtenerPdfNotaRemision(
        idNotaRemision
      );
      if (result.value) {
        this.pdf = await getPdf(result.data);
        this.mostrarPdf = true;
      }
      this.$loading(false);
    },
    async mostrarBuscadorPedidos() {
      // await this.obtenerPedidos();
      this.modalShow = true;
    },
    // async obtenerPedidos() {
    //   this.$loading(true);
    //   let r = await servicioPedidos.consultarPedidos();
    //   if (r.value) {
    //     this.pedidos = r.data;
    //   }
    //   this.$loading(false);
    // },
    async obtenerNotasRelacionadas() {
      this.$loading(true);
      let r = await servicioDosificador.obtenerNotasEspecialRelacionadas(
        this.notaRemision.folioPadre,
        this.notaRemision.folioGinco
      );
      if (r.value) {
        this.notasRelacionadas = r.data;
        console.log("informacion" + this.notasRelacionadas);
      }
      this.$loading(false);
    },
    aceptarParametrosEspeciales() {
      console.log("Click");
      this.mostrarParametroEspeciales = false;
    },
    abrirPdfNotaRemision() {
      var base64URL = "data:application/pdf;base64," + this.pdfBase64;
      var win = window.open();
      win.document.write(
        '<iframe src="' +
          base64URL +
          '" frameborder="0" style="border:0; top:0px; left:0px; bottom:0px; right:0px; width:100%; height:100%;" allowfullscreen></iframe>'
      );
    },
    async descargarPdfNotaRemision() {
      const downloadLink = document.createElement("a");
      const fileName = "Nota de remisi√≥n " + 1;
      downloadLink.href = "data:application/pdf;base64," + this.pdfBase64;
      downloadLink.download = fileName;
      await downloadLink.click();
    },
  },
};

function getPdf(pdfBase64) {
  return base64ToArrayBuffer(pdfBase64);
}

function base64ToArrayBuffer(base64) {
  var binary_string = window.atob(base64);
  var len = binary_string.length;
  var bytes = new Uint8Array(len);
  for (var i = 0; i < len; i++) {
    bytes[i] = binary_string.charCodeAt(i);
  }
  return bytes.buffer;
}
</script>

