.ocultarAutocomplete {
  inset: 0;
  z-index: 0;
  position: absolute;
}

.autocompletar {
  z-index: 10px;
}

.autocompletable {
  cursor: pointer;
  box-shadow: 1px 1px 1px 1px rgb(65, 65, 65);
  margin-top: 2px;
}

.hasError {
  border-color: red;
}

.labelSize {
  font-size: 16px;
  text-align: center;
  font-weight: bold;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.bounce-enter-active {
  animation: bounce-in 0.4s;
}

.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}

/* $pri: #128CFC;
$sec: #fffffe;
$dark-pri: #0B5AA2; */
* {
  box-sizing: border-radius;
  font-family: "Rubik", sans-serif;
}

.items-body {
  padding: 10px;
  margin: 10px;
  display: grid;
  grid-gap: 10px;
}

.items-body-content {
  padding: 10px;
  padding-right: 0px;
  display: grid;
  grid-template-columns: 10fr 1fr;
  font-size: 13px;
  grid-gap: 12px;
  border: 1px solid transparent;
  cursor: pointer;
}

.items-body-content:hover {
  border-radius: 15px;
  border: 1px solid #0b5aa2;
  background-color: rgb(230, 225, 225);
}

.items-body-content i {
  align-self: center;
  font-size: 15px;
  color: #0b5aa2;
  font-weight: bold;
  animation: icon 1.5s infinite forwards;
}

.modalPedido {
  background-color: red;
  height: 200px;
}

@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes icon {
  0%,
  100% {
    transform: translate(0px);
  }
  50% {
    transform: translate(3px);
  }
}
