<template>
    <b-container fluid>
        <h3 class="mb-4">Clientes Detenidos</h3>
        <!-- User Interface controls -->
        <b-row class="my-2">
            <b-col md="4">
                <b-form-group label="Filtrar"
                            label-cols-sm="1"
                            label-cols-md="2"
                            label-size="sm"
                            label-for="filterInput">
                    <b-input-group size="sm">
                        <b-form-input v-model="filter"
                                        type="search"
                                        id="filterInput"
                                        placeholder="Escribe el filtro..."
                                        autocomplete="off">
                        </b-form-input>
                        <b-input-group-append>
                            <b-button :disabled="!filter" @click="filter = ''">
                                <i class="fas fa-times"></i>
                            </b-button>
                        </b-input-group-append>
                    </b-input-group>
                </b-form-group>
            </b-col>
            <b-col md="3">
                <b-form-group label="Por p√°gina"
                                label-cols-sm="2"
                                label-cols-md="2"
                                label-cols-lg="4"
                                label-size="sm"
                                label-for="perPageSelect">
                    <b-form-select v-model="perPage"
                                    id="perPageSelect"
                                    size="sm"
                                    :options="pageOptions">
                    </b-form-select>
                </b-form-group>
            </b-col>
        </b-row>
        <!-- Main table element -->
        <b-table small
                stacked="md"
                head-variant="dark"
                :hover="hover"
                :striped="striped"
                :bordered="bordered"
                :items="items"
                :fields="fields"
                :current-page="currentPage"
                :per-page="perPage"
                :filter="filter"
                :filterIncludedFields="filterOn"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :sort-direction="sortDirection"
                @filtered="onFiltered">
            <template v-slot:cell(estatus)="row">
                <b-badge variant="danger" v-if="row.item.estatusNuevo == 'DC'" class="p-2">Detenido Limite Credito</b-badge>
            </template>
        </b-table>
        
        <b-pagination v-model="currentPage"
                        class="mt-1"
                        :total-rows="totalRows"
                        :per-page="perPage"
                        align="center"
                        aria-controls="my-table">
        </b-pagination>
    </b-container>
</template>

<script>

import servicio from '@/services/servicio-clientes'

export default {
    data(){
        return {
            items: [],
            credenciales:{
                usuario: '',
                password: '',
            },
            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],
            fields: [
                {
                    key: "nombreCompleto",
                    label: "Nombre",
                    sortable: true,
                    sortDirection: "desc",
                },
                {
                    key: "celular",
                    label: "Celular",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "correo",
                    label: "Correo",
                    sortable: true,
                    class: "text-center",
                },
                { key: "estatus", label: "Estatus", class: "text-center" },

            ],
        }
    },
    components: {
    },
    computed: {
        sortOptions() {
        // Create an options list from our fields
        return this.fields
            .filter((f) => f.sortable)
            .map((f) => {
            return { text: f.label, value: f.key };
            });
        },
    },
  async mounted() {
        await this.ObtenerClientesDetenidos();
        await this.CargarRenglones();
  },
  methods: {
    
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },

    async ObtenerClientesDetenidos () {
        this.items = [];
        this.$loading(true);
        await servicio.obtenerClientesDetenidos()
                        .then(response => {
                            this.$loading(false);
                            console.log(response);
                            if(response.value){
                                this.items = response.data;
                            }
                        })
                        .catch(error => {
                            this.$loading(false);
                            console.log(error)
                        })
    },

    CargarRenglones() {
        console.log("---->" + this.items.length);
        setTimeout(() => {
        this.totalRows = this.items.length;
        }, 1000);
    }
  
  },

}
</script>





