<template>
    <div>
        <div class="container mt-3">
            
            <div class="mt-3">
                <h3><i class="fas fa-gas-pump"></i> Registro</h3>
            </div>

            <div class="row mt-3">
                <div class="form-group col-sm-12 col-md-6 mb-2">
                    <label>Equipo</label>
                    <div class="input-group">
                        <select v-model="equipoSeleccionado"
                                class="form-control">
                            <option v-for="equipo in Equipos " 
                                    v-bind:key="equipo.codigo"
                                    v-bind:value="equipo">  {{equipo.nombre}}
                            </option>
                        </select>
                    </div>
                </div>
                
                <div class="form-group col-sm-12 col-md-6">
                    <label>Operador</label>
                    <div class="input-group">
                        <select v-model="operadorSeleccionado"
                                class="form-control">
                            <option v-for="operador in Operadores " 
                                    v-bind:key="operador.codigo"
                                    v-bind:value="operador">  {{operador.nombreCompleto}}
                            </option>
                        </select>
                        
                    </div>
                </div>
            </div>

            <!-- diesel -->
            <div class="form-group col-sm-12 col-md-6">
                <label>Diesel</label>
                <div class="row">
                    <input v-model="suministros.diesel"
                            v-selectonfocus
                            @focus="onFocus(1)"
                            @keypress="numerosDecimales"
                            type="text"
                            class="form-control"
                            placeholder="Capture el nivel de diesel de la unidad."/>
                </div>
            </div>

            <!-- anticongelante -->
            <div class="form-group col-sm-12 col-md-6">
                <label>Anticongelante</label>
                <div class="row">
                    <input v-model="suministros.anticongelante"
                            v-selectonfocus
                            @focus="onFocus(2)"
                            @keypress="numerosDecimales"
                            type="text"
                            class="form-control"
                            placeholder="Capture el nivel de anticongelante de la unidad."/>
                </div>
            </div>
            
            <!-- aceite -->
            <div class="form-group col-sm-12 col-md-6">
                <label>Aceite</label>
                <div class="row">
                    <input v-model="suministros.aceite"
                            v-selectonfocus
                            @focus="onFocus(3)"
                            @keypress="numerosDecimales"
                            type="text"
                            class="form-control"
                            placeholder="Capture el nivel de aceite de la unidad."/>
                </div>
            </div>

            <!-- botones -->
            <b-row class="mt-5">
                <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                    <b-button @click="GuardarSuministros" block variant="success" class="mr-2 mt-2">
                        <i class="fa fa-save"></i> Guardar
                    </b-button>
                </b-col>
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                    <b-button @click="CancelarEntradaSalida" block variant="danger" class="mr-2 mt-2">
                        Cancelar
                    </b-button>
                </b-col>
            </b-row>
        </div>
    </div>
</template>

<script>

import Swal from 'sweetalert2'
// import TableSortable from "@/components/TableSortable";

import servicioEquipos from '@/services/servicio-equipos';
import servicioOperadores from '@/services/servicio-operadores';
import servicioPorteros from '@/services/servicio-porteros';


export default {
    data(){

        const now = new Date()
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
        var todayDate = today.toISOString().slice(0,10);

        return{

            Equipos: [],
            equipoSeleccionado: [],
            Operadores: [],
            operadorSeleccionado: [],

            historial: true,
            desde: todayDate,
            hasta: todayDate,
            // items: [],
            // fields: [
            //     { key: "equipo", label: "Equipo", sortable: true, sortDirection: "desc", },
            //     { key: "operador", label: "Operador", sortable: true, sortDirection: "desc", },
            //     { key: "usuario", label: "Usuario", sortable: true, class: "text-center", },
            //     { key: "fecha", label: "Fecha", sortable: true, class: "text-center" },
            //     { key: "action", label: "", class: "text-center" },
            // ],
            inputSeleccionado: 0,

            suministros: {
                diesel: 0,
                anticongelante: 0,
                aceite: 0,
                codEquipo: 0,
                CodOperador: 0,
            },
            parametro: 0,
        }
    },
    components: {
        // TableSortable
    },
    mounted() {
        // this.verificaPermiso("suministros");
        this.ObtenerEquipos();
        this.ObtenerOperadores();
    },
    created() {
        
        this.parametro = this.$route.params.id;
        console.log('P->', this.parametro);
    },
    computed: {
    },
    methods: {

        Inicializar(){
            this.historial = true;
            this.ObtenerEquipos();
            this.ObtenerOperadores();
            // this.ObtenerSuministros();
            this.suministros = {
                diesel: 0,
                anticongelante: 0,
                aceite: 0,
                codEquipo: 0,
                codOperador: 0,
            };
        },

        async ObtenerEquipos() {
            this.Equipos = [];

            this.$loading(true);
            await servicioEquipos.equipos(0)
                .then(r=>{
                    this.Equipos = r.data;
                    this.equipoSeleccionado = this.Equipos[0];
                    this.$loading(false);
                }).catch(error=>{console.log(error); this.$loading(false);});
        },

        async ObtenerOperadores() {
            this.Operadores = [];

            this.$loading(true);
                await servicioOperadores.obtenerOperadores(0)
                    .then(r=>{
                        console.log(r);
                        this.Operadores = r.data;
                        this.operadorSeleccionado = this.Operadores[0];
                        this.$loading(false);
                    }).catch(error=>{console.log(error); this.$loading(false);});
        },

        // async ObtenerSuministros() {
        //     this.$loading(true);
        //     await servicioPorteros
        //             .obtenerSuministros(this.desde, this.hasta)
        //             .then((r) => {
        //                 this.items = r.data;
        //                 this.$loading(false);
        //             })
        //             .catch((error) => {
        //                 console.log(error);
        //             });
        // },

        async GuardarSuministros() {

            let r = await Swal.fire({
                title: "¬°ATENCI√ìN!",
                text: "¬øDesea guardar los niveles de suministros de la unidad?",
                showCancelButton: true,
                icon: 'question',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Aceptar",
                cancelButtonText: "Cancelar",
            });

            if (r.value) {
                if(this.equipoSeleccionado.codigo > 0) {
                    if(this.operadorSeleccionado.codigo > 0) {

                        if(this.suministros.diesel == 0 && this.suministros.anticongelante == 0 && this.suministros.aceite == 0) {
                            Swal.fire(
                                '¬°ATENCI√ìN!',
                                'Favor de ingresar correctamente los datos.',
                                'info'
                            );
                        } else {

                            await this.ValidacionDeDatos();

                            this.$loading(true);
                            servicioPorteros.guardarSuministros(this.suministros)
                                .then(r=>{
                                    if(r.value) {
                                        Swal.fire(
                                            '¬°EXITO!',
                                            'Se guardo correctamente los suministros',
                                            'success'
                                        );
                                        this.$router.push({ path: "/portero/suministros" })

                                        // this.Inicializar();
                                    } else {
                                        Swal.fire(
                                            '¬°ATENCI√ìN!',
                                            r.message,
                                            'error'
                                        );
                                    }
                                    this.$loading(false);
                                })
                                .catch(error=>{
                                    console.log(error);
                                    this.$loading(false);
                                });
                        }
                    } else {
                        this.$swal.fire({
                            text: `Favor de seleccionar un equipo`,
                            icon: "warning"
                        });

                    }
                } else {
                    this.$swal.fire({
                        text: `Favor de seleccionar un operador`,
                        icon: "warning"
                    });
                }
            }
        },

        async CancelarEntradaSalida() {
            await this.Inicializar();
        },



        // buscar() {
        //     this.ObtenerSuministros();
        // },

        // Nuevo() {
        //     this.historial = false;
        // },

        numerosDecimales ($event) {
            let keyCode = ($event.keyCode ? $event.keyCode : $event.which);

            if(this.inputSeleccionado == 1) {
                if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || this.suministros.diesel.indexOf('.') != -1)) { // 46 is dot
                    $event.preventDefault();
                }
            } else if(this.inputSeleccionado == 2) {
                if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || this.suministros.anticongelante.indexOf('.') != -1)) { // 46 is dot
                    $event.preventDefault();
                }
            } else {
                if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || this.suministros.aceite.indexOf('.') != -1)) { // 46 is dot
                    $event.preventDefault();
                }
            }

        },

        onFocus: function (index){
            this.inputSeleccionado = index;
        },


        ValidacionDeDatos() {
            if(this.suministros.diesel == ""){
                this.suministros.diesel = 0;
            }

            if(this.suministros.anticongelante == ""){
                this.suministros.anticongelante = 0;
            }

            if(this.suministros.aceite == ""){
                this.suministros.aceite = 0;
            }

            this.suministros.codEquipo = this.equipoSeleccionado.codigo;
            this.suministros.codOperador = this.operadorSeleccionado.codigo;
        }

        
    }
}
</script>

