const selectOnFocus = {}

const selectText = function(event){
    event.preventDefault()    
    event.target.select()
}

selectOnFocus.install = function(Vue){
    Vue.directive('selectonfocus', {        
        inserted(el){
            el.addEventListener('focus', selectText)
            el.addEventListener('click', selectText)
        }
    })
}

export default selectOnFocus