
import base from '@/services/servicio-base'

const servicio = {}

servicio.obtenerUltimoFolioGinco = ()=>{    
    return base.get(`/dosificador/ultimo-folio-ginco`)
        .then( r => {
            return r.data;
        })
        .catch( error => {
            return error;
        })    
}

