servicio.guardarNotaRemision= (notaRemision, parametrosEspeciales) => {
    return base.post("/dosificador/notaRemision/guardar", notaRemision,parametrosEspeciales,{
        headers: {
            'Content-Type': 'application/json'
        }
        })
        .then(r => {
            return r.data
        })
        .catch(e => {
            console.log(e);
        })
}

