<template>
    <div>
        <div class="container mt-3">

            <div class="mt-3">
                <h3><i class="fas fa-truck-moving"></i> Entradas y Salidas</h3>
            </div>

            <!-- botones -->
            <b-row>
                <b-col class="col-md-6 col-sm-12 col-12" align-self="end"/>
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                    <b-button @click="SalidaNueva" block variant="danger" class="mr-2 mt-2">
                        <i class="fa fa-plus"></i> Nueva Salida
                    </b-button>
                </b-col>
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                    <b-button @click="EntradaNueva" block variant="primary" class="mr-2 mt-2">
                        <i class="fa fa-plus"></i> Nueva Entrada
                    </b-button>
                </b-col>
            </b-row>

            <b-row>
                <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                    <label>Desde:</label>
                    <b-form-datepicker v-model="desde" class="mb-2" locale="es"></b-form-datepicker>
                </div>
                <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                    <label>Hasta:</label>
                    <b-form-datepicker v-model="hasta" class="mb-2" locale="es"
                        ></b-form-datepicker>
                </div>
            </b-row>
            <b-row>
                <div class="col-sm-12 col-md-3 col-lg-2 mb-1">
                    <b-button @click="buscar" block variant="success" class="mr-2 mt-2">
                        <i class="fa fa-search"></i> Buscar
                    </b-button>
                </div>

            </b-row>
            
            
            <div>
                <table-sortable class="mt-3"
                    :items="items"
                    :fields="fields"
                ></table-sortable>
            </div>
        </div>
    </div>
</template>

<script>

import servicioPorteros from '@/services/servicio-porteros';
import TableSortable from "@/components/TableSortable";

export default {
    data(){

        const now = new Date()
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
        var todayDate = today.toISOString().slice(0,10);

        return{
            desde: todayDate,
            hasta: todayDate,
            items: [],
            fields: [
                { key: "fecha", label: "Fecha", sortable: true, class: "text-center" },
                { key: "folioPedido", label: "# P.", sortable: true, class: "text-center" },
                { key: "notaRemision", label: "Nota de Remisi√≥n", sortable: true, class: "text-center", },
                { key: "equipo", label: "Equipo", sortable: true, sortDirection: "desc", },
                { key: "operador", label: "Operador", sortable: true, sortDirection: "desc", },
                { key: "usuario", label: "Usuario", sortable: true, class: "text-center", },
                { key: "entradasalida", label: "Tipo", class: "text-center" },
                { key: "action", label: "", class: "text-center" },
            ],
        }
    },
    components: {
        TableSortable,
    },
    mounted() {
        this.ObtenerEntradasSalidas();
    },
    
    created() {
        
    },
    
    computed: {
    },
    
    methods: {

        Inicializar(){
            const now = new Date()
            const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
            var todayDate = today.toISOString().slice(0,10);
            this.desde = todayDate;
            this.hasta = todayDate;
            this.items = [];
            this.ObtenerEntradasSalidas();
        },

        async ObtenerEntradasSalidas() {
            this.$loading(true);
            await servicioPorteros
                    .obtenerEntradasSalidas(this.desde, this.hasta)
                    .then((r) => {
                        this.items = r.data;
                        // this.items.forEach((element) => {
                        //     if (element.entrada != null && element.entrada != undefined) {
                        //         element._rowVariant = element.entrada ? "success" : "danger";
                        //     }
                        // });
                        this.$loading(false);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
        },

        EntradaNueva() {
            this.$router.push({ path: "/portero/entradas-salidas/registro/1" })
        },

        SalidaNueva() {
            this.$router.push({ path: "/portero/entradas-salidas/registro/0" })
        },

        buscar() {
            this.ObtenerEntradasSalidas();
        },

    }
}
</script>

