<template>
  <div id="app">
    <!-- <Header v-if="mostrarHeader"></Header> 
    <router-view @tokenValido="tokenValido"></router-view> -->
    <div class="container my-5">
      <div>
        <h3><i class="fas fa-users"></i> Empleados</h3>
      </div>
      <b-container fluid>
        <b-form-group label="Filtrar tipos de usuarios:">
          <b-form-checkbox-group
            id="checkbox-group-1"
            v-model="selectTiposEmpleados"
            :options="tiposEmpleados"
            name="flavour-1"
            @change="seleccionados"
          >
          </b-form-checkbox-group>
        </b-form-group>
      </b-container>
      <table-sortable
        :items="items"
        :fields="fields"
        :rutaNuevo="rutaNuevo"
        :permisoEscritura="permisoEscritura"
      ></table-sortable>
    </div>
  </div>
</template>

<script>
import TableSortable from "@/components/TableSortable";
import servicio from "@/services/servicio-empleados";
export default {
  data() {
    return {
      rutaNuevo: "empleados/registro/",
      tiposEmpleados: [],
      selectTiposEmpleados: [],
      items: [],
      itemsCopy: [],
      fields: [
        {
          key: "nombreCompleto",
          label: "Nombre",
          sortable: true,
          sortDirection: "desc",
        },
        {
          key: "celular",
          label: "Celular",
          sortable: true,
          class: "text-center",
        },
        {
          key: "correo",
          label: "Correo",
          sortable: true,
          class: "text-center",
        },
        { key: "tipo", label: "Area", sortable: true, class: "text-center" },
        { key: "documentacion", label: "Documentos", class: "text-center" },
        { key: "sendWS", label: "Contactar", class: "text-center" },
        { key: "actions", label: "Acciones", class: "text-center" },
      ],
      permisoEscritura: true,
    };
  },
  components: {
    TableSortable,
  },
  mounted() {
    this.$loading(true);
    // if (this.verificaTokenValido()) {
      // this.verificaPermiso("empleados");
      servicio
        .empleados()
        .then((r) => {
          this.items = r.data;
          this.items.forEach((element) => {
            if (element.estatus != null && element.estatus != undefined) {
              element._rowVariant = element.estatus ? "success" : "danger";
              element.estatus = element.estatus ? "Activo" : "Inactivo";
            }
          });

          this.itemsCopy = this.items;
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
        });

      servicio.tiposUtilizados().then((r) => {
        // this.tiposEmpleados = r.data;
        r.data.forEach((el) => {
          let tipoEmpleado = { text: el.descripcion, value: el.codigo };
          this.tiposEmpleados.push(tipoEmpleado);
        });
      });
    // }
  },
  methods: {
    seleccionados(checked) {
      if (checked.length > 0) {
        this.items = [];
        checked.forEach((chk) => {
          let tempItems = [];
          tempItems = this.itemsCopy.filter((x) => x.codigoTipoEmpleado == chk);
          tempItems.forEach((y) => this.items.push(y));
        });
      } else {
        this.items = this.itemsCopy;
      }
    },
  },
};
</script>
<style scoped>
</style>
