<template>
  <div>   
    <!-- <Header v-if="mostrarHeader"></Header> 
    <router-view @tokenValido="tokenValido"></router-view> -->
    <div class="container my-5">
        <div class="my-1">
            <h3><i class="fas fa-users"></i> Usuarios</h3>
        </div>
        <b-row>
            <b-col md="3" offset-md="8">
              <!-- @click="redirect"
                v-if="permisoEscritura && rutaNuevo.length > 0" -->
                <b-button
                v-b-modal.modal-usuario
                variant="primary"
                class="btn btn-sm py-1 btn-block"
                >
                <i class="fa fa-plus"></i> Nuevo
                </b-button>
            </b-col>
        </b-row>
        <table-sortable :items="items"
                        :fields="fields">
        </table-sortable>
        <b-modal
            id="modal-usuario"
            ref="modal"
            title="Registro usuario"
            centered 
            @ok="handleOk"
            >
             <!-- FORMULARIO MODAL-->
            <form ref="form" @submit.stop.prevent="guardarUsuario">
                <b-form-group
                label="Empleado"
                label-for="cboEmpleado"
                invalid-feedback="Campo requerido"
                >
                <b-form-select id="cboEmpleado" 
                v-model="selectedEmpleado" 
                :options="empleadosCombo" 
                size="sm" class="mt-3"
                @change="usuarioModel.codEmpleado = selectedEmpleado">
                </b-form-select>
                </b-form-group>

                <b-form-group
                label="Usuario"
                label-for="txtUsuario"
                invalid-feedback="Campo requerido"
                
                >
                <b-form-input
                    id="txtUsuario"
                    v-model="usuarioModel.usuario"
                    
                    required
                    autocomplete="off"
                ></b-form-input>
                </b-form-group>

                <b-form-group
                label="Contrase√±a"
                label-for="txtContrasena"
                invalid-feedback="Campo requerido"
                
                >
                <b-form-input
                    id="txtContrasena"
                    v-model="usuarioModel.contrasena"
                    
                    required
                    type="password"
                    autocomplete="off"
                ></b-form-input>
                </b-form-group>

                <b-form-group
                label="Confirmar contrase√±a"
                label-for="txtContrasenaConfirmacion"
                invalid-feedback="Campo requerido"
                >
                <b-form-input
                    id="txtContrasenaConfirmar"
                    v-model="confirmaContra"
                    
                    required
                    type="password"
                    autocomplete="off"
                ></b-form-input>
                </b-form-group>
            </form>
        </b-modal>
    </div>
  </div>
</template>

<script>
import TableSortable from "@/components/TableSortable"
import servicio from '@/services/servicio-usuarios'
import servicioEmpleados from '@/services/servicio-empleados'
    export default{   
      data(){
        return{
            items: [],
            fields: [
              { key: 'usuario', label: 'Usuario', sortable: true, class: 'text-center' },
              { key: 'nombreCompleto', label: 'Nombre', sortable: true, sortDirection: 'desc' },
              { key: 'tipoEmpleado', label: 'Tipo Empleado', sortable: true, sortDirection: 'desc' },
              { key: 'fechaUltimaConexion', label: 'Ultima Conexi√≥n', sortable: true, sortDirection: 'desc' },
              { key: 'actionsUsuarios', label: '', sortable: true, class: 'text-center' },
            ],
             modalInfo: {
                id: 'modal',
                title: '',
                content: '',
                codigo:'',
                disabled:false
            },
            empleadosCombo:[{value:null,text:'Seleccione un empleado...'}],
            selectedEmpleado:null,
            usuarioModel:{
                usuario:'',
                contrasena:'',
                codEmpleado:null
            },
            confirmaContra:'',
        }
      },      
      components:{
        TableSortable
      },
      mounted(){
        this.$loading(true);
        // if(this.verificaTokenValido()){          
        servicio.usuarios()
        .then(r=> {    
            this.items = r.data;
            console.log(this.items);
            return r.data
        }).catch(error=>{
            console.log(error);
        });     
        
        servicioEmpleados.empleados()
        .then(r=>{
            if(r.value){
                console.log("data ->  ", r.data)
                r.data.forEach(e=>{
                    if(e.usuario == ""){
                        let empleado = { value: e.codigo, text: e.nombre+" "+e.apellidoP+" "+e.apellidoM}
                        this.empleadosCombo.push(empleado);
                    }
                })
            }
        })
        
        
        
        this.$loading(false);
          // this.verificaPermiso("equipos");
        // }                
      },
        methods:{
            modal(){
                this.modalInfo.title = "Registro de usuario";
                this.$root.$emit("bv::show::modal-usuario")
            },
            handleOk(bvModalEvt) {
                // Prevent modal from closing
                bvModalEvt.preventDefault()
                // Trigger submit handler
                this.guardarUsuario()
            },
            async guardarUsuario(){
                this.$loading(true);
                this.$nextTick(() => {
                    this.$bvModal.hide('modal-usuario')
                })
                await servicio.guardarUsuario(this.usuarioModel).then(r=>{
                    if(r.value){
                        this.$swal.fire({
                            text: `El Usuario se ha guardado exitosamente!`,
                            icon: "success"
                        });
                    }
                });
                this.$loading(false);
            }
        } 
    }
</script>
<style scoped>

</style>
