import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                     G E N E R A L E S
//............................................................
servicio.obtenerDatosCP = (codigoPostal) => {
    return base.get(`/generales/codigoPostal/${codigoPostal}`)
        .then(r => {
            console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.obtenerParametro = (nombre) => {
    return base.get(`/generales/parametro/${nombre}`)
        .then(r => {
            console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.obtenerEmpresasCombo = () => {
    return base.get(`/empresas/combo`)
        .then(r => {
            console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

export default servicio