<template>
<div>
	<div class="container mt-3 mb-5">
		<div class="mt-3">
			<h3 v-if="parametro == 0"><i class="fas fa-users" ></i> Registrar pedido</h3>
			<h3 v-if="parametro > 0"><i class="fas fa-users" ></i> Actualizar el pedido</h3>
		</div>
		<hr>

		<!-- Hora y Fecha Salida -->
		<div class="row">
			<div class="form-group col-sm-12 col-md-4 col-lg-4 col-xl-3 mb-1" >
				<label>Hora de salida de la planta</label>
				
				<input v-model="pedido.horaSalida"
						class="form-control"
						type="time"
						:disabled="disabledPedido"/>
			</div>

			<div class="form-group col-sm-12 col-md-6 col-lg-5 mb-1">
				<label>Fecha de salida</label>
				<b-form-datepicker v-model="pedido.fechaSalida"
									:disabled="disabledPedido"
									locale="es"
									size="sm">
				</b-form-datepicker>

			</div>
		</div>

		<!-- Vendedor -->
		<div class="row">
			<div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
				<label>Vendedor</label>
				<div class="input-group">
					<b-form-select id="cboVendedor"
									v-model="pedido.codVendedor"
									:options="vendedores"
									v-bind:class="{ hasError: hasErrorVendedor }"
									v-on:change="obtenerClientes"
									:disabled="disabledVendedor || disabledPedido">
					</b-form-select>
				</div>
			</div>

			<div class="form-group col-sm-12 col-md-4 col-lg-4 col-xl-3 mb-2" >
				<b-form-checkbox v-model="pedido.tieneDescuento"
								name="check-button"
								checkbox
								size="lg"
								> Descuento
				</b-form-checkbox>
				<div class="row" v-if="pedido.tieneDescuento">
					<input v-selectonfocus
								type="text"
								class="form-control col-4 col-lg-8"
								v-model="pedido.porcentajeDescuento"
								/>
					<h3>%</h3>
				</div>
				
			</div>
		</div>

		<!-- Cliente y Obra-->
		<div class="row">
			<div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
				<label>Cliente</label>
				<div class="input-group">
					<b-form-select id="cboCliente"
									:disabled="disabledClientes || disabledPedido"
									v-model="pedido.codCliente"
									:options="clientes"
									v-bind:class="{ hasError: hasErrorCte }"
									v-on:change="obtenerObras">
					</b-form-select>
				</div>
			</div>

			<div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
				<label>Obras</label>
				<div class="input-group">
					<b-form-select id="cboObras"
									:disabled="disabledObras || disabledPedido"
									v-model="pedido.codObra"
									:options="obras"
									v-bind:class="{ hasError: hasErrorObra }"
									v-on:change="obraSeleccionada">
					</b-form-select>					
				</div>
				<b-form-text id="input-live-help">
						Dist. Apr√≥x: {{distanciaActual}} <b>Km.</b>
						<br/>
						Tiempo Apr√≥x: {{tiempoActual}}
				</b-form-text>
			</div>
		</div>
		
		<!-- Producto / Formula -->
		<div class="row">
			<div class="form-group col-sm-12 col-md-4 col-lg-4 col-xl-12 mb-1">
				<label>F√≥rmula</label>
				<div class="row">
					<div class="input-group col-xl-10">
					<input
						ref="refFormula"
						type="text"
						class="form-control"
						autocomplete="off"
						v-model="state"
						@input="filterState"
						:disabled="disabledPedido"
						@focus="focusInputFormula"
						v-bind:class="{ hasError: hasErrorProducto }"
					/>
					<span class="input-group-btn">
						<b-button
						:disabled="disabledFormula || disabledPedido"
						variant="outline-primary"
						class="input-group-4"
						@click="limpiarProducto"
						><i class="fa fa-times" aria-hidden="true"></i
						></b-button>
					</span>
					</div>
					<b-button
					variant="primary"
					class="col-xs-4"
					data-toggle="tooltip"
					data-placement="top"
					:disabled="disabledPedido"
					@click="habilitarFormula"
					><i class="fa fa-edit" aria-hidden="true"></i> Modificar</b-button
					>
				</div>
				<div v-if="formulasFiltradas && modal">
					<ul class="list-group items-body">
					<li
						class="list-group-item py-2 items-body-content"
						v-for="formula in formulasFiltradas"
						v-bind:key="formula.value"
						@click="setState(formula)"
					>
						<span> {{ formula.text }}</span>
						<i class="fa fa-angle-right"></i>
					</li>
					</ul>
				</div>
				<transition name="fade">
					<label v-show="mostrarProducto">
					<span class="labelSize">Nomenclatura: </span>
					{{ pedido.producto }}</label
					>
				</transition>
			</div>
		</div>

		<!-- Cantidad y Cierre-->
		<div class="row hasError">
			<!-- Cantidad Original Pedido -->
			<div class="form-group col-sm-12 col-md-4 col-lg-3 mb-2">
				<label>Cantidad</label>
				<div class="input-group">
					<input id="cantidad"
							v-selectonfocus
							ref="refCantidad"
							type="text"
							class="form-control col-4 col-md-8"
							v-bind:class="{ hasError: hasErrorCantidad }"
							v-model="pedido.cantidad"
							v-on:change="hasErrorCantidad = false"
							@focus="onFocus(1)"
							@keypress="numerosDecimales"
							:disabled="disabledPedido"/>
					<label for="cantidad" class="labelSize col-4"><p>m<sup>3</sup></p></label>
				</div>
			</div>
			<div v-if="!pedido.tieneCierres"
				class="form-group col-sm-12 col-md-4 col-lg-4 col-xl-3 mb-2" >
				<b-form-checkbox v-model="pedido.cierre"
								name="check-button"
								switch
								size="lg"
								v-on:change="cierreFuncion()"
								>Cierre
				</b-form-checkbox>
				<div class="row" v-if="pedido.cierre">
					<input v-selectonfocus
							type="text"
							class="form-control col-4 col-lg-8"
							v-model="pedido.cantidadCierre"
							@focus="onFocus(2)"
							@keypress="numerosDecimales"/>
					<label for="cantidad" class="labelSize col-4"><p>m<sup>3</sup></p></label>
				</div>
				
			</div>
			<!-- Cantidad total (original + cierres) -->
			<div class="form-group col-sm-12 col-md-4 col-lg-3 mb-2" v-if="pedido.tieneCierres">
				<label>Cantidad Total</label>
				<div class="input-group">
					<input 
							disabled
							v-selectonfocus
							ref="refCantidad"
							type="text"
							class="form-control col-4 col-md-8"
							v-bind:class="{ hasError: hasErrorCantidad }"
							v-model="pedido.cantidadCierre"
							v-on:change="hasErrorCantidad = false"
							@focus="onFocus(1)"
							@keypress="numerosDecimales"/>
					<label for="cantidad" class="labelSize col-4"><p>m<sup>3</sup></p></label>
				</div>
			</div>
			
			<div class="form-group col-sm-12 col-md-6 mb-2" v-if="pedido.cierre">
				<div class="row">
					<div v-if="pedido.tieneCierres"
						class="form-group col-sm-6 col-lg-4 mb-2">
						<label class="text-white">.</label>
						<div class="row" v-if="pedido.cierre">
							<button class=" btn btn-info col-lg-10 sm-ml-3" @click="buscarCierres">
								<!-- <i class="fas fa-truck-moving"></i> -->
								<span v-if="!mostrarCierres"> Ver cierres </span>
								<span v-if="mostrarCierres"> Ocultar cierres </span>
							</button>
						</div>
					</div>
					<div v-if="pedido.tieneCierres"
						class="form-group col-sm-6 col-lg-4 mb-2">
						<label class="text-white">.</label>
						<div class="row" v-if="pedido.cierre">
							<button class=" btn btn-dark col-lg-10 sm-ml-3" @click="capturaNvoCierre" :disabled="disabledCierres">
								<span> Nuevo cierre </span>
							</button>
						</div>
					</div>
				</div>
				<div class="row">
					<transition name="bounce">
					<div class="input-group" v-show="nuevoCierre">

						<b-input ref="cierreNvo"
									placeholder="Cantidad del cierre"
									v-model="cierreNuevo"
									@focus="onFocus(3)"
									@keypress="numerosDecimales"
									>
						</b-input>
						<label for="cantidad ml-3 mr-3" class="labelSize col-2"><p>m<sup>3</sup></p></label>
						<span class="input-group-btn">
							<b-button variant="success"
										class="input-group-4"
										@click="aceptarNuevoCierre">
										<i class="fa fa-check"></i>
							</b-button>
						</span>
						<span class="input-group-btn ml-2">
							<b-button variant="danger"
										class="input-group-4"
										@click="cancelarNuevoCierre">
										<i class="fa fa-times"></i>
							</b-button>
						</span>
					</div>
				</transition>
				</div>
			</div>

			

			
			
		</div>

		

		<div v-if="mostrarCierres">
			<table-sortable class="mt-3"
				:items="items"
				:fields="fields"
			></table-sortable>
		</div>

		<!-- Guardar y Cancelar -->
		<b-row>
			<b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
			<b-col class="col-md-3 col-sm-12 col-12" align-self="end">
				<b-button @click="guardar" block variant="success" class="mr-2 mt-2">
					<i class="fa fa-save"></i> Guardar
				</b-button>
			</b-col>
			<b-col class="col-md-3 col-sm-12 col-12" align-self="end">
				<b-button @click="cancelar" block variant="danger" class="mr-2 mt-2">
					Cancelar
				</b-button>
			</b-col>
		</b-row>

	</div>
</div>
</template>

<script>

import TableSortable from "@/components/TableSortable";

import servicioDosificador from "@/services/servicio-dosificador.js";
import servicioClientes from "@/services/servicio-clientes";
import servicioAgente from "@/services/servicio-agentes";
import servicioPedidos from "@/services/servicio-pedidos";
import servicioPlanta from "@/services/servicio-planta";

import Swal from "sweetalert2";

export default {

	data() {
		const now = new Date()
		const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
		var todayDate = today.toISOString().slice(0,10);

		return {
			hasErrorCte: false,
			hasErrorObra: false,
			hasErrorVendedor: false,
			hasErrorCantidad: false,
			disabledObras: true,
			disabledClientes: false,
			disabledCierres: false,
			nuevoCierre: false,
			mostrarOperadores: true,
			distanciaActual:"0",
			tiempoActual:"0",
			clientes: [{ value: 0, text: "Seleccione el cliente" }],
			obras: [{ value: 0, text: "Seleccione la obra del cliente" }],
			vendedores: [{ value: 0, text: "Seleccione el vendedor" }],
			pedido: {
				folioPedido: 0,
				horaSalida: "12:00",
				fechaSalida: todayDate,
				codCliente: 0,
				codObra: 0,
				codVendedor: 0,
				cantidad: 0,
				cantidadCierre: 0,
				cierre: false,
				tieneCierres: false,
				producto: "",
				codProducto: 0,
				estatus: "",
				tieneDescuento: false,
				porcentajeDescuento: 0,
				observacion: '',
			},
			inputSeleccionado: 0,
			parametro: 0,
			
			items: [],
			fields: [
				{ key: "folioPedido", label: "Folio Pedido", sortable: true, sortDirection: "desc", },
				{ key: "cantidad", label: "Cantidad", sortable: true, sortDirection: "desc", },
				{ key: "cancelarPedidoCierre", label: "" },
			],
			mostrarCierres: false,
			cierreNuevo: "",


			disabledFormula: true,
			nuevaFormula: false,
			mostrarProducto: false,
			state: "",
			formulasFiltradas: [],
			formulas: [],
			
			formulaNueva: "",
			hasErrorProducto: false,
			disabledlimpiarProductos: false,
			modal: false,


			disabledVendedor: false,
			disabledPedido: false,
			continuarGuardado: false,
			// disabledClientes




		};

	},

	components: {
        TableSortable,
	},
	
	watch: {
		state: function (val) {
			val.length >= 1 && this.pedido.codProducto == 0
				? (this.modal = true)
				: (this.modal = false);

			if (val.length === 0) {
				this.pedido.producto = 0;
				this.pedido.codProducto = 0;
				this.mostrarProducto = false;
			}
		},
	},

	mounted() {
		// console.log('mounted');

		this.parametro = this.$route.params.id != undefined ? this.$route.params.id : 0;
		console.log('P->', this.parametro);

		if(this.parametro > 0){
			this.cargaPedido();
		} 

	},

	async created() {
		// console.log('created');
		// await this.obtenerClientes();



		if(this.usuario.agente === 1) {
			// console.log('es agente el vato');
			this.disabledVendedor = true;
			this.vendedores = [{ value: this.usuario.codEmpleado, text: this.usuario.nombre }];
			this.pedido.codVendedor = this.vendedores[0].value;
			this.obtenerClientes();
			// console.log(this.vendedores);
		} else {
			this.disabledVendedor = false;
			// console.log('no es agente :(');
			await this.obtenerVendedores();
			this.disabledClientes = true;

		}

		await this.cargarFormulas(0);
		if(this.parametro == 0 ) {
			var horaActual = `${new Date().getHours() + 1}:00`;
			this.pedido.horaSalida = horaActual;
		}
	},

	computed: {},
	methods: {

		//clientes
		async obtenerClientes() {

			this.hasErrorVendedor = false;
			this.hasErrorCte = false;
			this.hasErrorObra = false;

			this.clientes = [{ value: 0, text: "Seleccione el cliente" }];
			this.pedido.codCliente = this.clientes[0].value;
			this.obras = [{ value: 0, text: "Seleccione la obra del cliente" }];
			this.pedido.codObra = this.obras[0].value;
			this.disabledObras = true;

			if(this.pedido.codVendedor > 0) {
				// console.log('CODIGO: ', this.pedido.codVendedor);
				// this.clientes = [{ value: 0, text: "Seleccione el cliente" }];
				this.$loading(true);
				await servicioClientes.obtenerClientesAgente(this.pedido.codVendedor).then((r) => {
					if (r.value) {
						r.data.forEach((element) => {
							let tipo = { value: element.codigo, text: element.nombreCompleto };
							this.clientes.push(tipo);
						});
					}
				});
				this.disabledClientes = false;
				this.$loading(false);
			} else {
				this.disabledClientes = true;
				
			}

			
		},

		async obtenerObras() {

			this.hasErrorObra = false;

			let codCliente = this.pedido.codCliente;
			this.pedido.codObra = 0;
			this.obras = [{ value: 0, text: "Seleccione la obra del cliente" }];
			this.$loading(true);

			console.log('aqui ando');
			await servicioDosificador.obtenerObras(codCliente).then((r) => {
				this.$loading(false);
				if (r.value) {
					this.disabledObras = false;
					this.hasErrorCte = false;

					r.data.forEach((element) => {
						let tipo = { value: element.codigo, text: element.calleNumero };
						this.obras.push(tipo);
					});
					console.log(this.obras);
				} else {
					this.disabledObras = true;
				}
			});
			this.$loading(false);
		},

		obraSeleccionada() {
			this.hasErrorObra = false;
			this.obtenerDistanciaTiempoObra();
		},
		
		async obtenerDistanciaTiempoObra(){
			console.log("Obteniendo tiempo")
			this.$loading(true);
			let currentDireccion = this.pedido.codObra;			
			let coordenadasPlanta = "";
			let coordenadasDireccion = "";			
			await servicioPlanta.obtenerUbicacion().then(r=>{
				if(r.value){
					coordenadasPlanta = r.data.valor;					
				}
			}).catch(e=>{				
				console.err(e);
			});
			await servicioClientes.obtenerDireccion(currentDireccion).then(r=>{
				if(r.value){					
					coordenadasDireccion = `${r.data[0].latitud},${r.data[0].longitud}`					
				}				
			}).catch(e=>{				
				console.err(e);
			});	

			//Aqui me quede hay que modificar el servicio por el componente 


			let distance = require('google-distance-matrix');
			var origins = [coordenadasDireccion];
			var destinations = [coordenadasPlanta];
			//AQUI SE HACE LA MODIFICACION


			distance.key("AIzaSyDN3U-3Wii-C4MU_Poy7MncZgUdt7N2cU4");
			

			let _this = this;
			distance.matrix(origins, destinations, async function (err, distances) {
				if (!err){
					console.log("DISTANCIAS entre puntos ",distances.rows[0].elements[0].distance.text);
				}	
					_this.distanciaActual =  await distances?.rows[0].elements[0].distance.text;
					_this.tiempoActual =  await distances?.rows[0].elements[0].duration.text;			
			});
			// await servicioPedidos.obtenerTiempoDistancia(coordenadasPlanta,coordenadasDireccion);
			
			// const matrix = new google.maps.DistanceMatrixService();

			this.$loading(false)

			
		},

		// vendedores
		async obtenerVendedores() {
			this.$loading(true);
			await servicioAgente.agentesCombo().then((r) => {
				if (r.value) {
					r.data.forEach((element) => {
						let agente = {
							value: element.codVendedor,
							text: element.nombreVendedor,
						};
						this.vendedores.push(agente);
					});
				}
			});
			this.$loading(false);
		},

		// guardar
		async guardar() {
			console.log(this.pedido);
			if (!this.validarRegistros()) {
				let r = await Swal.fire({
					title: "¬°ATENCI√ìN!",
					text: "¬øDesea guardar el pedido?",
					showCancelButton: true,
					icon: 'question',
					confirmButtonColor: "#3085d6",
					cancelButtonColor: "#d33",
					confirmButtonText: "Aceptar",
					cancelButtonText: "Cancelar",
				});

				if (r.value) {
					console.log(this.pedido.fechaSalida);
					this.continuarGuardado = true;
				}
			} else {
				this.$swal.fire({
					text: 'Hay campos requeridos por completar',
					icon: "error",
				});
			}

			if(this.continuarGuardado) {

				await this.CapturarObservacion();
				if(this.continuarGuardado) {
					console.log('guardo');
				}
				
					this.$loading(true);
					let result = await servicioPedidos.guardarPedido(this.pedido);

					if(result.value) {
						this.$loading(false);
						Swal.fire(
							'¬°EXITO!',
							result.message,
							'success'
							);
						this.$router.push({ path: "/pedidos" })

					} else {
						this.$loading(false);
						this.$swal.fire({
							text: result.message,
							icon: "error",
						});
					}
			}


		},

		//cancelar
		async cancelar() {
			let r = await Swal.fire({
				title: "¬°ATENCI√ìN!",
				text: "¬øDesea cancelar el registro del pedido?",
				showCancelButton: true,
				icon: 'question',
				confirmButtonColor: "#3085d6",
				cancelButtonColor: "#d33",
				confirmButtonText: "Aceptar",
				cancelButtonText: "Cancelar",
			});

			if (r.value) {
				window.history.back();
			}
		},



		validarRegistros() {
			var erroresEncontrados = false;
			if (this.pedido.codCliente === 0) {
				this.hasErrorCte = true;
				erroresEncontrados = true;
			}

			if (this.pedido.codObra === 0) {
				this.hasErrorObra = true;
				erroresEncontrados = true;
			}

			if (this.pedido.cantidad === "0" || this.pedido.cantidad === 0) {
				this.hasErrorCantidad = true;
				erroresEncontrados = true;
			}

			if (this.pedido.codVendedor === 0) {
				this.hasErrorVendedor = true;
				erroresEncontrados = true;
			}

			if (this.pedido.codProducto === 0) {
				this.hasErrorProducto = true;
				erroresEncontrados = true;
			}
			return erroresEncontrados;
		},

		inicializarDatos() {
			this.pedido = {
				horaSalida : "12:00",
				fechaSalida: this.todayDate,
				codCliente: 0,
				codObra: 0,
				codVendedor: 0,
				cantidad: 0,
				cierre: false,
			};
		},

		numerosDecimales ($event) {
            let keyCode = ($event.keyCode ? $event.keyCode : $event.which);

            if(this.inputSeleccionado == 1) {
                if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || this.pedido.cantidad.indexOf('.') != -1)) { // 46 is dot
                    $event.preventDefault();
                }
            } else if(this.inputSeleccionado == 2) {
                if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || this.pedido.cantidadCierre.indexOf('.') != -1)) { // 46 is dot
                    $event.preventDefault();
                }
            } else if(this.inputSeleccionado == 3) {
                if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || this.cierreNuevo.indexOf('.') != -1)) { // 46 is dot
                    $event.preventDefault();
                }
            }

		},

		onFocus: function (index){
            this.inputSeleccionado = index;
		},

		async cargaPedido() {
			if (this.parametro > 0) {
				console.log('#', this.parametro);
				const now = new Date()
				const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
				var todayDate = today.toISOString().slice(0,10);
				let codObra = 0;
				let codCliente = 0;

				this.$loading(true);

				await servicioPedidos.obtenerPedidos(this.parametro, todayDate, todayDate)
					.then((r) => {
						if (r.value) {
							console.log('RR->', r);
							this.pedido.folioPedido = r.data[0].folioPedido;
							this.pedido.codVendedor = r.data[0].codVendedor;
							// this.pedido.codCliente = r.data[0].codCliente;
							codCliente = r.data[0].codCliente;
							this.pedido.horaSalida = r.data[0].horaSalida;
							this.pedido.fechaSalida = r.data[0].fechaSalida;
							this.pedido.cierre = r.data[0].cierre;
							this.pedido.cantidadCierre = r.data[0].cantidadCierre;
							this.pedido.cantidad = r.data[0].cantidad;
							this.pedido.tieneCierres = r.data[0].tieneCierres;
							this.pedido.codProducto = r.data[0].codProducto;
							this.pedido.estatus = r.data[0].estatus;
							this.pedido.tieneDescuento = r.data[0].tieneDescuento;
							this.pedido.porcentajeDescuento = r.data[0].porcentajeDescuento;
							codObra = r.data[0].codObra;
							this.$loading(false);
						} else {
							this.$loading(false);
							this.$swal.fire({
								text: `${r.message}`,
								icon: "error",
							});
							this.$router.push({ path: "/pedidos" })

						}
					});
				await this.obtenerClientes();
				this.pedido.codCliente = codCliente;
				await this.obtenerObras();
				// await this.cargarFormulas(0);
				this.pedido.codObra = codObra;
				console.log('PEDIDO -> ', this.pedido);
				console.log(this.pedido.codProducto);
				this.obtenerFormulaProducto(this.pedido.codProducto);

				if(this.pedido.estatus != "P") {
					console.log('Se bloqueara todo');
					this.disabledPedido = true;
				} else {
					this.disabledPedido = false;
				}
			}
		},

		async buscarCierres() {

			if(this.mostrarCierres) {
				this.mostrarCierres = false;
			} else {
				this.$loading(true);
				await servicioPedidos.obtenerCierres(this.pedido.folioPedido)
					.then((r) => {
							if (r.value) {
								console.log('CIERRES->', r);
								this.items = r.data;
								this.mostrarCierres = true;
								this.$loading(false);
							} else {
								this.$loading(false);
								this.$swal.fire({
									text: `${r.message}`,
									icon: "error",
								});
							}
					});
			}
			
			
		},

		async capturaNvoCierre() {
			let r = await Swal.fire({
				title: "¬°ATENCI√ìN!",
				text: "¬øDesea crear un nuevo cierre?",
				showCancelButton: true,
				icon: 'question',
				confirmButtonColor: "#3085d6",
				cancelButtonColor: "#d33",
				confirmButtonText: "Aceptar",
				cancelButtonText: "Cancelar",
			});

			if (r.value) {
				this.disabledCierres = true;
				// this.pedido.cantidadCierre = 0;
				this.nuevoCierre = true;

				this.$nextTick(() => {
					this.$refs.cierreNvo.focus();
				});
			}
		},

		async aceptarNuevoCierre() {
			console.log(this.cierreNuevo);

			if(parseFloat(this.cierreNuevo) && this.cierreNuevo > 0) {
				this.$loading(true);
				// this.pedido.cantidadCierre = this.cierreNuevo;

				await servicioPedidos.guardarCierre(this.pedido.folioPedido, this.cierreNuevo)
					.then((r) => {
					if (r.value && r.data != null) {
						Swal.fire(
							'¬°EXITO!',
							r.message,
							'success'
						);
						this.disabledCierres = false;
						this.cierreNuevo = "";
						this.cargaPedido();
						this.buscarCierres();
					} else {
						Swal.fire(
							'¬°ATENCI√ìN!',
							r.message,
							'error'
						);
					}
					this.nuevoCierre = false;
				});
				this.$loading(false);
			} else{
				Swal.fire(
					'¬°ATENCI√ìN!',
					'La cantidad del nuevo cierre tiene que ser mayor a 0 mts.',
					'info'
				);
			}
		},

		cancelarNuevoCierre() {
			this.disabledCierres = false;
			this.cierreNuevo = "";
			this.nuevoCierre = false;
		},

		cierreFuncion() {
			this.pedido.cierre = !this.pedido.cierre;
		},

		// Formula

		async agregarNvaFormula() {
			let r = await Swal.fire({
				title: "¬øDesea crear una nueva f√≥rmula de Producto?",
				showCancelButton: true,
				confirmButtonColor: "#3085d6",
				cancelButtonColor: "#d33",
				confirmButtonText: "Aceptar",
				cancelButtonText: "Cancelar",
			});

			if (r.value) {
				this.pedido.codProducto = 0;
				this.nuevaFormula = true;
				this.disabledProductos = true;
				this.$nextTick(() => {
				this.$refs.nvaFormula.focus();
				});
			}
		},
		async aceptarNuevaFormula() {
			this.$loading(true);
			await this.obtenerProducto(this.formulaNueva);
			let formula = {
				nomenclatura: this.formulaNueva,
				descripcion: this.pedido.producto,
			};
			await servicioDosificador.guardarFormula(formula).then((r) => {
				if (r.value && r.data != null) {
				let codFormulaNva = r.data[0];
				this.$swal.fire({
					text: r.message,
					icon: "success",
				});
				this.disabledProductos = false;
				this.cargarFormulas(0);
				this.pedido.codProducto = codFormulaNva;
				} else {
				this.$swal.fire({
					text: r.message,
					icon: "error",
				});
				}
				this.nuevaFormula = false;
			});
			this.$loading(false);
		},

		async cargarFormulas(codigo) {
			this.$loading(true);
			let r = await this.obtenerFormulas(codigo);
			if (r.value) {
				r.data.forEach((element) => {
				let tipo = {
					value: element.codigo,
					text: element.descripcion,
					producto: element.nomenclatura,
				};
				this.formulas.push(tipo);
				});
			}
			// console.log('FORMULAS->', this.formulas);
			this.$loading(false);
		},

		async obtenerFormulas(codigo) {
			return await servicioDosificador.obtenerFormulas(codigo);
		},


		filterState() {
			this.formulasFiltradas = this.formulas.filter((item) => {
				const descripcion = item.text.toString().toUpperCase();
				const nomenclatura = item.producto.toString().toUpperCase();
				const searchTerm = this.state.trim().toUpperCase();

				return descripcion.includes(searchTerm) || nomenclatura.includes(searchTerm)
			});
		},

		setState(formula) {
			console.log('ENTRA ESTA -> ',formula);
			this.pedido.codProducto = formula.value;
			this.state = formula.text;
			this.pedido.producto = formula.producto;
			this.modal = false;
			this.mostrarProducto = true;
			this.hasErrorProducto = false;
			this.disabledFormula = true;
		},

		obtenerFormulaProducto(idProducto) {
			let formula = this.formulas.filter((c) => c.value == idProducto);
			// console.log(formula);
			this.setState(formula[0]);
		},

		focusInputFormula() {
			if (this.state.length > 0) {
				this.modal = true;
			}
		},

		limpiarProducto() {
			this.pedido.producto = 0;
			this.pedido.codProducto = 0;
			this.mostrarProducto = false;
			this.disabledFormula = false;
			this.state = "";
			this.$nextTick(() => {
				this.$refs.refFormula.focus();
			});
		},

		habilitarFormula() {
			this.disabledFormula = false;
			this.pedido.producto = 0;
			this.pedido.codProducto = 0;
			this.mostrarProducto = false;
			this.state = "";
			this.$nextTick(() => {
				this.$refs.refFormula.focus();
			});			
			// this.disabledFormula = false;
			// this.pedido.producto = 0;
			// this.pedido.codProducto = 0;
			// this.mostrarProducto = false;
			// this.state = "";
			// this.$nextTick(() => {
			// 	this.$refs.refFormula.focus();
			// });
		},
		async CapturarObservacion() {

			let observacion;

			const { value: formValues } = await Swal.fire({
				title: 'Ingrese una observaci√≥n:',
				html:
					'<textarea id="swal-input2" class="swal2-input" placeholder="Capture una observaci√≥n" style="margin-top: 20px; margin-bottom: 20px; height: 120px;">',
				focusConfirm: false,
				showCancelButton: false,
				confirmButtonText: 'Aceptar',
				// cancelButtonText: 'Cancelar',
				confirmButtonColor: '#3085d6',
				// cancelButtonColor: '#d33',

				preConfirm: () => {

					observacion = Swal.getPopup().querySelector('#swal-input2').value

					let inputObservacion = Swal.getPopup().querySelector('#swal-input2');

					inputObservacion.style.borderColor = "";

					// if (observacion === '') {
					// 	inputObservacion.style.borderColor = "red";
					// 	Swal.showValidationMessage(`Favor de ingresar una observaci√≥n.`)
					// }
				},

			})

			if (formValues) {
				this.pedido.observacion = observacion;
				this.continuarGuardado = true;
			} else {
				this.continuarGuardado = false;
			}

		},
	},
};
</script>

<style scoped>

	.hasError {
		border-color: red;
	}

	.labelSize {
		font-size: 20px;
		font-weight: bold;
	}

	.fade-enter-active,
	.fade-leave-active {
		transition: opacity 0.5s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
		opacity: 0;
	}

	.bounce-enter-active {
		animation: bounce-in 0.4s;
	}

	.bounce-leave-active {
		animation: bounce-in 0.5s reverse;
	}

	@keyframes bounce-in {
		0% {
			transform: scale(0);
		}
		100% {
			transform: scale(1);
		}
	}

