<template>
  <div id="app">
    <div class="container my-5">
      <div class="my-5">
        <h3><i class="fas fa-users"></i> Clientes</h3>
      </div>
      <b-modal
        :id="modalEspecial.id"
        :title="modalEspecial.title"
        @hide="resetModalEspecial"
        @show="cargarDatosSugeridos"
        :codigo="modalEspecial.codigo"
        @ok="aceptarNombreUsuario"
      >
        <form ref="form" @submit.stop.prevent="handleSubmit">
          <b-form-group
            label="Nombre de usuario"
            label-for="txtNombreUsuario"
            invalid-feedback="El nombre de usuario es requerido"
          >
            <b-form-input
              id="txtNombreUsuario"
              required
              v-model="form.usuario"
              autocomplete="false"
              :disabled="modalEspecial.disabled"
            ></b-form-input>
          </b-form-group>
          <b-form-group
            label="Contrase√±a de usuario"
            label-for="txtContrase√±a"
            invalid-feedback="La contrase√±a es requerida"
          >
            <b-form-input
              id="txtContrase√±a"
              type="password"
              required
              v-model="form.contrase√±a"
              autocomplete="false"
              :disabled="modalEspecial.disabled"
            ></b-form-input>
            <b-form-text>
              El usuario y la contrase√±a son recomendadas por el sistema.
              <br />
              Las puede cambiar.
            </b-form-text>
          </b-form-group>
        </form>
      </b-modal>
      <table-sortable
        :items="items"
        :fields="fields"
        :rutaNuevo="rutaNuevo"
        :modalEspecial="modalEspecial"
        :permisoEscritura="permisoEscritura"
      ></table-sortable>
    </div>
  </div>
</template>

<script>
import TableSortable from "@/components/TableSortable";
import servicioClientes from "@/services/servicio-clientes";
export default {
  data() {
    return {
      form: {
        usuario: "",
        contrase√±a: "",
        codigo: 0,
      },
      modalEspecial: {
        id: "modal-especial",
        title: "",
        content: "",
        codigo: "",
        disabled: false,
      },
      rutaNuevo: "clientes/registro/",
      items: [],
      fields: [
        {
          key: "nombreComercial",
          label: "Nombre comercial",
          sortable: true,
          sortDirection: "desc",
        },
        {
          key: "creditoFormat",
          label: "Cr√©dito",
          sortable: true,
          class: "text-center",
        },
        {
          key: "fechaCorta",
          label: "Registro",
          sortable: true,
          class: "text-center",
        },
        {
          key: "credito",
          label: "Cr√©dito Utilizado",
          sortable: true,
          class: "text-center",
        },
        //   {
        //     key: 'isActive',
        //     label: 'is Active',
        //     formatter: (value, key, item) => {
        //         console.log(key,item)
        //       return value ? 'Yes' : 'No'
        //     },
        //     sortable: true,
        //     sortByFormatted: true,
        //     filterByFormatted: true
        //   },
        { key: "userClient", label: "Cliente-Usuario", class: "text-center", },
        { key: "sendWS", label: "Contactar", class: "text-center" },
        { key: "actions", label: "Acciones", class: "text-center" },
      ],
      permisoEscritura: true,
    };
  },
  components: {
    TableSortable,
  },
  mounted() {
    // if (this.verificaTokenValido()) {
    //   this.verificaPermiso("catalogos/clientes");
    //   this.consultarClientes();
    // }
      this.consultarClientes();

  },
  methods: {
    resetModalEspecial() {
      this.modalEspecial.title = "";
      this.modalEspecial.disabled = false;
      // this.modalEspecial.content = ''
    },
    cargarDatosSugeridos() {
      //Mandar llamar al servicio de recomendar usuario
      this.$loading(true);
      servicioClientes
        .sugerirUsuario(this.modalEspecial.codigo)
        .then((r) => {
          if (r.value) {
            this.form.usuario = r.data[0].usuario;
            this.form.contrase√±a = r.data[0].contrase√±a;
            this.form.codigo = this.modalEspecial.codigo;
            this.$loading(false);
          } else {
            servicioClientes
              .obtenerUsuario(this.modalEspecial.codigo)
              .then((r) => {
                this.form.usuario = r.data[0].usuario;
                this.form.contrase√±a = r.data[0].contrase√±a;
                // this.form.codigo = this.modalEspecial.codigo
                this.modalEspecial.disabled = true;
              });
            this.$loading(false);
          }
        })
        .catch((e) => {
          console.error(e);
        });
    },
    aceptarNombreUsuario() {
      this.$loading(true);
      let datosUsuario = this.form;
      servicioClientes
        .guardarUsuario(datosUsuario)
        .then((r) => {
          this.$loading(false);
          if (r.value) {
            this.$swal.fire({
              text: r.message,
              icon: "success",
            });
            this.consultarClientes();
          } else {
            this.$swal.fire({
              text: r.message,
              icon: "warning",
            });
          }
        })
        .catch((e) => {
          console.error(e);
        });
    },
    consultarClientes() {
      this.$loading(true);
      servicioClientes
        .clientes()
        .then((r) => {
          console.log(r)
          this.items = r.data.cliente;
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
          this.$loading(false);
        });
    },
  },
};
</script>
<style scoped>
</style>