<template>
  <pm-buscador
    v-focus
    :columnas="columnas"
    :items="items"
    :texto-buscar="textoBuscar"
    @buscar="buscar"
    @seleccionar="seleccionar"
    :limpiar-texto="limpiarTexto"
    :placeholder="placeholder"
  >
    <template v-slot:row="row">
      <td class="font-12 text-right">{{ row.item.folioPedido }}</td>
      <td class="font-12">{{ row.item.cliente }}</td>
      <td class="font-12">{{ row.item.vendedor }}</td>
    </template>
  </pm-buscador>
</template>

<script>
import PmBuscador from "@/components/BuscadorBase";
import servicioBuscador from "@/services/general/servicio-buscadores";
export default {
  props: {
    limpiarTexto: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      columnas: [
        { header: "Folio" },
        { header: "Cliente" },
        { header: "Vendedor" },
      ],
      items: [],
      textoBuscar: "",
      placeholder: "Buscar pedido...",
    };
  },
  components: {
    PmBuscador,
  },
  methods: {
    buscar(textoBuscar) {
      servicioBuscador.buscarPedidos(textoBuscar).then((response) => {
        this.items = response.data;
      });
      console.log("buscando..." + textoBuscar);
    },
    seleccionar(item) {
      if (item == undefined) return;

      this.textoBuscar = item.codigo;
      this.$emit("seleccionar", item);
    },
  },
};
</script>

