import config from "@/services/config";

const globales = {
    data() {
        return {}
    },
    methods: {
        isChrome() {
            return ((navigator.userAgent.toLowerCase().indexOf('chrome') > -1) && (navigator.vendor.toLowerCase().indexOf("google") > -1));
        },
        imgExtension() {
            return this.isChrome() ? 'webp' : 'jpg'
        },
        usuarioGlobal() {
            let token = undefined;
            let user = undefined;

            if (window.localStorage.tokenWebAdmin) {
                token = JSON.parse(window.localStorage.tokenWebAdmin)
                user = JSON.parse(token.user)
            }

            //let user = JSON.parse(window.localStorage.user)
            return user
        },
        uriServicio() {
            return config.apiUrl
        }
    },
    computed: {
        tituloPantalla2() {
            return this.tituloPantalla
        },
        usuario() {

            let token = JSON.parse(window.localStorage.tokenWebAdmin)
            let us = JSON.parse(token.user)

            //let us = JSON.parse(window.localStorage.user)
            return us
        },
        permisos() {
            let token = JSON.parse(window.localStorage.tokenWebAdmin)
            let p = JSON.parse(token.permisos)
                //let p = JSON.parse(window.localStorage.permisos)
            return p
        },
        empresas() {
            let token = JSON.parse(window.localStorage.tokenWebAdmin)
            let e = JSON.parse(token.empresas)
                //let e = JSON.parse(window.localStorage.empresas)
            return e
        }
    }
}

export default globales