<template >
	<div>
		<button class="btn  mr-2 p-0 px-1 pr-2" type="button" id="btnNotificacion" @click="marcarVistaNotificaciones">
			<!-- <span class="d-none d-xl-inline mx-1">Notificaciones</span> -->
            <i class="fas fa-bell fa-lg m-0"></i>
            <!-- <span class="badge badge-pill badge-danger">0</span> -->
			<i class="icon-trazado-1103 yellow-bell m-0">
				<span :class="{'point-red':notificaciones != null ? notificaciones.length : 0}"></span>
			</i>
		</button>
		<b-popover 
			target="btnNotificacion"
			placement="bottomleft"
			triggers="click"
			custom-class="popover-notificaciones" 
			ref="popover">
			<ul class="noti-list">
				<div class="row" v-for="notificacion in notificaciones" :key="notificacion.codNotificacion" :class="{'no-leidos':!notificacion.redirigido}">
					<li class="noti-item text-truncate">										
						<a href="#/" class="noti-fuente" @click="redirigeNotificacion(notificacion)">						
							{{notificacion.descripcion}} - <span class="noti-date">{{notificacion.fechaNotifFormat}}</span>
						</a>
					</li>					
				</div>				
			</ul>
			<hr class="popover-line-bottom"/>
			<div class="mt-0">
				<div class="popover-marcar-todas">
					<button class="btn btn-inline btn-info btn-marcar-todas" @click="limpiaNotificaciones()">Marcar como leidas</button>	
				</div>
			</div>
		</b-popover>
	</div>
</template>

<script>
	import servicio from '@/services/servicio-notificacion'
	export default {
		props: ["notificaciones","notificacionesPendientes"],
		data() {			
            return {								  		  
                seleccionaTodo:false,
                seleccionados:[]				  		  
            }            
		},
		methods:{
			seleccionarTodos(){
				this.seleccionaTodos = !this.seleccionaTodos;
				if(this.seleccionaTodos){
					this.notificaciones.forEach(n => {
						this.seleccionados.push(n.codNotificacion);
					});
				}else{
					this.seleccionados = [];
				}
			},						
            redirigeNotificacion(notificacion){		                
				servicio.marcarNotificacionRedirigida(false,notificacion.codNotificacion)
				.then(r=>{
					if(r.value){
						this.$router.push({path:notificacion.uRLDestino})
					}
				})                
			},
			limpiaNotificaciones(){				                
				servicio.marcarNotificacionRedirigida(true)
				.then(r=>{					
					if(r.value){
						this.notificaciones.forEach(n=>{
							n.redirigido = 1;
						})
					}
				}).catch(e=>console.error(e))
			},

			marcarVistaNotificaciones(){
				return servicio.marcarNotificacionesVista()
				.then(r=>{					
					if(r.value){
																
						this.obtenerNotificaciones();						
					}
				});								                
			}					
		},			
	}
</script>

<style scoped>
	
	.noti-fuente { 
		color: #273C6E !important; 
		text-decoration: none !important
	}

	.yellow-bell {
		color: var(--bg-yellow);
		font-size: 19px;
		position: relative;
	}
	.point-red {
		top: 0.17rem;
		right: 0.05rem;
		position: absolute;
		width: 0.6rem;
		height: 0.6rem;
		border-radius: 50%;
		background: red;
	}
	
	.popover-notificaciones{
		max-width: 435px;
        /* rgb(13, 67, 135) !important; */
		background-color: whitesmoke;
		border-radius: 0px;
		border: none;
		color: red;
	}

	.no-leidos{
		background-color: #9CA2B2 !important;
	}

	.popover-notificaciones.bs-popover-auto[x-placement^=bottom]>.arrow::after, .popover-notificaciones.bs-popover-bottom>.arrow::after {		
		border-bottom-color: #BCC1CF;
	}

	.popover-notificaciones ul{		
		padding: 0rem 0.5rem;			
	}

	.popover-notificaciones ul li{		
		font-size: 16px;
		text-align: left;
		font-weight: 600;		
		color: white;	
		list-style: none;
		padding: 0.4rem 0.2rem;
	}

	.popover-notificaciones ul li .noti-date{
		font-size: 12px;
		text-align: left;
		font-weight: lighter;						
	}

	.popover-notificaciones .btn-marcar-todas{
		font-size: 12px;
		text-align: left;
		font-weight: lighter;						
	}	

	.popover-line-bottom{
		min-height: 0.01rem;
		margin: 0em !important;		
		background-color: white;
	}

	.popover-marcar-todas{
		color: white;	
		text-align: right;
		font-size: 11px;
		margin-top: 0.3rem;
	}

	@media (max-width: 360px) {
		.popover-notificaciones{
			max-width: 98vw;	
					
		}
	}

</style>