<template>
  <div id="app">
    <div class="container my-5">
      <h3><i class="fas"></i> Tipos de Empleado</h3>
      <table-sortable
        :items="items"
        :fields="fields"
        :rutaNuevo="rutaNuevo"
        :permisoEscritura="permisoEscritura"
      ></table-sortable>
    </div>
  </div>
</template>
<script>
import TableSortable from "@/components/TableSortable";
import servicio from "@/services/servicio-empleados";
export default {
  data() {
    return {
      rutaNuevo: "tipos-empleado/registro/",
      permisoEscritura: true,
      items: [],
      fields: [
        {
          key: "descripcion",
          label: "Tipo de Empleado",
          sortable: true,
          class: "text-center",
        },
        {
          key: "nombreEquipo",
          label: "Equipo Asignado",
          sortable: true,
          class: "text-center",
          formatter: (value) => {
            return value == "" ? "* Sin equipo asignado *" : value;
          },
        },
        {
          key: "estatus",
          label: "Estado",
          sortable: true,
          class: "text-center",
          formatter: (value) => {
            return value ? "Activo" : "Inactivo";
          },
        },
        { key: "actions", label: "Acciones", class: "text-center" },
      ],
    };
  },
  components: {
    TableSortable,
  },
  mounted() {
    // if (this.verificaTokenValido()) {
      this.$loading(false);
      servicio
        .tiposEmpleado(0)
        .then((r) => {
          this.items = r.data;
        })
        .catch((error) => {
          console.log(error);
        });
      this.$loading(false);
      // this.verificaPermiso();
    // }
  },
};
</script>